<template>
  <div class="MenuToggler" :class="side === 'left' ? 'MenuToggler__lefty':'MenuToggler__righty'" v-html="picto" @click="toggle"></div>
</template>

<script>
  import bus from './../bus';

  export default {
    name: 'MenuToggler',
    props: ['side'],
    data() {
      return {
        state: false,
      };
    },
    computed: {
      picto() {
        return this.side === 'left' ? '&rsaquo;' : '&lsaquo;';
      },
    },
    mounted() {
      bus.$emit('hello');
    },
    methods: {
      toggle() {
        bus.$emit('toggleMenu');
      },
    },
  };
</script>

<style lang="scss">
  .MenuToggler {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    cursor:pointer;
    color: darkred;
    font-size:3em;
    font-family: 'Alegreya', sans-serif;
    font-weight: 900;
    z-index: 1;
    &__lefty {
      left:.75rem;
    }
    &__righty {
      right:.75rem;
    }
    @media screen and (max-width:900px) {
      &__lefty {
        left:.5rem;
      }
      &__righty {
        right:.5rem;
      }
    }
  }
</style>



