<template>
  <div id="app">
    <menu-toggler :side="'left'"></menu-toggler>
    <maps-toggler :side="'right'"></maps-toggler>
    <l-menu></l-menu>
    <l-maps></l-maps>
    <l-body></l-body>
  </div>
</template>

<script>
  import bus from './bus';
  import lMenu from './components/Menu';
  import lMaps from './components/Maps';
  import lBody from './components/Body';
  import menuToggler from './components/MenuToggler';
  import mapsToggler from './components/MapsToggler';

  export default {
    name: 'app',
    components: {
      lMenu,
      lMaps,
      lBody,
      menuToggler,
      mapsToggler,
    },
    mounted() {
      bus.$emit('hello');
    },
  };
</script>

<style lang="scss">
@import './scss/style';
</style>



