<template>
  <div class="Imago Imago__inline" :class="classes" @click.stop="showMe">
    <span :id="`image-${cat}${num}`">{{cat}}{{num}}</span>
    <figcaption class="Imago--caption" v-html="fcaption"></figcaption>
  </div>
</template>

<script>
  import bus from './../bus';

  export default {
    name: 'TextImago',
    props: ['cat', 'num', 'caption'],
    computed: {
      getSrc() {
        return `/memoire/static/images/${this.cat}/thumbs/${this.cat}${this.num}.jpg`;
      },
      fcaption() {
        return `${this.cat}${this.num} - ${this.caption}`;
      },
      classes() {
        return [`Imago__${this.cat}`];
      },
    },
    methods: {
      showMe() {
        bus.$emit('showImage', {
          cat: this.cat,
          num: this.num,
        });
      },
    },
  };
</script>

<style lang="scss">
  .Imago {
    font-family: 'Alegreya', sans-serif;
    font-weight:400;
    font-size:.9em;
    &__a {
      background: rgb(255,234,37);
    }
    &__b {
      background: rgb(0,99,169);
    }
    &__c {
      background: rgb(255,111,50);
    }
    &__d {
      background: rgb(147,214,77);
    }
    &__e {
      background: rgb(255,62,0);
    }
    &__f {
      background: rgb(209,122,148);
    }
    &__g {
      background: rgb(126,180,255);
    }
    &__h {
      background: rgb(0,138,32);
    }
    &__inline {
      display:inline-block;
      position:relative;
      padding:0 .25em;
      margin: 0 .125em;
    }
    position:relative;
    cursor:pointer;
    &:hover {
      .Imago--caption {
        display:block;
      }
    }
    &--caption {
      display:none;
      position:absolute;
      top: 3em;
      left:0;
      width:12rem;
      background:white;
      box-shadow: 0 0 10px rgba(0,0,0,0.3);
      z-index:1;
      font-family:'Alegreya', sans-serif;
      font-size:.65em;
      padding:.5em;
    }
  }
</style>



