<template>
  <div class="MapsToggler" :class="side === 'left' ? 'MenuToggler__lefty':'MenuToggler__righty'" v-html="picto" @click="toggle"></div>
</template>

<script>
  import bus from './../bus';

  export default {
    name: 'MapsToggler',
    props: ['side'],
    data() {
      return {
        state: false,
      };
    },
    computed: {
      picto() {
        return this.side === 'left' ? '&rsaquo;' : '&lsaquo;';
      },
    },
    mounted() {
      bus.$emit('hello');
    },
    methods: {
      toggle() {
        bus.$emit('toggleMaps');
      },
    },
  };
</script>

<style lang="scss">
  .MapsToggler {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    cursor:pointer;
    color: darkblue;
    font-size:3em;
    z-index: 1;
    font-family: 'Alegreya', sans-serif;
    font-weight: 900;
    &__lefty {
      left:1rem;
    }
    &__righty {
      right:1rem;
    }

    @media screen and (max-width:900px) {
      &__lefty {
        left:.5rem;
      }
      &__righty {
        right:.5rem;
      }
    }
  }
</style>



