<template>
  <figure :id="`maps-image-${this.cat}${this.num}`" @click="toggle" :style="{ display : this.show ? 'block' : 'inline-block' }">
    <img :src="getSrc" :style="{ opacity : this.show ? '0.5' : '1' }">
    <map-big-image :cat="cat" :num="num" :src="getBigSrc" :caption="caption"></map-big-image>
  </figure>
</template>

<script>
  import MapBigImage from './MapBigImage';
  import bus from './../bus';

  export default {
    name: 'lMapImage',
    props: ['cat', 'num', 'caption'],
    components: {
      MapBigImage,
    },
    data() {
      return {
        show: false,
      };
    },
    computed: {
      getSrc() {
        return `/memoire/static/images/${this.cat}/thumbs/${this.cat}${this.num}.jpg`;
      },
      getBigSrc() {
        return `/memoire/static/images/${this.cat}/${this.cat}${this.num}.jpg`;
      },
    },
    methods: {
      toggle() {
        if (this.show === false) {
          this.show = true;
          bus.$emit('showImage', {
            cat: this.cat,
            num: this.num,
          });
        } else {
          this.show = false;
          bus.$emit('closeImage', {
            cat: this.cat,
            num: this.num,
          });
        }
      },
      close() {
        this.show = false;
        bus.$emit('closeImage', {
          cat: this.cat,
          num: this.num,
        });
      },
    },
    mounted() {
      bus.$on('closeImages', () => {
        this.show = false;
      });
      bus.$on('showImage', (p) => {
        if (p.cat !== this.cat) {
          this.show = false;
          return;
        }
        if (p.num !== this.num) {
          this.show = false;
          return;
        }
        this.show = true;
      });
    },
  };
</script>

<style lang="scss" scoped>
  figure {
    padding:0;
    margin:.125em;
    cursor:pointer;
    display:inline-block;
  }
</style>



