<template>
  <section class="Partie" :class="name ? 'Partie__'+name : ''">
    <slot></slot>
  </section>
</template>

<script>
  export default {
    name: 'Partie',
    props: ['name'],
  };
</script>

<style scoped>
  p {
    max-width: 75ch;
  }
</style>



