<template>
  <div class="MenuWrapper" :class="show ? 'MenuWrapper__open' : 'MenuWrapper__closed'">
    <div class="Navigation--wrap">
      <menu-toggler :side="'right'"></menu-toggler>
  <nav class="Navigation">
    <div class="ut--fixpad">
    <ul>
      <li><a href="#z1">note de lecture</a></li>
      <li><a href="#z2">avant-propos</a></li>
      <li><a href="#z3">introduction</a></li>
      <li><a href="#p1">devant le miroir</a>
        <ul>
          <li><a href="#p1-1">1.1 Images et idéologie</a>
            <ul>
              <li><a href="#p1-1-1">Les choses comme elles sont<small>                à la recherche d’objectivité</small></a></li>
              <li><a href="#p1-1-2">Blanc comme neige<small>                le type caucasien comme standard</small></a></li>
              <li><a href="#p1-1-3">Image-type<small>                l’usage du stéréotype</small></a></li>
              <li><a href="#p1-1-4">Lost in translation<small>                parler la même langue</small></a></li>
            </ul>
          </li>
          <li><a href="#p1-2">1.2 Omniprésence et pouvoir de l’image</a>
            <ul>
              <li><a href="#p1-2-1">1 277 500/an<small>nos rapports complexes à l’image</small></a></li>
              <li><a href="#p1-2-2">Manifest destiny<small>un monopole occidental des moyens de diffusion</small></a></li>
              <li><a href="#p1-2-3">Les images ne parlent pas toutes seules<small>polysémie et prisme de lecture</small></a></li>
            </ul>
          </li>
        </ul>
      </li>
      <li><a href="#p2">Normalisation de la communication visuelle</a>
        <ul>
          <li><a href="#p2-1">2.1 Retour en arrière</a>
            <ul>
              <li><a href="#p2-1-1">Clarté, objectivité et lisibilité<small>la ligne du Style Typographique International</small></a></li>
              <li><a href="#p2-1-2">Mad Men<small>les identités visuelles à l’échelle international</small></a></li>
              <li><a href="#p2-1-3">Le verre de cristal<small>ou le designer devrait être invisible</small></a></li>
            </ul>
          </li>
          <li><a href="#p2-2">2.2 Le design(er) dans le système économique actuel</a>
            <ul>
              <li><a href="#p2-2-1">Le cas Helvetica<small>normalisation des identités visuelles</small></a></li>
              <li><a href="#p2-2-2">— Aparté : projet d’identité pour<br>le Fablab de Toulouse</a></li>
              <li><a href="#p2-2-3">vendre et se vendre<small>le branding et l’uberisation</small></a></li>
              <li><a href="#p2-2-4">Le designer ne peut pas être neutre<small>et devrait-il l’être&thinsp;?</small></a></li>
              <li><a href="#p2-2-5">— Aparté : retranscription d’une conférence<br>de Slavoj Žižek chez Google en 2008</a></li>
            </ul>
          </li>
        </ul>
      </li>
      <li><a href="#p3">Rôle et responsabilité du designer graphique</a>
        <ul>
          <li><ul>
            <li><a href="#p3-1">Construire une aile d’avion<small>quelle responsabilité du designer graphique face à sa production&thinsp;?</small></a></li>
            <li><a href="#p3-2">QQOQCCP<small>méthodes de projet dans d’autres domaines</small></a></li>
            <li><a href="#p3-3">— Aparté : témoignages tirés de clientsfromhell.com</a></li>
            <li><a href="#p3-4">Dans le même bateau<small>engager la responsabilité des acteurs d’un projet</small></a></li>
          </ul></li>
        </ul>
      </li>
      <li><a href="#z3">Conclusion</a></li>
      <li><a href="#z4">Références</a></li>
    </ul>
    </div>
  </nav>
  </div>
  </div>
</template>

<script>
  import bus from './../bus';

  import MenuToggler from './MenuToggler';

  export default {
    name: 'lMenu',
    data() {
      return {
        show: 0,
      };
    },
    components: {
      MenuToggler,
    },
    methods: {
      toggle() {
        this.show = !this.show;
      },
    },
    mounted() {
      bus.$emit('hello');
      bus.$on('closeMenu', () => {
        if (this.show) {
          this.toggle();
        }
      });
      bus.$on('closeThings', () => {
        if (this.show) {
          this.toggle();
        }
      });
      bus.$on('toggleMenu', () => {
        bus.$emit('closeMaps');
        this.toggle();
      });
    },
  };
</script>


<style lang="scss" scoped>
  ul, li {
    list-style: none;
  }

  .Navigation {
    background:white;
    position:relative;
    height: 100%;
    width:calc(100% + 16px);
    right:-16px;
    padding-right:5em;
    overflow-y: scroll;
    overflow-x: hidden;
    font-family: 'Alegreya', sans-serif;
    h3 {
      margin:0;
    }
    ul {
      padding-left:0;
      ul {
        padding-left:.5em;
      }
    }
    li {
      font-weight:900;
      li {
        font-weight:700;
        li {
          font-weight:400;
          margin-bottom: .25em;
          text-transform:lowercase;
        }
      }
    }
    &--wrap {
      box-shadow: 2px 0 10px rgba(0,0,0,0.4);
      transition: transform .3s;
      width:100%;
      height:100%;
      overflow:hidden;
      background: white;
    }
  }

  .MenuWrapper {
    width:100%;
    max-width:400px;
    position:fixed;
    left:-400px;
    top:0;
    bottom:0;
    z-index:10;
    background:white;

    &__open {
      .Navigation--wrap {
        transform:translate3d(400px,0,0);
      }
    }

    &__closed {
      .Navigation--wrap {
        transform: translate3d(0,0,0);
      }
    }
  }
  small {
    display:block;
    margin-top:.125em;
  }
  a {
    color:black;
    text-decoration: none;
  }
  .ut--fixpad {
    width:calc(100% - 2em);
  }
</style>




