<template>
  <h3 class="MapIcon" :class="classes" :style="modifier"><span>{{cat}}</span></h3>
</template>

<script>
  export default {
    name: 'MapIcon',
    props: ['cat'],
    computed: {
      classes() {
        return [`MapIcon__${this.cat}`];
      },
      modifier() {
        return {
          transform: `rotate(${(Math.random() > 0.5 ? -1 : 1) * Math.round(Math.random() * 4)}deg)`,
        };
      },
    },
  };
</script>

<style lang="scss">
  .MapIcon {
    display:block;
    padding: .5em;
    margin:1em 0;
    width:1.6em;
    height:1.6em;
    position:relative;
    span {
      position:absolute;
      font-size:2em;
      font-weight:900;
      top:50%;
      left:50%;
      transform:translate(-50%,-60%);
    }
    &__a {
       background: rgb(255,234,37);
     }
    &__b {
       background: rgb(0,99,169);
     }
    &__c {
       background: rgb(255,111,50);
     }
    &__d {
       background: rgb(147,214,77);
     }
    &__e {
       background: rgb(255,62,0);
     }
    &__f {
       background: rgb(209,122,148);
     }
    &__g {
       background: rgb(126,180,255);
     }
    &__h {
       background: rgb(0,138,32);
     }
  }
</style>



