    <partie :name="'Prelude'">
      <h2 id="z2">Avant-propos</h2>
      <p>Faut-il sauter du train en marche ? </p>
      <p>Cette métaphore du train est tirée de propos de Lars Müller dans la préface de la première édition de <em>Josef Müller-Brockmann, Pioneer of Swiss Graphic Design</em><sup><a id="link-0-1" href="#note-0-1">[1]</a></sup> en 1995. Il compare la communication
        visuelle de l’époque à un train lancé à pleine vitesse, avec à son bord des designers qui n’ont aucun contrôle sur sa destination.</p>
      <p>En 2000, lors de la réédition du livre, il ajoute dans sa préface que le train n’a toujours pas ralenti mais que de plus en plus de passagers ont décidé d’en sauter et d’utiliser leur propre élan, poussés par la nécessité de prendre du recul et de
        revenir à des bases solides.</p>
      <p>Sauter du train, de ce train-là, n’est pas suicidaire mais semble plutôt vital. Sauter du train, loin d’être un abandon, permet de l’observer et d’en comprendre les rouages, d’acquérir petit à petit une vision plus large de son impact sur le monde
        qui nous entoure. Ce recul doit permettre de ne plus s’effrayer de la complexité du monde mais de reconnaître cette structure comme un moteur pour rendre possible autre chose.</p>
      <ul class="Footnotes">
        <li><span id="note-0-1"><a href="#link-0-1">[1]</a></span>. Lars Müller,[1995] <em>Josef Müller-Brockmann, Pioneer of Swiss Graphic Design</em>, Lars Müller, 2015, 272&thinsp;p.</li>
      </ul>
      <citation :big="1">Si le design est plutôt une incitation à consommer, alors nous devons rejeter le design&nbsp;; si l’architecture sert plutôt à codifier le modèle bourgeois de société et de propriété, alors nous devons rejeter l’architecture&nbsp;; si l’architecture
        et l’urbanisme sont plutôt la formalisation des divisions sociales injustes actuelles, alors nous devons rejeter l’urbanisation et ses villes… jusqu’à ce que tout acte de design ait pour but de rencontrer les besoins primordiaux. D’ici là, le design
        doit disparaître. Nous pouvons vivre sans architecture. <cite>— Alfonso Natalini (1971)</cite>
      </citation>
    </partie>

    <partie :name="'Introduction'">
      <h2 id="z3">Introduction</h2>

      <p>Dix-sept ans plus tard, le train ne ralentit toujours pas et il semble difficile de trouver sa place en tant que designer graphique dans le contexte socio-politique actuel, mouvant et instable. Cette place occupée ou à occuper n’a jamais cessé d’être
        questionnée et débattue, mais nous ne pouvons plus nous permettre de ne pas considérer cette discipline comme intrinsèquement politique. La pensée de Tony Fry<sup><a id="link-1-1" href="#note-1-1">[1]</a></sup> selon laquelle le designer se situe
        entre deux positions, soit celle de servir et renforcer le statu quo ou bien de le remettre en cause, semble pertinente dans la dynamique libérale actuelle. Nous savons créer, nous avons appris à le faire, mais nous avons certainement plus de mal
        à prendre du recul sur la complicité que notre profession peut entretenir, par nature, avec des structures dominantes discutables. Dans ces conditions, il paraît essentiel d’engager une réflexion critique aussi bien quant à l’impact de notre métier
        sur l’environnement dans lequel il s’inscrit, que sur les responsabilités qui accompagnent la tâche de produire et donner à voir des images. Devant tout acte de design, la pensée de Victor Papanek<sup><a id="link-1-2" href="#note-1-2">[2]</a></sup>      selon laquelle le designer ne doit pas se demander s’il <em>peut</em> le faire mais s’il <em>doit</em> le faire semble essentiel dans cette réflexion sur le rôle et la responsabilité du designer graphique.</p>
      <p>La recherche présentée dans ce mémoire s’articule autour de trois questionnements qui se recoupent inévitablement. La première partie, <em>devant le miroir</em>, constitue un état des lieux qui s’appuie d’abord sur une analyse de productions visuelles
        avec un recul critique permettant de les mettre en lien et d’en dégager des problématiques plus larges. À la suite de cette étude de cas vient un questionnement plus théorique sur les évolutions de nos rapports aux images avec les nouveaux moyens
        de diffusion que nous connaissons aujourd’hui, qui oblige à examiner dans un même temps le monopole occidental de ces moyens de diffusion. Il semble également essentiel d’aborder la polysémie des images et les différences dans leur interprétation
        qui découlent de l’expérience et de la culture propre à chacun. Ce prisme de lecture du monde, complexe et unique, entre évidemment en jeu lorsque nous produisons des images et il semble donc important d’essayer de le cerner pour mieux comprendre
        les mécanismes inconscients qui entrent en jeu dans sa propre pratique.</p>
      <p>Ensuite, la partie <em>normalisation de la communication visuelle</em>, propose un retour en arrière sur ce qui semble avoir largement influencé notre manière d’apprendre le design graphique et de le pratiquer. Le Style Typographique International,
        avec son déploiement dans le monde entier et sa présence toujours visible aujourd’hui a particulièrement marqué l’histoire du design graphique. Il s’agira dans un premier temps de cerner les principes du mouvement qui peuvent éclairer des problématiques
        contemporaines liées à ma recherche. En s’imposant comme style de prédilection pour les grandes entreprises au moment où se sont considérablement intensifiés les échanges internationaux, le mouvement s’est trouvé au devant de la scène et semble
        avoir forgé la notion d’identité visuelle telle qu’on la connaît aujourd’hui. La place du designer, entre neutralité ou engagement, est également une question que l’on peut aborder avec ce mouvement qui considérait l’objectivité comme première garantie
        de la qualité de la production. Enfin, si l’on étudie le lien entre l’économie de marché et le design graphique, on doit aussi aborder l’influence de cette économie sur le métier même de designer. L’uberisation, décrivant un nouveau statut de travailleurs,
        semble pour beaucoup menacer la profession. Néanmoins, elle n’est que rarement abordée comme la conséquence de failles qui rendent cette même uberisation possible. Il s’agira également de démystifier ce terme en exposant les mécanismes qu’il sous-entend,
        et voir qu’ils sont déjà à l’oeuvre dans notre milieu sans pour autant être nommés de cette manière. </p>
      <p>Une dernière partie, <em>rôle et responsabilités du designer graphique</em>, tente d’explorer des formes de pratiques qui définissent inévitablement le pouvoir du designer sur le monde qui l’entoure, et par conséquent les responsabilités qui en découlent.
        Dans un premier temps, il s’agira de tenter de cerner ce rôle complexe. Une seconde partie explorera, en passant par l’observation de méthodes en place dans d’autres domaines, comment nous pourrions aller vers une analyse des besoins et problèmes
        plus approfondie dans le contexte de la commande de design graphique. Puis, nous évoquerons les types de relations entre les acteurs d’un projet qui semblent les plus compatibles avec les thèses avancées plus tôt.</p>
      <p>Cette recherche se base donc sur une volonté de prise de recul sur ce que l’on désire produire, sur ce que l’on produit, sur ce dont on est le produit, et sur la manière dont on produit. De cette prise de recul découle un positionnement dans la pratique
        du design graphique, et cette réflexion semble déjà s’apparenter à <em>sauter du train en marche</em>.</p>
      <p>Si cette étude m’a permis d’explorer de manière plus approfondie le contexte d’exercice du métier de designer graphique, elle devra évidemment être confrontée à ce même contexte dans ma pratique. Cette articulation entre théorie et application a pour
        but de définir le positionnement personnel que je souhaite adopter dans le futur.</p>
      <ul class="Footnotes">
        <li><span id="note-1-1"><a href="#link-1-1">[1]</a></span>. Tony Fry, Book review, in&thinsp;:&nbsp;<em>Design Issues</em> [en ligne], MIT Press, 2007, vol. 23, n°3, 112&thinsp;p., p.88-92. Disponible en ligne&thinsp;:&nbsp;<a href="http://www.mitpressjournals.org/doi/abs/101162/desi.2007.23.3.88?journalCode=desi">www.mitpressjournals.org</a></li>
        <li><span id="note-1-2"><a href="#link-1-2">[2]</a></span>. Victor Papanek, [1971] <em>Design for a Real World</em>&thinsp;:&nbsp;<em>Human Ecology and Social</em><br><em>Change</em>, New-York, Paladin, 1974, 312&thinsp;p.</li>
      </ul>
    </partie>

    <partie :name="'Corps'">
      <h2 id="p1">Devant le miroir</h2>
      <h3 id="p1-1">Images et idéologie</h3>
      <p>
        <citation :inline="1" :tr="1">«&nbsp;Le pouvoir et ses politiques et hiérarchies existent partout&thinsp;:&nbsp;dans chaque conversation que nous avons, dans chaque échange et transaction que nous faisons, dans chaque visage que nous embrassons.&nbsp;»<sup><a id="link-2-1" href="#note-2-1">[1]</a></sup></citation>
        Dans chaque brush Photoshop que nous utilisons ? Blague à part, ces mots de Barbara Kruger m’ont permis, entre autres, d’en arriver à l’affirmation&thinsp;:&nbsp;<em>tout acte de design est politique</em>.</p>
      <p>La notion essentielle quand il s’agit du lien entre design et politique est l’idéologie. En effet, il est impossible de déconnecter une production de design de l’ensemble d’idées, de valeurs et d’opinions dans lesquelles elle est née.
        <citation :tr="1"
                  :inline="1">«&nbsp;L’idéologie est dans tout ce qui nous entoure, nous la percevons comme naturelle<sup><a id="link-2-2" href="#note-2-2">[2]</a></sup>&nbsp;»</citation>, affirme Ruben Pater, et c’est peut-être pour cette raison qu’il est difficile de réaliser
        pleinement que design et idéologie sont, dans <em>tous les cas</em>, liés. À partir de ce constat, j’ai voulu me rapprocher de cette notion de manière empirique, en m’appuyant sur des exemples concrets de productions visuelles mises en lien les
        unes avec les autres pour former les quatre textes qui suivent.</p>
      <ul class="Footnotes">
        <li><span id="note-2-1"><a href="#link-2-1">[1]</a></span>. Interview de Barbara Kruger, <em>Amnesty</em>, mars 2005. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;Power and its politics and hierarchies exist everywhere&thinsp;:&nbsp;in
          every conversation we have, in every deal we make, in every face we kiss.&nbsp;»</li>
        <li><span id="note-2-2"><a href="#link-2-2">[2]</a></span>. Ruben Pater, <em>The Politics of Design</em>&thinsp;:&nbsp;<em>A (Not So) Global Manual for Visual Communication</em>, Amsterdam, BIS Publishers, 2016, 160&thinsp;p., p. 2. <br>Traduction personnelle.
          Citation originale&thinsp;:&nbsp;«&nbsp;ideology is in everything around us, we perceive it as natural »
        </li>
      </ul>

      <h3 id="p1-1-1">Les choses comme elles sont</h3>
      <h4>à la recherche d’objectivité</h4>
      <p>La représentation de l’Homme ou d’informations, quand elle se fait dans une recherche d’objectivité, ouvre des questionnements intéressants. Un exemple qui entre dans cette catégorie est l’ISOTYPE développé par Otto Neurath entre 1925 et 1934. L’ISOTYPE<sup><a id="link-2-3" href="#note-2-3">[3]</a></sup>      est un ensemble de symboles standardisés servant à représenter des données sociales et scientifiques. La célèbre phrase de Neurath, «&nbsp;Les mots divisent, les images unissent<sup><a id="link-2-4" href="#note-2-4">[4]</a></sup>&nbsp;», illustre
        sa principale motivation&thinsp;:&nbsp;créer un outil d’éducation par l’image qui efface les barrières linguistiques et culturelles. Nous allons nous concentrer sur une des planches de ce langage visuel&thinsp;:&nbsp;<em>Symboles pour 5 groupes d’hommes</em><sup><a id="link-2-5" href="#note-2-5">[5]</a></sup>.
        <text-imago :num="'01'" :cat="'a'" :caption="`<em>Signs for 5 groups of men</em>, Otto Neurath, 1936`"></text-imago> Ces symboles servent de base pour représenter des données liées à l’origine ethnique de la population, par exemple pour la planche <em>Les États-Unis et l’Angleterre dans le monde</em><sup><a id="link-2-6" href="#note-2-6">[6]</a></sup>.
        <text-imago :num="'02'" :cat="'a'" :caption="`<em>United States and Great Britain in the World</em>, Otto Neurath, 1936`"></text-imago>Dans cette représentation, on peut identifier les origines suivantes&thinsp;:&nbsp;européen, latino, africain, indien et asiatique. En plus de réduire la population à seulement cinq groupes, la façon de les représenter est sans équivoque.
        Seul l’homme européen est complètement habillé et blanc, tandis que les autres sont représentés noirs et torse nu. Quand l’homme européen porte un chapeau de tous les jours pour l’époque, les autres sauf l’africain portent des coiffes ou chapeaux
        traditionnels. Cet exemple montre que ce qui se veut objectif et lucide doit toujours être vu sous l’angle du contexte politique et social qui a accompagné sa création. L’ISOTYPE était novateur pour son temps et les qualités graphiques qui s’en
        dégagent sont évidentes, mais il donne aussi à voir l’atmosphère d’une époque où de nombreux pays de l’hémisphère nord colonisent une grande partie du monde.</p>
      <p>On ne peut pas voir les planches de Neurath sans noter l’influence qu’elles ont eue sur nos systèmes de pictogrammes actuels. L’exemple qui suit porte sur un pictogramme en particulier&thinsp;:&nbsp;le symbole international d’accessibilité
        <text-imago
          :num="'03'" :cat="'a'" :caption="`Symbole International d’accessibilité. À gauche : Susanne Koefoed, 1968. À droite : Karl Montan, 1969`"></text-imago> dans les normes ISO 7000 et ISO 7001<sup><a id="link-2-7" href="#note-2-7">[7]</a></sup>. Il a été créé en 1968 par Susanne Koefoed, une étudiante danoise, lors d’une compétition internationale pour l’ICTA<sup><a id="link-2-8" href="#note-2-8">[8]</a></sup>.
        D’abord sans tête, il a ensuite été optimisé dans les normes ISO et équipé d’une tête par Karl Montan en 1969. Contrairement à d’autres pictogrammes, il est géométrique et rectiligne, ne rendant compte d’aucun aspect organique du corps humain.
        De plus, sur toute la population handicapée, seul 1% utilise un fauteuil roulant<sup><a id="link-2-9" href="#note-2-9">[9]</a></sup>, il est donc extrêmement réducteur et regroupe toutes les formes de handicap avec un symbole qui n’est que très
        peu représentatif de la réalité. De nombreuses organisations, notamment celle qui conduit le projet <em>Accessible Icon</em>, ont critiqué ce symbole, et particulièrement le fait que la personne handicapée soit représentée comme complètement inerte
        et déshumanisée. En 2011, Sara Hendren et Brian Glenney ont créé dans le cadre de ce projet <em>Accessible Icons</em> un symbole alternatif et libre de droit qui montre une personne handicapée poussant elle-même son fauteuil.
        <text-imago :num="'04'"
                    :cat="'a'" :caption="`<em>Accessible Icon</em>, Accessible Icon Project, 2011`"></text-imago>
        <text-imago :num="'05'" :cat="'a'" :caption="`<em>Accessible Icon</em>, Accessible Icon Project, 2011`"></text-imago> Pour l’anecdote, ce symbole a été préféré au symbole ISO par Apple, Microsoft ou encore Twitter<sup><a id="link-2-10" href="#note-2-10">[10]</a></sup> dans leurs Emojis, sans pour autant donner d’explication sur ce choix.
        <text-imago
          :num="'06'" :cat="'a'" :caption="`Emoji Apple`"></text-imago>
      </p>
      <p>La représentation de groupes de population, qu’ils soient ethniques dans le cas de l’ISOTYPE, ou relatifs au handicap dans celui du symbole international d’accessibilité, présente des difficultés éthiques évidentes. Et quand on s’amuse à représenter
        l’humanité entière, on peut obtenir la plaque de Pioneer
        <text-imago :num="'07'" :cat="'a'" :caption="`Plaque Pioneer, Carl Sagan et Linda Salzman, 1972`"></text-imago>
        <text-imago :num="'08'" :cat="'a'" :caption="`Carl Sagan tenant une des plaques originales, 1972`"></text-imago> au début des années 1970. Surnommée bouteille à la mer intergalactique, cette plaque métallique embarquée dans les sondes spatiales Pioneer 10 et 11 est un message de l’humanité destiné à d’éventuels êtres extraterrestres. Une partie
        de ce message donne des informations sur l’origine des sondes et une autre représente un homme et une femme nus. La dessinatrice, Linda Salzman, a déclaré avoir tenté d’effacer les marques d’appartenance ethnique des personnages, mais tout indique
        qu’ils sont de type caucasien. La différence entre le traitement des deux sexes est également révélatrice&thinsp;:&nbsp;les parties génitales de l’homme sont détaillées contrairement à celles de la femme, et il fait un geste de la main quand elle
        reste passive et semble en retrait. Comme l’ISOTYPE de Neurath, cet exemple témoigne lui aussi de la réalité de l’époque dans laquelle il a vu le jour. On pourrait néanmoins se demander si l’on constaterait beaucoup de différences si elle été réalisée
        aujourd’hui, presque cinquante ans plus tard.</p>
      <p>Nous avons vu dans ces exemples des représentations de l’Homme, mais on trouve aussi les mêmes problématiques dans la représentation des territoires. La projection de Mercator
        <text-imago :num="'09'" :cat="'a'" :caption="`Projection de Mercator`"></text-imago> est une projection cylindrique du globe terrestre tangente à l’équateur, formalisée par Gerardus Mercator en 1569. Elle s’est rapidement imposée grâce à sa précision pour les voyages en mer et reste encore aujourd’hui la plus utilisée.
        Les projections, par nature, ne peuvent pas conserver dans un même temps les angles, les aires et les distances. La projection de Mercator conserve les angles, mais présente d’importantes déformations des aires et des distances qui augmentent au
        fur et à mesure de l’éloignement avec l’équateur. Quand l’Amérique du Sud, l’Inde, ou l’Afrique apparaissent beaucoup plus petites qu’elles ne le sont, l’Europe, et notamment la Scandinavie apparaissent plus étendues. Souvent qualifiée de coloniale,
        cette carte met en effet en avant les grandes puissances de l’époque. Il existe de nombreuses projections<sup><a id="link-2-11" href="#note-2-11">[11]</a></sup>
        <text-imago :num="'10'" :cat="'a'" :caption="`En haut à gauche : Projection de Bonne - En haut à droite : Projection de Robinson - En bas à gauche : Projection de Gall-Peters - En bas à droite : Projection de Lambert`"></text-imago> qui corrigent ces déformations mais elles sont peu connues et leur apparence nous semble absurde tant la projection de Mercator est ancrée dans nos esprits. On l’utilise de manière presque automatique tandis qu’il s’agit d’une des
        plus déformée et connotée.</p>
      <p>Nous avons l’habitude de voir des représentations des territoires en plongée, mais ce type de représentation n’a rien d’universel et certaines communautés se basent sur un système cartographique bien différent. C’est le cas des Tunumiit, un groupe
        vivant à l’est du Groenland.
        <text-imago :num="'11'" :cat="'a'" :caption="`Carte du Groenland`"></text-imago> C’est en 1880 qu’a eu lieu leur premier contact avec un européen, l’officier maritime Gustav Holm. Lors de son voyage, il a fait la rencontre de Kunit, qui lui a vendu une carte en bois
        <text-imago :num="'12'" :cat="'a'" :caption="`Cartes en bois de la tribu des Tunumiit. Musée National du Groenland.`"></text-imago> sculpté représentant la côte de Sermiligak à Kangerdlugsuatsiak. Ces cartes en bois offrent une représentation tactile des reliefs côtiers du Groenland, permettant aux habitants d’identifier les contours, îles et glaciers alentours
        lors de leurs sorties en kayak. Dans cette partie du monde où la nuit polaire dure six mois, l’utilisation de la vision pour se repérer sur une carte est évidemment impossible, il est donc logique que ces cartes soient faites pour être touchées
        et non lues. Résistante à l’eau et au climat polaire, ces cartes sont un parfait exemple d’objet qui répond avec une grande exactitude au contexte dans lequel il sera utilisé.</p>
      <p>Pour revenir à la notion d’objectivité, on peut s’intéresser à des éléments qui se veulent objectifs de par leur nature et également de par la matière qu’ils donnent à voir. Les diagrammes, représentations graphiques de données souvent complexes,
        n’invitent pas réellement à la remise en question ou au doute concernant leur véracité. Si dans certains cas les données sont exactes, c’est la manière de les mettre en forme qui peut leur donner un tout autre sens. En 2012, Fox Business a diffusé
        un diagramme en barre,
        <text-imago :num="'13'" :cat="'a'" :caption="`Extrait d’un journal télévisé, Fox News, 2012`"></text-imago> que l’on pourrait au mieux taxer de manipulateur. En effet, sans rentrer dans les détails politiques de l’époque, la valeur y commence à 34% alors qu’elle devrait commencer à 0%. La différence réelle des deux valeurs n’est que de 4,6%
        (de 35% à 39,6%),
        <text-imago :num="'14'" :cat="'a'" :caption="`Le même graphique corrigé`"></text-imago> mais elle paraît être multipliée par cinq à la vue du diagramme. Fox a utilisé ce genre de stratagèmes de nombreuses fois pour illustrer différents sujets. Il y a des dizaines de techniques permettant d’orienter la lecture similaire
        à celle-ci, et elles sont particulièrement efficaces puisque ces représentations font généralement autorité.</p>
      <p>Les éléments décrits dans cet article montrent qu’il est essentiel de se poser les questions <em>Qui ? Quand ? Où ?</em>l'homme devant n’importe quel élément visuel afin d’en comprendre au mieux les enjeux. Selon le Larousse en ligne, l’objectivité
        est définie comme la
        <citation :inline="1">«&nbsp;qualité de ce qui est conforme à la réalité, d’un jugement qui décrit les faits avec exactitude.<sup><a id="link-2-12" href="#note-2-12">[12]</a></sup>&nbsp;»</citation> Lorsque l’on représente le monde qui nous entoure, c’est notre propre
        perception de ce monde qui entre en jeu. À partir de là, peut-on faire réellement abstraction des idées, valeurs et des opinions qui ont construit notre regard ?</p>

      <ul class="Footnotes">
        <li><span id="note-2-3"><a href="#link-2-3">[3]</a></span>. ISOTYPE&thinsp;:&nbsp;International System of TYpographic Picture Education <br>(en français&thinsp;:&nbsp;système international d’éducation par les images typographiques) </li>
