        <li><span id="note-2-12"><a href="#link-2-12">[12]</a></span>. <a href="http://www.larousse.fr/dictionnaires/francais/objectivit%C3%A9/55365">www.larousse.fr</a></li>
      </ul>

      <h3 id="p1-1-2">Blanc comme neige</h3>


      <h4>Le type caucasien comme standard</h4>


      <p>Je suis devant le rayon pharmacie d’un supermarché et j’ai le choix entre des pansements beiges, des pansements transparents, ou des pansements Disney. J’ai la peau blanche, j’ai donc tous les choix possibles, même si l’on sait tous que les transparents
        ne collent pas bien, et que les personnages de dessins animés ne passent pas inaperçus. Même si des marques comme Mercurochrome commence à développer des pansements qui offrent des teintes différentes, il est difficile d’en trouver en bas de chez
        soi.
        <text-imago :num="'01'" :cat="'b'" :caption="`Série de photographies réalisées en octobre 2016. Modèle : Arnaud Maury.`"></text-imago> Cet exemple est révélateur d’un environnement où <em>l’homme blanc</em><sup><a id="link-2-13" href="#note-2-13">[13]</a></sup> est le standard. Les exemples qui suivent approchent cette idée sous différents angles.</p>
      <p>
        <citation :inline="1" :tr="1">Un bon cameraman fera poser une «&nbsp;fille couleur&nbsp;» [sic] devant la camera pendant que les techniciens se concentreront sur sa peau pour régler l’équilibre des couleurs. Cette «&nbsp;fille couleur&nbsp;» est toujours blanche.<sup><a id="link-2-14" href="#note-2-14">[14]</a></sup></citation><br>La
        <em>fille couleur</em> dont parle ici Jan Kasoff, ancien cameraman de la NBC, c’est une Shirley en photographie et une China Girl dans l’industrie du cinéma. Shirley, une employée de Kodak dans les années 1950, a servi de premier modèle pour le
        calibrage des pellicules couleurs modernes (Kodachrome). Après elle, de nombreuses Shirley se sont succédées sur les cartes de calibrage de la marque, et toutes devaient avoir la peau la plus blanche possible.
        <text-imago :num="'02'" :cat="'b'"
                    :caption="`Six «Shirley cards» de Kodak`"></text-imago> Les films couleurs Kodak utilisés des années 1950 à 1970 étaient calibrés pour les peaux blanches et ne parvenaient pas à rendre compte des différents tons de peaux mates ou noires. La photographe Syreeta McFadden, une femme noire,
        se rappelle la vision de ses photos d’enfance en ces termes&thinsp;:&nbsp;<br>
        <citation :tr="1" :inline="1">Sur certaines photos, je suis marron-boue, sur d’autres je suis une noire bleue. Certaines des photos ont été prises à très peu d’intervalles l’une de l’autre. Quelqu’un m’a dit «&nbsp;tu ressembles à du charbon&nbsp;», et a rit. Je me suis sentie
          insultée, mais je n’avais pas encore les mots pour ça.<sup><a id="link-2-15" href="#note-2-15">[15]</a></sup></citation><br>En 1977, le réalisateur Jean-Luc Godard a refusé d’utiliser les Kodachrome quand il a été commissionné pour réaliser un
        film au Mozambique, se plaignant que le film était, selon ses propres mots, raciste. Au début des années 1980, de nombreuses entreprises, produisant notamment des meubles en bois ou du chocolat, se sont plaints de ne pas pouvoir rendre compte des
        différentes nuances de couleurs dans leurs publicités ou catalogues. C’est à partir de ces années-là que Kodak a configuré des films pour rendre compte avec plus de justesse des tons sombres.</p>
      <p>Plus récemment, on constate un problème qui fait écho au précédent avec les appareils photographiques numériques destinés au grand public. En effet, certains de ces appareils ou téléphones portables utilisent la reconnaissance faciale pour offrir
        à leurs utilisateurs des options de repérage des visages, des sourires, ou des yeux fermés. Le phénomène le plus récurrent avec cette technologie est la reconnaissance de tous les visages de personnes à la peau blanche dans un groupe quand celui
        des personnes à la peau noire n’est pas reconnu,
        <text-imago :num="'04'" :cat="'b'" :caption="`La reconnaissance faciale de l’appareil photographique reconnait le visage d’une femme blanche sur le tee-shirt d’un homme noir et non son propre visage.`"></text-imago> et l’indicateur indique souvent que quelqu’un ferme les yeux
        <text-imago :num="'03'" :cat="'b'" :caption="`Un appareil photographique compact Nikon signale qu’une femme aux yeux bridés ferme les yeux alors qu’ils sont ouverts.`"></text-imago> quand il s’agit de personnes aux yeux bridés. Une fois encore, le type caucasien est le modèle, et le calibrage de l’appareil est fait à partir de ses caractéristiques.</p>
      <p>Dans une optique similaire, Google a lancé en mai 2015 Google Photos qui, grâce à un algorithme de reconnaissance d’image, classe automatiquement les photos importées dans des albums. Il n’y a qu’à ajouter les photos et le programme reconnaît ce qui
        s’y trouve et donne lui-même un nom à l’album. Alors parfois un cheval se retrouve dans un album qui s’appelle «&nbsp;chiens&nbsp;», mais tant que les animaux n’utilisent pas de smartphones, personne ne pourra être vexé. Le classement automatique
        des albums est encore un peu bancal, comme en témoigne Laura Knotek sur le forum androidcentral&thinsp;:&nbsp;<br>
        <citation :tr="1" :inline="1">Mais comment je peux sortir la photo du sapin de noël de la catégorie «&nbsp;concerts&nbsp;» et quand même la sauvegarder ? Même problème avec les décorations gonflables d’Halloween qui ne sont pas du «&nbsp;feu&nbsp;».<sup><a id="link-2-16" href="#note-2-16">[16]</a></sup></citation><br>      Ces cas-là ne sont pas dérangeants, mais quand il s’agit de photos de personnes, cela peut s’avérer bien plus délicat. En juin 2015, un américain, Jackie Alcine, s’est aperçu que l’application avait classé une photo de lui et d’une amie dans un
        album intitulé <em>gorilles</em>.
        <text-imago :num="'05'" :cat="'b'" :caption="`Capture d’écran de Google Photo, mai 2015`"></text-imago>
      </p>
      <p>En 2010, Neil Irvin Painter publie son livre <em>The History of White People</em>, explorant notamment le rapport entre peau blanche et beauté dans la Grèce Antique.
        <text-imago :num="'06'" :cat="'b'" :caption="`Statue en marbre de la déesse Athéna`"></text-imago> Il cite notamment J. J. Winckelmann<sup><a id="link-2-17" href="#note-2-17">[17]</a></sup> dans ses propos :
      </p>
      <p>Cependant, dans l’analyse finale, la relativité s’efface quand il <em>[Winckelmann]</em> adopte la notion kantienne d’un seul idéal pour l’humanité entière —
        <citation :tr="1" :inline="1">«&nbsp; le profil grec est la première caractéristique de grande beauté dans la forme du visage. &nbsp;»</citation> La peau blanche, ajoute-t-il, rend l’apparence du corps plus belle. À travers le monde occidental, ces règles sont vite devenues
        aussi gravées dans la pierre que les statues qui les ont inspirées.<sup><a id="link-2-18" href="#note-2-18">[18]</a></sup></p>
      <p>Si pendant des siècles le marbre blanc des statues est resté un standard de beauté, il semble qu’il soit basé sur une idée fausse. En effet, aux alentours des années 2000, des études scientifiques ont prouvé la présence d’une polychromie sur les statues
        de la Grèce antique. Même si ce fait était déjà fortement supposé depuis les années 1900, des techniques de détection de peintures «&nbsp;fantômes&nbsp;», notamment avec des lumières UV ont permis de le prouver scientifiquement<sup><a id="link-2-19" href="#note-2-19">[19]</a></sup>.
        <text-imago :num="'07'" :cat="'b'" :caption="`Reconstitution des couleurs d’une statue de l’Empereur Caligula`"></text-imago>
      </p>
      <p>On retrouve ce type de standard de beauté dans le contenu créé à destination des enfants. Il suffit de s’intéresser au princesses Disney pour voir qu’une princesse à la peau blanche est la norme quand une princesse à la peau mate ou noire est l’exception.
        La première princesse Disney, Blanche-Neige, est apparue en 1937. Il a fallu attendre cinquante-cinq ans et quatre princesses plus tard pour voir l’apparition d’une princesse à la peau mate, Jasmine. Sur onze princesses, seulement trois n’ont pas
        la peau blanche.
        <text-imago :num="'08'" :cat="'b'" :caption="`Couleurs de peau des onze princesses Disney classées par ordre chronologique de création`"></text-imago> Ces personnages, symbole de beauté pour des millions d’enfants, établissent un idéal qui ne laisse que peu de place à la diversité.
        <text-imago :num="'09'" :cat="'b'" :caption="`Les onze princesses Disney`"></text-imago>
      </p>
      <p>On peut facilement rapprocher les exemples de ce texte avec ceux vus dans le texte précédent. Ils témoignent certainement d’une domination qui persiste depuis l’époque des grandes puissances coloniales. Il peut également être intéressant de se demander
        si ces exemples ne font pas état d’une société où le marché cible un consommateur riche qui devient alors la norme dans la production.</p>

      <ul class="Footnotes">
        <li><span id="note-2-13"><a href="#link-2-13">[13]</a></span>. À entendre ici comme blanc sociologique, personne d’origine européenne à la peau blanche. </li>
        <li><span id="note-2-14"><a href="#link-2-14">[14]</a></span>. Jan Kasoff, ancien cameraman de la NBC, New York, 20 novembre 1994. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;According to Jan Kasoff, an NBC colour-television cameraman
          on the program Saturday Night Live, […] a good VCR person will have a colour girl stand in front of the cameras and stay there while the technicians focus on her flesh tones to do their fine adjustments to balance the cameras. This colour girl
          is always white. » </li>
        <li><span id="note-2-15"><a href="#link-2-15">[15]</a></span>. Rosi Cima, How Photography Was Optimized for White Skin Color, <br>in&thinsp;:&nbsp;Priceonomics [en ligne], 2015. Disponible en ligne&thinsp;:&nbsp;<a href="https://priceonomics.com/how-photography-was-optimized-for-white-skin/">priceonomics.com</a><br>Traduction
          personnelle. Citation originale&thinsp;:&nbsp;«&nbsp; In some pictures, I am a mud brown, in others I’m a blue black. Some of the pictures were taken within moments of one another. ‘You look like charcoal,’ someone said, and giggled. I felt insulted,
          but I didn’t have the words for that yet.&nbsp;»</li>
        <li><span id="note-2-16"><a href="#link-2-16">[16]</a></span>. Laura Knotek sur le forum androidcentral le 24 octobre 2016.<br>Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;But how do I get my Christmas tree pictures out of the concerts
          category and still save them ? Same problem with the Halloween inflatables that aren’t fires. »</li>
        <li><span id="note-2-17"><a href="#link-2-17">[17]</a></span>. Johann Joachim Winckelmann (1717 - 1768), archéologue et historien<br>de l’art allemand. Il a joué un rôle important dans l’apparition du mouvement néoclassique allemand et européen. Il
          est souvent considéré comme <br>le fondateur de l’histoire de l’art et de l’archéologie en temps que disciplines modernes.</li>
        <li><span id="note-2-18"><a href="#link-2-18">[18]</a></span>. Neil Irvin Painter, <em>The History of White People</em>, W. W. Norton &amp; Company, 2010. 496&thinsp;p., p. 61. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;In the final
          analysis, however, relativity loses as he [J. J. Winckelmann] adopts the Kantian notion of a single ideal figure for all humanity — «&nbsp;the Greek profile is the first character of great beauty in the formation of the visage.&nbsp;» White skin,
          he adds, makes bodily appearance more beautiful. Throughout the Western world, these rules soon became as carved in stone as the statues that inspired them. »</li>
        <li><span id="note-2-19"><a href="#link-2-19">[19]</a></span>. Christopher Reed, Ancients reborn in bright array, in&thinsp;:&nbsp;Harvard Magazine [En ligne], 2007. Disponible en ligne&thinsp;:&nbsp;<a href="http://harvardmagazine.com/2007/11/dazzlers-html">harvardmagazine.com</a></li>
      </ul>

      <h3 id="p1-1-3">Image-type</h3>
      <h4>l’usage du stéréotype</h4>

      <p>«&nbsp;<em>Chinois et japonais, arabe, mexicain, romain et grec, russe, divers</em>&nbsp;», voici les catégories proposées par le site DaFont.com dans la rubrique «&nbsp;Étranger&nbsp;»<sup><a id="link-2-20" href="#note-2-20">[20]</a></sup>. Les polices
        de caractères gratuites répertoriées dans ces catégories reprennent des codes associés à différents endroits du monde, on appelle ça «&nbsp;typographies ethniques&nbsp;». On peut identifier clairement leur utilisation dans les restaurants,
        <text-imago
          :num="'01'" :cat="'c'" :caption="`Boîte de plat chinois à emporter`"></text-imago> lancés pour beaucoup dans une course effrénée à celui qui aura l’air le plus chinois, japonais, français ou grec. L’exemple que j’ai choisi de développer est l’identité de l’Afrika Museum à Berg en Dal aux Pays-Bas.
        <text-imago :num="'02'"
                    :cat="'c'" :caption="`Entrée de l’Afrika Museum à Berg en Dal aux Pays-Bas`"></text-imago> Ce musée, à la fois en extérieur et intérieur, présente une large collection de pièces d’art africain ancien et moderne et reconstitue également des habitations de différentes tribus. Il a été pensé en priorité pour les enfants,
        leur offrant de nombreuses activités. En 2006, le designer René Knip, spécialisé dans la création de caractères, a réalisé la nouvelle identité du musée avec une police de caractères
        <text-imago :num="'03'" :cat="'c'" :caption="`Police de caractères réalisée par René Knip pour l’Afrika Museum`"></text-imago> qui semble reprendre des totems et formes que nous associons à l’Afrique<sup><a id="link-2-21" href="#note-2-21">[21]</a></sup>. Si j’ai employé «&nbsp;que nous associons&nbsp;» c’est bien parce que ce projet s’appuie plus sur ce
        que l’Afrique convoque dans son imagination que sur ce que l’art africain pourrait vraiment apporter en terme de formes et d’éléments de typographie. En 2014, le designer a clarifié son intention lors d’une conférence à San Francisco<sup><a id="link-2-22" href="#note-2-22">[22]</a></sup>,
        expliquant que son intention n’était pas de créer une identité «&nbsp;sérieuse&nbsp;» car le musée est surtout visité par des enfants. </p>
      <p>Dans la même recherche d’authenticité, les producteurs de la série Homeland ont approché des street-artistes afin de réaliser des graffitis sur les murs d’un des lieux de tournage à Berlin. Les décors sur lesquels ils ont dû intervenir recréaient
        un camp de réfugiés à la frontière entre la Syrie et le Liban. Les artistes étant en fort désaccord avec la manière dont la série représente le monde musulman ont transformé cette demande en sabotage. Au lieu d’écrire des slogans pro-Assad comme
        on leur avait demandé, ils ont écrit des phrases comme «&nbsp;Homeland est raciste&nbsp;», «&nbsp;Homeland&nbsp;» est une blague, et ça ne nous a pas fait rire&nbsp;» ou encore «&nbsp;#blacklivesmatter&nbsp;».
        <text-imago :num="'04'" :cat="'c'"
                    :caption="`Graffitis réalisés sur un des décors de la série Homeland`"></text-imago> Il est étrange que la production n’ait pas voulu vérifier la signification des inscriptions, un des artistes, Heba Amin, l’explique ainsi&thinsp;:&nbsp;<br>
        <citation :inline="1" :tr="1">«&nbsp;Le contenu de ce qui était écrit sur les murs n’avait aucune importance. À leurs yeux, l’écriture arabe est seulement un supplément visuel qui complète l’horreur fantasmée [sic] du Moyen-Orient.&nbsp;»<sup><a id="link-2-23" href="#note-2-23">[23]</a></sup></citation><br>Tala
        Sharafeddine, une amie vivant à Beyrouth, m’a exposé les mêmes constats. Elle m’a donné l’exemple d’une expérience vécue par une de ses amies, elle aussi libanaise, et travaillant actuellement dans une entreprise de prêt-à-porter au Canada&thinsp;:&nbsp;<br>
        <citation
          :tr="1" :inline="1">«&nbsp;Ses supérieurs voulaient faire un tee-shirt avec le logo de la marque en arabe, même si aucun d’eux (et aucune des personnes ne travaillant là) n’était arabe. Je trouve ça irrespectueux, parce qu’ils vont les vendre avec cette typographie
          qui sera seulement là comme fonction esthétique alors que la langue arabe (particulièrement aux États-Unis et au Canada) est actuellement quelque chose dont les gens ont peur… Mais seulement quand cela vient d’une personne arabe.&nbsp;»<sup><a id="link-2-24" href="#note-2-24">[24]</a></sup></citation><br>Ici
        comme dans l’exemple précédent, l’utilisation d’un élément visuel, l’écriture arabe, est un élément esthétique dont le sens n’importe pas; <em>l’autre comme décor</em>.</p>
      <p><em>L’autre comme décor</em> peut aussi évoquer les représentations stéréotypées de certains groupes. C’est le cas notamment des Massaïs (ou Maasaïs, Masaïs, <em>ceux qui parlent la langue maa</em>) une population d’éleveurs et de guerriers semi-nomades
        d’Afrique de l’Est. Lorsque l’on cherche des images des Massaïs sur Internet, on ne les voit qu’en tenues traditionnelles rouges, et la plupart du temps sautant au milieu de la savane.
        <text-imago :num="'05'" :cat="'c'" :caption="`Des Massais en tenues traditionnelles dans la savane`"></text-imago> Ces images sont comme une image authentique que l’on ramène chez soi pour montrer qu’on a réellement vu l’Afrique, au même titre que la photo d’un troupeau d’éléphants. Alors que de plus en plus de massaïs vivent maintenant en ville,
        l’artiste et écrivain Jan hoek a voulu, au travers de son projet <em>New Ways of Photographing the New Masai</em><sup><a id="link-2-25" href="#note-2-25">[25]</a></sup>,
        <text-imago :num="'06'" :cat="'c'" :caption="`À droite : Jan Hoek, <em>New Ways of Photographing the New Masai</em>, 2014 - À gauche : Série de photographies pour Filémon, Jan Hoek, 2014`"></text-imago> briser ce stéréotype et leur donner la possibilité d’être présents à travers des prises de vue. Il a alors demandé à sept d’entre eux de quelle manière ils aimeraient être photographiés, et ce projet a abouti à une série de photographies
        mêlant réel et photomontages.</p>
      <p>Une autre représentation stéréotypée est particulièrement présente en occident, notamment aux États-Unis&thinsp;:&nbsp;celle des Amérindiens. Ces premiers et seuls habitants de l’Amérique avant la colonisation européenne ont été victimes d’un génocide,
        <text-imago :num="'07'" :cat="'c'" :caption="`American Progress, John Gast, 1872. Cette peinture est une représentation allégorique de la «Destinée Manifeste», idéologie selon laquelle la nation américaine avait pour mission divine de répandre la démocratie et la «civilisation» vers l’Ouest.`"></text-imago> et leur image a ensuite été, et est toujours, utilisée à tort et à travers. Dans le cinéma américain, l’iconographie de l’indien, au travers de films western, a manipulé le stéréotype de l’Indien, pour les utiliser comme «&nbsp;personnages&nbsp;»
        détachés de toute réalité historique<sup><a id="link-2-26" href="#note-2-26">[26]</a></sup>. Un autre domaine où la représentation des Amérindiens est largement empruntée est le sport aux États-Unis, avec bon nombre de mascottes d’équipes de sport
        étant des caricatures d’Indiens.
        <text-imago :num="'08'" :cat="'c'" :caption="`Mascotte des Cleveland Indians aux États-Unis`"></text-imago> L’utilisation du terme peaux rouges (redskins)
        <text-imago :num="'09'" :cat="'c'" :caption="`Logo des Redskins aux États-Unis`"></text-imago> est également très présent, par exemple avec l’équipe des Washington Redskins, dont le propriétaire, Daniel Snyder, a déclaré en 2013&thinsp;:&nbsp;
        <citation :inline="1" :tr="1">«&nbsp;Nous ne changerons jamais le nom, c’est aussi simple que ça. JAMAIS — vous pouvez le mettre en capitales.&nbsp;»</citation><sup><a id="link-2-27" href="#note-2-27">[27]</a></sup> Shannon Sullivan, professeure de philosophie, l’explique ainsi&thinsp;:&nbsp;<br>
        <citation
          :inline="1">[...] il s’agit en fait d’une tendance qu’ont les américains d’origine euro-péenne à s’approprier les Indiens (et plus spécifiquement leurs traditions et rituels religieux) tels des objets exotiques en vue d’une utilisation à des fins matérielles,
          de plaisir ou de consommation.<sup><a id="link-2-28" href="#note-2-28">[28]</a></sup>
          <text-imago :num="'10'" :cat="'c'" :caption="`Un paquet de cigarettes American Spirit`"></text-imago>
        </citation>
      </p>
      <p>Dans la même veine, Snapchat a mis en place pendant l’été 2016 un filtre permettant aux utilisateurs de transformer leurs selfies en caricatures asiatiques.
        <text-imago :num="'11'" :cat="'c'" :caption="`Capture d’écran d’un filtre Snapchat`"></text-imago>
        <text-imago :num="'12'" :cat="'c'" :caption="`«Yellowface», la caricature qui a inspiré le filtre Snapchat`"></text-imago> Snapchat a déclaré pour se défendre que ce filtre était inspiré des animés japonais, avant de retirer le filtre et d’annoncer qu’il ne sera jamais remis en place. Beaucoup d’entreprises en lien avec les nouvelles technologies ont fait
        face récemment à des contrôles concernant la diversité de leurs équipes. Certaines ont accepté de donner leurs statistiques, révélant que les femmes et les personnes de couleur étaient largement sous-représentées. Snapchat a refusé de fournir ses
        données, mais on peut penser que cette supposée sous-représentation dans leur équipe favorise largement l’apparition de ce type de contenu offensant.</p>
      <p><em>L’autre comme décor</em> pourrait être l’expression résumant les exemples vus précédemment. Il n’est évidemment pas question de prohiber l’utilisation de codes qui n’appartiennent pas à notre culture en affirmant que cela serait, par nature, irrespectueux
        de l’autre culture. Néanmoins, ces utilisations nécessitent une approche mesurée et documentée pour éviter de tomber dans des stéréotypes.</p>

      <ul class="Footnotes">
        <li><span id="note-2-20"><a href="#link-2-20">[20]</a></span>. <a href="http://www.dafont.com/fr/themes.php">www.dafont.com</a></li>
