      <h3 id="p1-1-4">Lost in translation</h3>
      <h4>parler la même langue</h4>

      <p>Le terme <em>langue</em> renvoie généralement à l’idée de signes linguistiques et vocaux, mais ce terme englobe également les signes graphiques ou gestuels. Il est impossible de savoir avec précision combien de langues sont parlées dans le monde.
        Selon le rapport de l’<em>Ethnologue</em><sup><a id="link-2-29" href="#note-2-29">[29]</a></sup>, il y aurait 7 099 langues vivantes au début de cette année, dont plus d’un tiers seraient en danger, avec généralement moins de 1 000 personnes les
        parlant.</p>
      <p>Un des objets qui aura le plus marqué la connaissance de l’écriture est la Pierre de Rosette,
        <text-imago :num="'01'" :cat="'d'" :caption="`Pierre de Rosette, IIème siècle av. J.C.`"></text-imago> fragment de stèle gravée datant de l’Égypte antique. Elle comporte un décret du pharaon Ptolémée promulgué à Memphis en 196 av. J.-C., écrit en deux langues, l’égyptien ancien et le grec ancien, et en trois écritures, les hiéroglyphes,
        l’égyptien démotique et l’alphabet grec. Aux alentours de 1822, elle a permis de déchiffrer les hiéroglyphes dont la signification avait été perdue depuis la fin de l’Empire romain et elle est un des objets multilingues les plus connus. Il semblerait
        que déjà à cette époque historique la société ne fonctionnait pas avec une langue unique. Dans les grandes lignes, les hiéroglyphes étaient l’écriture utilisée pour les monuments, l’égyptien démotique était l’écriture «&nbsp;commune&nbsp;» et le
        grec était celle du gouvernement, notamment pour les textes officiels. On peut donc supposer que le texte ait été traduit dans ces trois écritures pour permettre à toutes les couches lettrées de la société de prendre connaissance du texte.</p>
      <p>On voit se développer aujourd’hui des outils de traduction de plus en plus performants, et des supports d’informations de plus en plus disponibles en version multilingue. Néanmoins, dans de nombreux pays, le problème reste encore d’informer les personnes
        ne maîtrisant ni la lecture ni l’écriture. L’UNICEF recensait un peu plus de 37% de la population indienne en situation d’illettrisme en 2012<sup><a id="link-2-30" href="#note-2-30">[30]</a></sup>. Dans cette situation, une des bases du fonctionnement
        de la société, l’accès pour tous au vote, est un véritable challenge. Le principal élément à penser pour permettre à tous de voter est le design des bulletins de vote. Les partis politiques indiens ont créé un catalogue de petites images et chaque
        candidat peut en choisir un. Il s’agit d’images très simples, comme un éléphant, une main, un ballon ou un soleil, et ce symbole est associé au candidat pendant toute la campagne.
        <text-imago :num="'02'" :cat="'d'" :caption="`Machine de vote en Inde en mai 2009`"></text-imago> Au Soudan, un système similaire a été utilisé lors d’un référendum qui a abouti à l’indépendance du Soudan du Sud.
        <text-imago :num="'03'" :cat="'d'" :caption="`Les deux images représentant les choix possibles lors du référendum sur l’indépendance du Soudan du Sud en 2011.`"></text-imago> Les bulletins étaient composés de deux parties, d’un côté pour l’indépendance avec le mot «&nbsp;SEPARATION&nbsp;» en anglais et en arabe et le dessin d’un main qui fait le signe stop. L’autre côté, contre l’indépendance, présentait
        le mot «&nbsp;UNITY&nbsp;» en anglais et arabe et le dessin d’une poignée de main. Cette solution est aussi simple qu’utile, mais son utilisation est-elle seulement justifiée dans des pays avec un taux d’illettrisme comparable à celui de l’Inde
        ? Nicole Rouja, que j’ai contacté par le biais de l’ANLCI <em>(Agence Nationale de Lutte Contre l’Illettrisme)</em>, m’a confirmé que rien n’était mis en place dans les bureaux de vote français afin d’aider les personnes illettrées. Pour elle, la
        plupart peuvent avoir acquis des connaissances suffisantes pour lire les noms sur les bulletins, mais elle confirme en revanche que cela peut représenter une réelle difficulté pour les personnes n’ayant jamais été scolarisées et n’étant pas accompagnées
        lors du vote. </p>
      <p>Même si, comme on l’a dit plus tôt, les outils de traduction sont de plus en plus efficaces, certains passages d’une langue à l’autre peuvent s’avérer complexe, notamment quand il s’agit de langues ayant des caractéristiques de construction complètement
        différentes comme l’anglais et le mandarin. Lorsque Coca-Cola est entré sur le marché chinois en 1928, ils n’avaient encore aucun logo ou traduction officielle de leur nom. Il a donc fallu trouver quatre caractères chinois parmi les 400 000 existants
        dont la prononciation rendait à peu près les sons «&nbsp;ko-ka-ko-la&nbsp;» et dont le sens était correct.
        <text-imago :num="'04'" :cat="'d'" :caption="`Le logotype de Coca-Cola en langue latine en haut et en langue chinoise en bas.`"></text-imago> Parmis tous les caractères, environs 200 se rapprochaient de ce dont la marque avait besoin, mais beaucoup ont dû être éliminés à cause de leur sens. Durant la recherche de ces quatre caractères, la marque s’est rendu compte que des
        petits commerces avaient déjà fait leurs propres traductions, et ce, de manière totalement phonétique. Ces signes fait à la main sonnaient comme Coca-Cola mais leurs sens allaient de «&nbsp;female horse fastened with wax&nbsp;» (femelle cheval fixée avec de la cire<sup><a id="link-2-31" href="#note-2-31">[31]</a></sup>),
        à «&nbsp;bite the wax tadpole&nbsp;» (mords le têtard en cire<sup><a id="link-2-32" href="#note-2-32">[32]</a></sup>). Au final, l’équivalent le plus proche trouvé par la marque a été «&nbsp;K’o K’ou K’o Lê&nbsp;» qui
        veut dire littéralement «&nbsp;to permit the mouth to rejoice&nbsp;» (permettre à la bouche de se réjouir<sup><a id="link-2-33" href="#note-2-33">[33]</a></sup>). Même si cette traduction nous paraît étrange en anglais
        et en français, son sens est correct en mandarin. Coca-Cola n’est évidemment pas la seule entreprise s’étant heurtée à ce genre de difficultés, mais contrairement à d’autres elle s’en est sortie avec succès. Pour l’anecdote, la marque automobile
        Mazda a sorti son modèle Mazda Laputa,
        <text-imago :num="'05'" :cat="'d'" :caption="`Mazda Laputa`"></text-imago> d’abord commercialisé au Japon puis en Amérique du Sud. Inutile de traduire le nom ou d’expliquer pourquoi le modèle s’est très mal vendu dans les pays où la langue principale est l’espagnol. Le constructeur a déclaré pour se défendre
        avoir choisi le nom en référence à une île imaginaire dans <em>Les voyages de Gulliver</em> de Jonathan Swift. A3 e-tron, Chevrolet Nova (<em>ne va pas</em> en espagnol), BMW i8 (<em>I hate</em> en anglais, «&nbsp;je déteste&nbsp;»), Ford Pinto
        (au brésil, cela désigne un sexe de petite taille) ou encore Ford Probe (en anglais&thinsp;:&nbsp;sonde spatiale mais aussi gastrique…), l’industrie automobile nous offre de beaux exemples de passages ratés d’une langue à une autre.<sup><a id="link-2-34" href="#note-2-34">[34]</a></sup></p>
      <p>Si on a vu que ce passage entre les langues est délicat, il en va de même pour le basculement des logotypes dans des langues latines vers des langues arabes. Avec les politiques bilingues menées notamment par l’Arabie Saoudite et les Émirats Arabes
        Unis, les marques internationales ont l’obligation d’apparaître à la fois en latin et en arabe.
        <text-imago :num="'06'" :cat="'d'" :caption="`Le logotype de Pizza Hut en écriture arabe`"></text-imago> Ce travail d’adaptation, aussi passionnant que complexe, est souvent fait dans une mécanique de copier-coller, sans se soucier des différences de construction des deux écritures. Je ne peux évidemment pas apporter un regard aiguisé
        sur la question puisque je ne parle pas de langue arabe, c’est pourquoi j’ai décidé de m’appuyer sur l’article «&nbsp;Arabic Logotype Adaptation for existing Latin Logotype<sup><a id="link-2-35" href="#note-2-35">[35]</a></sup>&nbsp;» de Pascal
        Zoghbi. Ce designer et typographe libanais vivant aux Pays-Bas a fondé le studio 29letters en 2006, spécialisé en création de caractères typographiques arabes contemporains et en polices multilingues.
        <text-imago :num="'07'" :cat="'d'" :caption="`Police de caractères 29LT Zarid par 29letters. Typographe pour le latin : Khajag Apelian. Typographe pour l’arabe : Pascal Zoghbi.`"></text-imago> Dans son article, il donne d’abord les caractéristiques qui permettent de reconnaître des mauvaises adaptations, des «&nbsp;Frankenstein Arabic&nbsp;» selon ses mots, à savoir :</p>
      <ul class="Body--list">
        <li>- La présence de copier-coller de parties de lettre de l’alphabet latin vers l’arabe.</li>
        <li>- Des caractères à empattement dans les logotypes en arabe alors que l’écriture arabe ne peut pas en contenir.</li>
        <li>- Des disproportions dans la forme des lettres et l’absence de lignes directrices définies dans la construction des caractères. <br>Ces déformations entraînent souvent une quasi-illisibilité de la lettre.</li>
        <li>- Des traits rompus alors qu’ils devraient être continus.</li>
        <li>- De mauvais espacements entre les lettres.</li>
      </ul>

      <p>Bien sûr, certaines de ces caractéristiques ne peuvent pas être détectées sans savoir lire l’arabe, mais on peut considérer que si le logotype semble seulement transposé et non adapté à la nouvelle écriture, il y aura un problème dans sa lisibilité.
        La présence d’empattements dans des logotypes en arabe est certainement une des transpositions abruptes les plus évidentes. Pour éviter les maladresses, Pascal Zoghbi estime que :<br><citation :tr="1" :inline="1">Les adaptations en arabe doivent être dessinées en partant de zéro tout en gardant en tête les proportions et les caractéristiques du logotype en latin.<sup><a id="link-2-36" href="#note-2-36">[36]</a></sup></citation><br>Pour lui, il faut d’abord bien prendre en compte toutes les caractéristiques du logo en latin, en allant du caractère typographique utilisé jusqu’à l’aspect général en terme de formes, de couleurs et de proportions, et ensuite trouver le style de
        caractère arabe qui y fait le plus écho. Il n’y a pas de hauteur d’x en arabe, il faut donc décider de beaucoup de paramètres comme la hauteur d’œil ou les proportions des contre-formes. Les contrastes, l’épaisseur des traits et la forme des terminaisons
        des lettres sont également un facteur à prendre en compte dans l’adaptation. Ces détails montrent que le passage entre les deux styles d’écriture nécessite une grande attention et doit être fait (ou au minima vérifié) avec une personne qui parle
        la langue. Le sens de lecture différent est également un aspect primordial lors des adaptations.</p>
      <p>
        <text-imago :num="'08'" :cat="'d'" :caption="`Le logotype de Fedex pour les langues latines (en haut) et pour en arabe (en bas)`"></text-imago> Si le logotype contient des flèches, ou est ascendant ou descendant, ces caractéristiques doivent être inversées dans leur version en arabe. C’est le cas par exemple pour fedEx ou Burger King. Il est extrêmement rare de trouver des
        langues qui comme les hiéroglyphes n’ont pas un sens de lecture fixe.
        <text-imago :num="'09'" :cat="'d'" :caption="`Hiéroglyphes gravés dans la pyramide de Khéops`"></text-imago> Cette écriture est, en effet, particulière puisqu’elle peut s’écrire de droite à gauche, de gauche à droite ou de haut en bas. Pour savoir dans quel sens lire les signes, il faut repérer les hiéroglyphes asymétriques, souvent les figures
        animales ou humaines, dont le sens du regard indique la direction du texte.<sup><a id="link-2-37" href="#note-2-37">[37]</a></sup> On peut aussi penser aux écritures boustrophédon,
        <text-imago :num="'10'" :cat="'d'" :caption="`Illustration expliquant le boustrophédon`"></text-imago> qui changent alternativement de sens ligne après ligne, de gauche à droite puis de droite à gauche la ligne suivante. On retrouve ce système principalement dans les stades anciens des écritures avant que le sens ne soit fixé, comme
        ce fut le cas avec le grec.</p>
      <p>Le sens de lecture, plus qu’une simple donnée linguistique, influence aussi la lecture des images. Une étude a été menée sur les nouveaux arrivants des Pays-Bas afin de mettre en lumière des aspects qui diffèrent d’une culture à une autre, notamment
        en fonction de la langue maternelle<sup><a id="link-2-38" href="#note-2-38">[38]</a></sup>. Une des questions de cette étude était accompagnée du dessin d’un orage et de quelques arbres, et la personne interrogée devait décrire le déroulement de
        la scène avec une phrase contenant le mot «&nbsp;orage&nbsp;» et «&nbsp;arbres&nbsp;».
        <text-imago :num="'11'" :cat="'d'" :caption="`Extrait du questionnaire Introductiecursus voir vrijwilligsters van het Amsterdams Buurvrouwen Contact, janvier 2015`"></text-imago>Les résultats ont montré qu’une grande majorité des personnes lisant de gauche à droite construisaient une phrase comparable à «&nbsp;l’orage arrive sur les arbres&nbsp;», tandis que les personnes lisant de droite à gauche auront tendance
        à dire que «&nbsp;l’orage est passé sur les arbres&nbsp;» ou «&nbsp;l’orage s’éloigne des arbres&nbsp;». Cet exemple montre bien que cette différence n’influence pas seulement la lecture des textes, mais aussi celle des symboles et des images.</p>
      <p>La communication visuelle à travers les cultures doit prendre en compte la faculté de lire et comprendre les images, au même titre que celle de lire les textes. Cette faculté dépend entièrement de l’environnement visuel dans lequel nous vivons, de
        la quantité d’images auxquelles nous sommes exposés et de notre expérience. Les différences de cultures visuelles peuvent déboucher sur des incompréhensions à la vue d’une image. Un des exemples de cette différence de perception est le portrait,
        qui dans l’histoire de l’art occidental s’est imposé comme une forme de représentation dominante,
        <text-imago :num="'12'" :cat="'d'" :caption="`Léonard de Vinci, La Joconde, entre 1503 et 1506`"></text-imago> et qui est aujourd’hui une sorte de convention photographique pour la publicité, les pages de magazines ou les affiches. </p>
      <p>Cette manière de représenter les individus porte généralement des valeurs positives en occident, comme celle du pouvoir ou de l’accomplissement personnel. Dans certaines cultures, ou le groupe et la famille sont des valeurs qui sont restées plus importantes
        que chez nous, la perception des portraits est totalement différente. C’est le problème qui s’est posé lorsque le gouvernement mexicain a lancé une campagne d’information sur la distribution d’un kit de produit de santé à destination des communautés
        rurales.
        <text-imago :num="'13'" :cat="'d'" :caption="`Campagne mexicaine visant à informer les populations rurales sur la distribution d’un kit basique de produits d’hygiène et de santé`"></text-imago> Cette campagne a été globalement ignorée et a fait l’objet d’une étude dans Beyond Pro Bono<sup><a id="link-2-39" href="#note-2-39">[39]</a></sup> d’Anne Bush. Elle s’appuie dans cet article sur une analyse menée par une de ses étudiantes,
        Erica Wong. Elle a découvert en discutant avec les communautés visées par la campagne que l’image présentant un garçon seul avait été une des premières erreurs dans la réalisation de l’affiche. En effet, dans ces communautés, l’image de l’individu
        est souvent associée à celle du groupe et cette image représentait plutôt à leurs yeux celle d’un enfant abandonné et triste, que celle d’un enfant en bonne santé. Selon Anne Bush,<citation :tr="1" :inline="1">«&nbsp;les gens du village ne pouvaient pas comprendre pourquoi
          il avait été laissé seul<sup><a id="link-2-40" href="#note-2-40">[40]</a></sup>&nbsp;»</citation>. Cette caractéristique de l’image a notamment conduit à une incompréhension de la part des communautés rurales et a empêché la campagne d’arriver à son objectif.</p>
      <p>Les technologies de traduction de plus en plus performantes qui se développent à notre époque permettent de faciliter les échanges, et que le multilingue devienne une norme dans beaucoup de parties du monde. Il faut néanmoins être extrêmement prudent
        lorsque l’on doit manipuler une langue que l’on maîtrise peu ou pas du tout. Certains des exemples précédents montrent aussi qu’il est essentiel de considérer le langage visuel au même titre que la langue, comme quelque chose qui s’apprend et qui
        n’est pas le même d’une culture à une autre.</p>

