      <h3 id="p1-2">Omniprésence et pouvoir de l’image</h3>

      <p>La réflexion précédente basée sur l’analyse de productions visuelles amène naturellement vers plusieurs questionnements. Déjà, on peut se demander quelle place occupe l’image avec l’essor des nouvelles technologies qui deviennent un vecteur privilégié
        de leur diffusion. La circulation des images est considérablement facilitée et accélérée, mais quelles images et par quels canaux ? Cette question rejoint celle du pouvoir de l’image et, par conséquent, de celui qui détient les moyens de communication
        capable de les répandre partout dans le monde. </p><p>Certes, une image peut être aujourd’hui vue simultanément par deux personnes se trouvant à des milliers de kilomètres l’une de l’autre, mais peut-on pour autant dire qu’ils verront la même chose
        ? Une image peut-elle ne contenir qu’un seul sens, ou être universelle ? Peut-on avoir un prisme de lecture des images commun ?</p>

      <h3 id="p1-2-1">1 277 500/an</h3>
      <h4>nos rapports à l’image</h4>

      <p>
        <text-imago :num="'01'" :cat="'e'" :caption="`Le bord d’une route de Taipei (Chine)`"></text-imago> 1 277 500 correspond au nombre de publicités auxquelles serait exposé chaque année un londonien, soit environ 3 500 par jours, selon un article d’Owen Gibson pour <em>The Guardian</em><sup><a id="link-2-41" href="#note-2-41">[41]</a></sup>.
        Si cette observation ne prend en compte que la publicité, et que nous n’allons pas seulement aborder les images commerciales dans ce texte, elle est néanmoins révélatrice d’une sollicitation permanente de notre regard vers ce type d’images.</p>
      <p>Il semble nécessaire de s’interroger d’abord sur le terme d’<em>image</em> lui-même avant d’aborder le rapport que nous entretenons avec elle. Provenant du latin imago qui désigne les masques mortuaires moulés sur les visages juste après la mort,
        <text-imago
          :num="'02'" :cat="'e'" :caption="`Masque mortuaire d’Isaac Newton`"></text-imago> le terme porte aujourd’hui des significations multiples. Comme le fait remarquer Martine Joly<sup><a id="link-2-42" href="#note-2-42">[42]</a></sup>, il est difficile d’en donner une définition tant leurs apparitions se font sous différentes
        formes. Naturelles ou artificielles, visuelles ou non, tangibles ou conceptuelles, on peut finalement se demander ce qui n’est pas une <em>image</em>. Dans le texte qui suit, il sera question des images artificielles, c’est-à-dire produites par l’homme,
        matérielles, à l’opposé de mentales, c’est à dire qui convoquent le regard, et fixes.</p>
      <p>Les expressions qui qualifient le rapport de nos sociétés à l’image sont nombreuses, «&nbsp;empire de l’image&nbsp;» ou «&nbsp;iconocratie&nbsp;»<sup><a id="link-2-43" href="#note-2-43">[43]</a></sup>, «&nbsp;civilisation de l’image&nbsp;», «&nbsp;ère
        de l’image&nbsp;» ou encore «&nbsp;règne&nbsp;»<sup><a id="link-2-44" href="#note-2-44">[44]</a></sup>, on la place au rang de forme de communication dominante, en mettant aussi en lumière son pouvoir. Cette nouvelle configuration remet évidemment
        en cause le statut de la forme textuelle, comme l’évoque Ernst Gombrich dans les années 1990&nbsp;<sup><a id="link-2-45" href="#note-2-45">[45]</a></sup>. Pour l’historien de l’art, nous entrons dans une époque où la parole et l’écriture sont peu
        à peu remplacées par l’image où la vue et l’image prennent une place centrale. Dans une perspective historique, on ne peut évidemment pas affirmer que le pouvoir des images est quelque chose de nouveau, puisque les empereurs affichaient déjà, depuis
        Auguste
        <text-imago :num="'03'" :cat="'e'" :caption="`Statue en marbre de l’Empereur Auguste`"></text-imago> en (27 av. J.-C.), leurs portraits partout afin d’imposer leur autorité&nbsp;<sup><a id="link-2-46" href="#note-2-46">[46]</a></sup>. Ce pouvoir de l’image n’a fait que s’accroître à partir du milieu du XIXème siècle avec l’apparition
        de la photographie, puis ensuite du cinéma, de la télévision, et tout ce que nous connaissons actuellement. Ainsi, Peter Weibel affirme :<br>
      <citation :inline="1">L’espace public se trouve presque entièrement occupé ou colonisé par des images du pouvoir, qu’il soit commercial ou politique.<sup><a id="link-2-47" href="#note-2-47">[47]</a></sup></citation><br>En effet, comme le souligne également Jonathan Crary dans <em>24/7&thinsp;:&nbsp;le capitalisme à l’assaut du sommeil</em><sup><a id="link-2-48" href="#note-2-48">[48]</a></sup>, le pouvoir n’a pas besoin d’afficher son image, mais plutôt que les
        images soient lues selon sa volonté.</p>
      <p>On ne peut pas affirmer que la quantité d’images s’accroît de façon exponentielle sans nuancer en s’intéressant plutôt à leurs vecteurs. Les moyens de circulation des images se sont multipliés ces deux dernières décennies avec l’essor des technologies
        numériques dans notre société et c’est certainement cet aspect qui engendre leur accumulation dans nos vies. En dix minutes sur un écran, on pourra voir plus d’images qu’un homme en aura vu pendant toute sa vie il y a un siècle. Nous sommes «&nbsp;dans
        la profusion sans limite du voir&nbsp;»<sup><a id="link-2-49" href="#note-2-49">[49]</a></sup> dont parle David Le Breton. Si la quantité et la forme de ce qui nous est donné à voir a changé, c’est aussi la capacité nouvelle à s’approprier les images
        qu’il faut prendre en compte. C’est certainement dans ce changement que réside un des aspect clé du nouveau statut des images. En effet, il est évident que les médias de masse traditionnels ont perdu le monopole de l’image et de sa distribution
        avec l’arrivée d’internet, notamment avec les réseaux sociaux qui permettent de capter et diffuser des images dont nous ne sommes pas forcément l’auteur. Si l’on suit la pensée de Nicholas Mirzoeff<sup><a id="link-2-50" href="#note-2-50">[50]</a></sup>,
        ce sont les modalités et les lieux d’apparition des images qui ne sont plus socialement définis comme ils l’étaient auparavant. Le destinataire, autrefois passif, peut maintenant s’approprier les images et les sortir de leur contexte, il est à la
        fois destinataire et émetteur.</p>
      <p>Ce flot d’images qui peuplent notre quotidien agit de façon paradoxale.
        <text-imago :num="'04'" :cat="'e'" :caption="`Les abords d’une zone commerciale vers Marseille`"></text-imago> En effet, cette sollicitation permanente construit en nous une image du monde et sensibilise notre regard, mais elle peut aussi le désensibiliser de par son accumulation. Nous avons grandi dans cette «&nbsp;ère de l’image&nbsp;», tous
        à différents degrés, et sommes habitués à cette présence ininterrompue dans nos vies. Bernard Stiegler en parle ainsi en 2010 dans le documentaire <em>Le temps de cerveau disponible</em> :
        <text-imago :num="'05'" :cat="'e'" :caption="`Image tirée du documentaire Le Temps de Cerveau Disponible de Jean-Robert Viallet et Christophe Nick`"></text-imago>

      <br><citation :inline="1">La capacité à voir des choses abominables est devenue très grande, mais cela veut dire que la sensibilité des gens est devenue de moins en moins grande.<sup><a id="link-2-51" href="#note-2-51">[51]</a></sup></citation><br>
      Il parle ici de la présence toujours plus grande à la télévision d’images violentes ou transgressives, mais on peut se demander si le même mécanisme ne s’opère pas avec toutes les images en général. De plus, ce flux d’images les met toutes sur un
        même plan, empêchant toute hiérarchie et noyant ce qui pourrait être important sous un océan de répétition de mêmes éléments visuels et de mêmes messages. Cette prise en otage de notre regard peut conduire à une désensibilisation, à l’action de
        voir sans pour autant regarder. Cette omniprésence des images est également dangereuse car elle n’invite pas à la remise en question de cette même présence. Elles sont aussi naturelles que le ciel au-dessus de nos têtes, étant pourtant entièrement
        construites et artificielles. L’image devient ainsi un canal de manipulation privilégié et la méfiance semble donc nécessaire.</p>
      <p>La notion même d’image est aussi difficile à cerner que l’est le rapport qu’elle entretient avec notre société. Vecteur d’images du pouvoir, commercial ou politique, elle s’est immiscée plus profondément dans nos vies avec l’essor des technologies
        numériques. Nos rapports avec elle prennent de multiples formes, loin de ce destinataire passif du passé, nous pouvons maintenant tous devenir des émetteurs. Le pouvoir de l’image semble redistribué, et sa présence démultipliée.</p>

