      <h3 id="p1-2-2">Manifest destiny <text-imago :num="'07'" :cat="'c'" :caption="`<em>American Progress</em>, John Gast, 1872. Cette peinture est une représentation allégorique de la «Destinée Manifeste», idéologie selon laquelle la nation américaine avait pour mission divine de répandre la démocratie et la «civilisation» vers l’Ouest.`"></text-imago></h3>
      <h4>un monopole des moyens de diffusion</h4>

      <p>
        Les images, en formant notre regard et en construisant notre image du monde portent, au même titre que le langage, une fonction de vecteur culturel. Ce parallèle avec le langage trouve son intérêt dans les nombreuses théories que l’on peut trouver sur
        son rapport avec la culture. Robert Berthelier décrit cette relation ainsi :<br>
      <citation :inline="1">Il n’est ni culture sans langage, ni langage sans culture. L’un permet à l’autre d’exister, le traduit, autorise sa transmission et son évolution, donc sa vie.<sup><a id="link-2-52" href="#note-2-52">[52]</a></sup></citation><br>Il explique ici la relation mutuelle et symétrique les reliant, caractéristiques qui peuvent être transposées à la relation entre la culture et l’image. Le terme culture a de nombreuses définitions, mais il s’agit ici de celle qui la décrit comme :<br>
        <citation :inline="1">L’ensemble des phénomènes matériels et idéologiques qui caractérisent un groupe ethnique ou une nation, une civilisation, par opposition à un autre groupe ou à une autre nation.<sup><a id="link-2-53" href="#note-2-53">[53]</a></sup></citation></p>
      <p>Pour parler de la question de la culture dans le contexte mondial actuel, on ne peut pas éviter les termes mondialisation et globalisation. Issus de l’anglais globalization, ils sont souvent employés comme synonymes alors qu’il ont acquis au fil
        du temps des sens légèrement différents en français. Pour certains, la globalisation est la phase qui suit la mondialisation, alors que d’autres mettent les deux phénomènes en parallèle et leur donnent des sens différents. Néanmoins, ils font
        tout les deux référence à une transformation des relations à l’échelle mondiale. Selon Henri Bartoli :<br><citation :inline="1">[...] ce qui “globalise” tend à devenir un ensemble régi par des règles telles que le tout organisé constitue un “système” [alors que] ce qui se “mondialise” tisse de multiples liens et interconnexions entre les États-nations, les entreprises, les
        sociétés de telle que les événements, les décisions survenant en un lieu de la planète retentissent plus ou moins intensément sur les individus et les collectivités vivant en d’autres lieux.<sup><a id="link-2-54" href="#note-2-54">[54]</a></sup></citation><br>On s’approche donc de notions comme l’uniformisation et l’universalité, mais ne parlant pas d’économie, mais de culture et d’images, il semble inutile de se positionner sur un des deux termes tant leurs significations propres sont complexes.</p>
      <p>Le milieu du XXe siècle a vu naître, principalement dans les pays occidentaux, la consommation de masse qui a nourri et a été nourrit par la communication de masse. Pour que les gens consomment, il fallait utiliser des moyens de communication avec
        un spectre toujours plus large, des «&nbsp;médias de masse&nbsp;».
        <text-imago :num="'05'" :cat="'e'" :caption="`Image tirée du documentaire Le Temps de Cerveau Disponible de Jean-Robert Viallet et Christophe Nick`"></text-imago> Ce changement va considérablement modifier les modes de vie, et faire apparaître ce que l’on appelle maintenant la culture de masse.</p>
      <p>Ce serait une erreur de considérer que c’est cette nouvelle configuration mondiale qui a fait évoluer les cultures puisque qu’elles sont, par définition, en perpétuelle transformation depuis toujours. Néanmoins, les moyens de diffusion modernes
        ont défini une nouvelle portée physique des images. Comme nous avons pu le voir précédemment, leurs lieux d’apparition ont changé, et il en va de même pour leurs périmètres d’apparition. Quand, dans le passé, un élément visuel ne sortait généralement
        pas des limites des villes, ou au maximum d’un pays, il peut maintenant passer toute frontière grâce à Internet. Une image peut faire le tour du monde en quelques secondes, et il s’en dégage forcément de nouveaux enjeux sociaux et culturels.</p>
      <p>Il faut encore, pour qu’une image puisse faire le tour du monde, qu’elle ait un véhicule en ayant la capacité. Il pourra aujourd’hui s’agir d’une part des médias sociaux alimentés par leurs utilisateurs, et d’autre part des médias de masse, diffusant
        des contenus commerciaux, informatifs ou politiques. On se rend vite compte que les détenteurs de ces médias font tous partie du monde occidental.
        <text-imago :num="'06'" :cat="'e'" :caption="`Carte de l’occident`"></text-imago> Cela comprend, dans la définition géopolitique du terme l’Europe Occidentale, le Canada, les États-Unis, l’Australie et la Nouvelle-Zélande et définit une civilisation partageant une culture similaire et ayant les mêmes racines gréco-romaines.
        Outre l’occident, c’est aujourd’hui les États-Unis qui semblent propager le plus leur culture dans le monde.</p>
      <p>Au sortir de la Première Guerre Mondiale, l’aide américaine avait déjà créé un certain intérêt pour cette culture en Europe, mais c’est à la fin de la Seconde Guerre Mondiale que va s’amorcer une réelle présence des produits culturels américain,
        notamment en France. Lors de l’accord franco-américain Blum-Byrnes en 1946,
        <text-imago :num="'07'" :cat="'e'" :caption="`Léon Blum et James F. Byrnes lors de la signature de l’accord Blum-Byrnes le 28 mai 1946`"></text-imago> une partie de la dette française est liquidée en échange de certaines contreparties. Parmi elles, les États-Unis obtiennent l’autorisation de projeter des films dans les cinémas français. Il s’agit alors d’une manoeuvre pour promouvoir
        le mode de vie américain, l’<em>American Way Of Life</em>,
        <text-imago :num="'08'" :cat="'e'" :caption="`Des afro-américain victimes d’une inondation font la queue pour obtenir des vêtements et de la nourriture devant un local de la Croix Rouge. Derrière eux, un panneau de promotions du mode de vie américain, avec les inscriptions : «Les plus hauts standards de vie» et «Il n’y a rien de comparable au mode de vie américain». Louisville, Kentucky, 1937.`"></text-imago> au delà de leurs frontières. Peu après, des produits de grande consommation comme les chewing-gums, les sodas ou les cigarettes se sont répandus en France, contribuant à faire rayonner la culture de masse américaine. Pour le journaliste
        et politologue David Rothkopf, les États-Unis influencent aujourd’hui <citation :tr="1" :inline="1">«&nbsp;les goûts, les vies, et les aspirations de pratiquement tous les pays<sup><a id="link-2-55" href="#note-2-55">[55]</a></sup>&nbsp;»</citation>, et décrit
        aussi la propagation de leurs produits culturels ainsi :<br>
      <citation :tr="1">La musique américaine, les films américains, la télévision américaine, et les logiciels américains sont si dominants, si convoités, et si visibles qu’ils sont maintenant disponibles absolument partout dans le monde.<sup><a id="link-2-56" href="#note-2-56">[56]</a></sup></citation><br>Si les produits matériels et intellectuels américains sont disponibles à travers la quasi-totalité du monde, l’occident maîtrise aussi les médias et donc la diffusion des informations et des images. Dans certains pays, les chaînes occidentales font
        des audiences records et peuvent devenir la première source d’informations, y compris pour les informations locales. On peut penser à la BBC Arabic<sup><a id="link-2-57" href="#note-2-57">[57]</a></sup>,
        <text-imago :num="'09'" :cat="'e'" :caption="`Logotype de la BBC Arabic`"></text-imago> diffusée dans le moyen-orient et les pays d’Afrique du Nord, ou encore CNBC<sup><a id="link-2-58" href="#note-2-58">[58]</a></sup> qui possède une chaîne officielle dans les pays de l’Afrique sub-saharienne ou encore en Inde. En
        suivant toujours la pensée de David Rothkopf, cette domination américaine se réalise par plusieurs moyens dont la langue, les télé&shy;communications, la standardisation, les produits culturels et les valeurs.<br><citation :tr="1" :inline="1">[...] il est dans l’intérêt économique et politique des États-Unis de s’assurer que si le monde va vers une langue commune, ce soit l’anglais; que si le monde va vers une fusion des moyens de télécommunication, des standards de sécurité et de qualité, ce soit ceux des États-Unis; que si le monde se lie par la télévision, la radio, la musique, que le programme soit américain; et que si des valeurs communes se développent, ce soient des valeurs auxquelles les américains sont habitués.<sup><a id="link-2-59" href="#note-2-59">[59]</a></sup></citation></p>
      <p>On a vu que les moyens de diffusion des produits intellectuels étaient dominés au niveau mondial par l’occident, et plus particulièrement les États-Unis. Ces régions du monde ont le monopole de la diffusion des produits intellectuels, qui tendent
        à s’unifier de par leur caractère majoritairement commercial ou populaire. Si l’occident détient le monopole de diffusion, alors il détient aussi le monopole de l’image et le pouvoir qui lui est associé. Cette configuration, avec les valeurs et
        idées qu’elle porte, nous semble naturelle puisque nous sommes nous-même dans une région du monde qui consent à cette idéologie.</p>

