          personnelle. Citation originale : «&nbsp;And it is in the economic and political interest of the United States to ensure that if the world is moving toward a common language, it be English; that if the world is moving toward common telecommunications,
          safety, and quality standards, they be american; that if the world is becoming linked by television, radio, and music, programming would be American; and that if common values are being developed, they be values with which Americans are comfortable.&nbsp;»</li>
