      <h3 id="p1-2-3">Les images ne parlent pas toutes seules</h3>
      <h4>polysémie et prisme de lecture</h4>

      <p>Ainsi, l’omniprésence des images dans nos vies et le pouvoir qu’elles portent a considérablement évolué avec les moyens de diffusion que nous connaissons aujourd’hui. Le monopole occidental des moyens de diffusion va logiquement vers un monopole
        de l’image et de son pouvoir. Tout comme un discours, une image est dans la grande majorité des cas fabriquée. Pour éclairer cette réflexion, il faudrait à la fois s’intéresser au caractère polysémique des images, et, d’autre part, aux mécanismes
        engagés lorsqu’on les lit, en suivant notamment la pensée de Régis Debray, affirmant que <citation :inline="1">«&nbsp;l’image n’existe que dans la mesure où on la regarde<sup><a id="link-2-60" href="#note-2-60">[60]</a></sup>&nbsp;»</citation>. L’image n’impose que très rarement
        un sens unique. Si on suit Roland Barthes dans son essai <em>Rhétorique de l’image</em><sup><a id="link-2-61" href="#note-2-61">[61]</a></sup>, elle est toujours polysémique, et implique ce qu’il nomme <citation :inline="1">«&nbsp;une “chaîne flottante” de signifiés&nbsp;»</citation>,
        c’est-à-dire que ce qu’elle connote peut être multiple et dépend de la lecture que l’on en fait. Si l’image seule peut avoir plusieurs sens, son interprétation est souvent guidée par un message littéral (légende, texte, article, etc.) qui permet
        d’écarter une partie de ses possibles connotations. C’est un moyen selon Barthes de <citation :inline="1">«&nbsp;fixer la chaîne des signifiés, de façon à combattre la terreur des signes incertains&nbsp;»</citation> et <citation :inline="1">«&nbsp;téléguide [le lecteur] vers un sens choisi à l’avance&nbsp;»</citation>.
        Si l’image porte toujours plusieurs sens, son interprétation dépend de son contexte d’apparition, ainsi que des savoirs et de l’expérience propre à chacun. Ainsi, la lecture d’une image ne peut se faire qu’à travers un prisme qui n’est en rien
        commun à tous.</p>
      <p>Dans ces conditions, il semble d’une part essentiel d’encourager une prise de recul sur les images afin de mieux les comprendre, mais aussi afin de mieux les produire. Il est en effet inévitable de parler de cette différence de regards à la fois
        lors de la lecture d’une image, mais également lors de sa production. David Le Breton utilise dans son article «&nbsp;Pour une anthropologie des sens&nbsp;» la métaphore de la forêt pour expliciter l’influence de l’histoire de chacun dans la compréhension
        du monde&thinsp;:&nbsp;<br><citation :inline="1">Parcourant la même forêt, des individus différents ne sont pas sensibles aux mêmes données. Il y a la forêt du chercheur de champignons, du flâneur, du fugitif, celle de l’Indien, la forêt du chasseur, du garde-chasse ou du braconnier, celle des
        amoureux, des égarés, des ornithologues, la forêt aussi des animaux ou de l’arbre, celle du jour et de la nuit. Mille forêts dans la même, mille vérités d’un même mystère qui se dérobe et ne se donne jamais qu’en fragments. Il n’y a pas de vérité
        de la forêt, mais une multitude de perceptions à son propos selon les angles d’approche, les attentes, les appartenances sociales et culturelles.<sup><a id="link-2-62" href="#note-2-62">[62]</a></sup></citation><br>L’auteur met ici en avant l’impossibilité d’un point de vue similaire sur une même image de la part de plusieurs individus. Le regard de chacun ne s’arrête pas sur les mêmes composantes de l’image, et de fait, l’interprétation de chacun en sera
        différente. Comme on a pu le voir dans la partie <em>lost in translation</em>, lire les images, au même titre que lire du texte, s’apprend. Nous vivons dans un contexte social et culturel où les images nous ont toujours entourés, et en grande
        quantité. Cette notion d’apprentissage peut donc paraître absurde, tant ce mécanisme est pour nous naturel. Pourtant, si on reprend par exemple le cas du portrait de l’enfant pour la campagne du gouvernement mexicain,
        <text-imago :num="'13'"
                    :cat="'d'" :caption="`Campagne mexicaine visant à informer les populations rurales sur la distribution d’un kit basique de produits d’hygiène et de santé`"></text-imago> on se rend compte que des codes qui sont admis pour nous, dont celui du portrait, ne sont pas universels. On peut aussi se référer à l’analyse de Victor Papanek du <em>problème des neufs points</em><sup><a id="link-2-63" href="#note-2-63">[63]</a></sup>,
        <text-imago :num="'10'" :cat="'e'" :caption="`Scan de la page 137 de <em>Design for the Real World : Human Ecology and Social Change</em>, Victor Papanek, 1974`"></text-imago> sorte de casse-tête où l’on doit relier neuf points à l’aide de quatre lignes droites et sans lever le crayon. Papanek explique que cet exercice pourra être résolu par un enfant Eskimo en quelques minutes quand un adulte chez nous
        pourra passer des heures dessus sans trouver la solution. Cette différence serait due à leur vision différente de l’espace qui influe également sur la manière dont ils produisent des images. Si l’on peut se trouver confus devant certaines peintures
        ou dessins Inuits, c’est parce qu’ils ne présentent généralement pas de sens de lecture ni de cadre bien défini, comme on en trouve dans l’art occidental. On peut penser que c’est cette construction du regard et cette conception de l’espace qui
        les autorise naturellement à sortir des limites définies par les points dans le problème des neufs points. Il existe de nombreux exemples qui font écho à celui-ci, et qui montrent que notre construction personnelle, notre culture, et notre environnement
        jouent un rôle évident dans la manière dont nous concevons les images.</p>

      <ul class="Footnotes ut-mbot2">
        <li><span id="note-2-60"><a href="#link-2-60">[60]</a></span>. Michel Field, Le Cercle de Minuit, interview de Régis Debray, France 2, 16 novembre 1992. Visionnable en ligne&thinsp;:&nbsp;<a href="http://fresques.ina.fr/jalons/fiche-media/InaEdu04634/entretien-avec-regis-debray-sur-le-role-de-l-image.html">fresques.ina.fr</a></li>
        <li><span id="note-2-61"><a href="#link-2-61">[61]</a></span>. Roland Barthes, Rhétorique de l’image, in&thinsp;:&nbsp;<em>Communications</em>, 4, 1964, p.40-50. Disponible en ligne&thinsp;:&nbsp;www.persee.fr/doc/comm_0588-8018_1964_num_4_1_1027</li>
        <li><span id="note-2-62"><a href="#link-2-62">[62]</a></span>. David Le Breton, Pour une anthropologie des sens, in&thinsp;:&nbsp;VST - Vie sociale et traitements, 2007, n° 96, p. 45-53. paragr. 15. Disponible en ligne&thinsp;:&nbsp;<a href="http://www.cairn.info/revue-vie-sociale-et-traitements-2007-4-page-45.htmhtm">www.cairn.info</a></li>
        <li><span id="note-2-63"><a href="#link-2-63">[63]</a></span>. Victor Papanek,[1971] <em>Design for the Real World&thinsp;:&nbsp;Human Ecology and Social Change</em>, Paladin, 1974. p.137.</li>
      </ul>

      <p>Rendre lisible des mécanismes et référentiels culturels inconscients, qui constituent notre prisme de lecture des images, paraît essentiel. Il semblerait primordial de redonner à nos modes d’interprétation leur statut de mécanismes socialement construits.
        Bien sûr, ces constructions sociales existent à bien des niveaux, allant de ce que l’on acquiert individuellement, déjà orienté par notre environnement, jusqu’aux constructions relevant de la direction prise par la société dans laquelle nous vivons
        et de ce que l’on appelle «&nbsp;l’air du temps&nbsp;», ou plus justement le <em>Zeitgeist</em> des penseurs allemands Gottfried von Herder, Hegel et Heidegger, désignant le paradigme intellectuel d’une époque. </p>
      <p>Si ces constructions sont identifiables à différents niveaux, un mélange de parcours individuels et d’environnement social, elles ne sont pas à bannir, mais il semble important de conscientiser leur caractère non-naturel. Dans le cas inverse, leur
        statut latent et invisible peut empêcher une prise de recul suffisante sur l’interprétation et la production d’images. Savoir que notre lecture est conditionnée ne nous apporte pas les clés de lecture étrangère à notre culture, mais savoir que
        l’on ne les possède pas est un bon point de départ vers plus d’humilité concernant la portée de nos images. </p>
      <p>Doublée d’une prise de conscience sur la puissance des images, de leur nombre, de l’influence qu’elles ont sur les constructions sociales, cette réflexion amène naturellement à adopter une distance critique sur tout acte de design graphique. Même
        si nous veillons à l’influence que notre condition sociale exerce sur nos productions, leur impact réel est déterminé par les véhicules qui les diffusent, et donc par les désirs des entités qui contrôlent ces moyens de diffusion. Julian Assange,
        fondateur de Wikileaks, écrivait à propos des empires que leur pouvoir dépendait de leurs moyens de communication, et que, de fait, les empires étaient leurs moyens de communication.<br><citation :inline="1" :tr="1">L’étude des empires a longtemps été l’étude de leurs moyens de communication. Gravés dans la pierre ou inscrits à l’encre dans du parchemin, les empires, de Babylone à la dynastie Ming ont laissé des traces d’une organisation centralisée qui communique
        avec sa périphérie. Cependant, dans les années 1950, les étudiants des empires historiques se sont rendu compte que, d’une façon ou d’une autre, l’ensemble des moyens de communication était en soi l’empire en question. Ses méthodes pour organiser
        la consigne, le transport, l’archivage et le stockage de ses communications, et pour désigner qui était autorisé à les lire et à y écrire, constituaient en réalité l’empire. Lorsque les méthodes utilisées par un empire pour communiquer changeaient,
          l’empire lui-même changeait.<sup><a id="link-2-64" href="#note-2-64">[64]</a></sup></citation></p>

      <ul class="Footnotes">
        <li><span id="note-2-64"><a href="#link-2-64">[64]</a></span>Julian Assange, The wikileaks files&thinsp;:&nbsp;The world according to the U.S empire, Verso, 2014, 624&thinsp;p. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;The study
          of empires has long been the study of their communications. Carved into stone or inked into parchment, empires from Babylon to the Ming dynasty left records of the organizational center communicating with its peripheries. However, by the 1950s
          students of historical empires realized that somehow the communications medium was the empire. Its methods for organizing the inscription, transportation, indexing and storage of its communications, and for designating who was authorized to
          read and write them, in a real sense constituted the empire. When the methods an empire used to communicate changed, the empire itself was changed. »</li>
      </ul>
    </partie>

    <partie :name="'Corps'">
      <h2 id="p2">Normalisation de la communication visuelle</h2>
      <h3 id="p2-1">Retour en arrière</h3>

      <p>Il est nécessaire de comprendre de quelle manière s’est fait l’avènement de la communication visuelle normalisée avec laquelle nous cohabitons. Un bref historique des conditions de la naissance des différents mouvements ayant conduit au modernisme
        laisse apparaître un désir de neutralité, de réduction des artifices formels, motivé par le souhait de laisser parler le contenu par lui-même. Ce désir n’est pas apparu <em>ex nihilo</em>, mais suit une transformation politique d’un monde industriel
        au début de ce qu’on appelle globalisation. Ainsi, supprimer tout biais culturel ou individualité dans la forme paraît un objectif noble&thinsp;:&nbsp;combien de bâtiments ont été qualifiés de «&nbsp;caprices d’architectes&nbsp;», d’objets de «&nbsp;fantasmes
        de designers&nbsp;» ? Le style international promeut la neutralité comme garante de la portée d’un message, et une affiche se doit alors d’être aussi intuitive qu’un réveil dessiné par Dieter Rams.<sup><a id="link-3-1" href="#note-3-1">[1]</a></sup>
        <text-imago :num="'01'" :cat="'f'" :caption="`Radio réveil Braun conçu par Dieter Rams en 1978`"></text-imago>
      </p>

      <ul class="Footnotes">
        <li><span id="note-3-1"><a href="#link-3-1">[1]</a></span>. Dieter Rams est un designer industriel allemand. Il a été chef de la <br>conception chez Braun pendant près de 35 ans.</li>
      </ul>

      <h3 id="p2-1-1">Clarté, objectivité et lisibilité</h3>
      <h4>la ligne du Style Typographique International</h4>


      <p>Pendant l’entre deux guerres, des mouvements d’avant-garde comme De Stijl
        <text-imago :num="'02'" :cat="'f'" :caption="`N°12 de la revue De Stijl, 1920`"></text-imago> aux Pays-Bas ou le Constructivisme ou le Suprématisme en Russie ont poussé les designers vers une recherche de formes toujours plus épurées. Les mouvements comme le Bauhaus,
        <text-imago :num="'03'" :cat="'f'" :caption="`Bâtiment du Bauhaus réalisé par Walter Gropuis, 1926`"></text-imago> De Stijl ou encore La Nouvelle Typographie
        <text-imago :num="'04'" :cat="'f'" :caption="`Une double page de l’article «Qu’est-ce que la Nouvelle Typographie et que veut-elle?» de Jan Tschichold, AMG, n°19, 1930`"></text-imago> ont à la fois influencé et été influencés par le Style Typographique International<sup><a id="link-3-2" href="#note-3-2">[2]</a></sup>. </p>
      <p>Ernst Keller,
        <text-imago :num="'05'" :cat="'f'" :caption="`Affiche de Ernst Keller pour le musée des Beaux-Arts de Zurich, 1952`"></text-imago> typographe, designer et professeur est un des personnages les plus marquants dans l’émergence du mouvement et est d’ailleurs vu comme le «&nbsp;père du Style Suisse&nbsp;». Il a commencé a enseigné en 1918 à la Kunst-gewerbeschule<sup><a id="link-3-3" href="#note-3-3">[3]</a></sup>      de Zurich en Suisse, et ses cours en design et typographie ont largement guidé ce qui allait plus tard devenir un mouvement de grande ampleur. Les principes qu’il a posé à cette époque-là, de l’importance de la typographie à laconception d’outils
        de mise en pages comme les grilles rigides, sont retranscrites dans le livre de son étudiant Josef Müller-Brockmann, <em>Grid systems in graphic design</em><sup><a id="link-3-4" href="#note-3-4">[4]</a></sup>.
        <text-imago :num="'06'" :cat="'f'"
                    :caption="`Une double page de <em>Grid System in Graphic Design</em>, Josef Müller-Brockmann, 1981`"></text-imago> Le Style a ensuite été développé et ses principes formalisés dans deux écoles suisses, toutes dirigées par des anciens étudiants de Keller<sup><a id="link-3-5" href="#note-3-5">[5]</a></sup>. </p>
      <p>Bien avant d’énoncer des principes formels à ses étudiants, Ernst Keller leur a enseigné que <citation :inline="1" :tr="1">«&nbsp;la solution à un problème de design doit émerger de son contenu<sup><a id="link-3-6" href="#note-3-6">[6]</a></sup>&nbsp;»</citation>,
        directement relié à la règle énoncée par l’architecte américain Louis Sullivan&thinsp;:&nbsp;<citation :inline="1" :tr="1">«&nbsp;la forme suit toujours la fonction<sup><a id="link-3-7" href="#note-3-7">[7]</a></sup>&nbsp;»</citation>. Pour le Style international,
        la forme ne doit pas distraire du contenu, et ne doit pas non plus tenter de la compléter ni de combler un manque, il doit le soutenir et le montrer de la manière la plus claire possible.</p>
      <p>Le mouvement est donc à la recherche d’une forme permettant de transmettre des messages de la manière la plus directe et ne faisant pas entrer en jeu les opinions du designer. On pourrait énoncer les principes du Style International ainsi&thinsp;:&nbsp;clarté,
        objectivité et lisibilité. Ces trois caractéristiques se traduisent par des propriétés formelles strictes et souvent énoncées par les chefs de file du mouvement comme des lois, par exemple dans <em>De l’Ordre Typographique</em><sup><a id="link-3-8" href="#note-3-8">[8]</a></sup>      d’Emil Ruder. Le mouvement se caractérise déjà par la recherche d’une unité formelle atteinte grâce à une organisation mathématique des éléments visuels sur une grille. Pour Josef Müller-Brockmann&thinsp;:&nbsp;<br>
        <citation><em class="Translation">[...] leur incorporation dans un système de grille crée un sentiment de conception compacte, d’intelligibilité et de clarté, et suggère l’ordre dans la production. L’ordre ajoute de la crédibilité à l’information et induit de la confiance.<sup><a id="link-3-9" href="#note-3-9">[9]</a></sup></em></citation></p>
      <p>Cette grille de composition est la plupart du temps utilisée pour construire des mises en pages asymétriques et la typographie, qui était souvent vues comme un élément parmi les autres, devient l’élément principal de toute production de design. Le
        caractère d’imprimerie est préféré pour son caractère «&nbsp;anonyme et neutre<sup><a id="link-3-10" href="#note-3-10">[10]</a></sup>&nbsp;» et totalement différencié de l’écriture manuscrite. De plus, les polices de caractères sans empattements comme Helvetica, Akzidenz Grotesk ou Univers <text-imago  :num="'07'" :cat="'f'" :caption="`Helvetica (M. Miedinger, fonderie Haas), Univers (A. Frutiger) et Akzidenz Grotesk (fonderie H. Berthold AG)`"></text-imago>		sont largement préférées aux polices de caractères plus traditionnelles et avec empattements. Ces polices de caractères étaient censées exprimer <citation :inline="1" :tr="1">«&nbsp;l’esprit d’un âge plus progressiste<sup><a id="link-3-11" href="#note-3-11">[11]</a></sup>&nbsp;»</citation>, mais également parce que les empattements pouvaient représenter une sorte d’ornementation qui était entièrement rejetée par le mouvement. Si on écoute Massimo Vignelli<sup><a id="link-3-12" href="#note-3-12">[12]</a></sup>, les bonnes polices de
        caractères se comptent sur les doigts d’une main&thinsp;:&nbsp;<br><citation :inline="1" :tr="1">Nous croyons en la typographie, mais nous pensons qu’il n’y a pas beaucoup de bonnes polices de caractères. En étant généreux, je dirais une douzaine. Le plus souvent, je n’en utilise que trois.<sup><a id="link-3-13" href="#note-3-13">[13]</a></sup></citation></p>
      <p>Au niveau des visuels, la photographie va être préférée à l’illustration et au dessin pour la valeur d’objectivité qu’elle semble porter. C’est une technique qui a, selon le mouvement, la capacité de montrer un sujet de manière impartiale. Cette idée
        sera développée dans un chapitre suivant <em>(le designer ne peut pas être neutre)</em> et par ailleurs contredit par la pensée d’auteurs comme Susan Sontag. Ces principes formels et cette ligne directrice basée sur la mise en forme d’information
        de la manière la plus objective possible, à une époque où les échanges internationaux sont en plein essor, n’ont pas tardé à susciter l’intérêt d’entités désirant se rendre visibles à une échelle internationale.</p>

      <ul class="Footnotes">
        <li><span id="note-3-2"><a href="#link-3-2">[2]</a></span>. Le mouvement est aussi appelé Style Suisse ou abrégé par Style International.</li>
        <li><span id="note-3-3"><a href="#link-3-3">[3]</a></span>. Littéralement «&nbsp;École des Arts et de l’artisanat&nbsp;», où les principes du Bauhaus et de la Nouvelle Typographie ont été enseigné.</li>
        <li><span id="note-3-4"><a href="#link-3-4">[4]</a></span>. Josef Muller-Brockmann, [1981] <em>Grid systems in graphic design</em>, Niggli, 2012, 176&thinsp;p.</li>
        <li><span id="note-3-5"><a href="#link-3-5">[5]</a></span>. Le mouvement a été approfondi dans deux écoles de design suisses, l’une à Basel dirigée par Armin Hofmann et Emil Ruder, et l’autre à Zurich dirigée par Josef Müller-Brockmann.</li>
        <li><span id="note-3-6"><a href="#link-3-6">[6]</a></span>. Philip B. Meggs et Alston W. Purvis, [1983] Meggs’ History of Graphic Design, Wiley, 2016, 696&thinsp;p. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;the solution to the
          design problem should emerge from its content&nbsp;»</li>
        <li><span id="note-3-7"><a href="#link-3-7">[7]</a></span>. Louis Sullivan, The Tall Office Building Artistically Considered, in&thinsp;:&nbsp;Lippincott’s Magazine, 1896, p. 403-409. Disponible en ligne&thinsp;:&nbsp;<a href="https://ia800200.us.archive.org/34/items/tallofficebuildi00sull/tallofficebuildi00sull.pdf">ia800200.us.archive.org</a></li>
        <li><span id="note-3-8"><a href="#link-3-8">[8]</a></span>. Emil Ruder, <em>De l’ordre typographique</em>, in&thinsp;:&nbsp;Graphis, 1959, p. 37. Disponible en ligne&thinsp;:&nbsp;<a href="http://indexgrafik.fr/de-lordre-typographique/">indexgrafik.fr</a></li>
        <li><span id="note-3-9"><a href="#link-3-9">[9]</a></span>. Josef Muller-Brockmann, [1981] <em>Grid systems in graphic design</em>, Niggli, 2012, 176&thinsp;p., p. 13. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;[...] their incorporation
          in a grid system creates a sense of compact planning, intelligibility and clarity, and suggests orderliness of design. The orderliness lends added credibility to the information and induces confidence. »</li>
        <li><span id="note-3-10"><a href="#link-3-10">[10]</a></span>. Emil Ruder, <em>De l’ordre typographique</em>, in&thinsp;:&nbsp;Graphis, 1959, p. 37. Disponible en ligne&thinsp;:&nbsp;<a href="http://indexgrafik.fr/de-lordre-typographique/">indexgrafik.fr</a></li>
        <li><span id="note-3-11"><a href="#link-3-11">[11]</a></span>. Philip B. Meggs et Alston W. Purvis, [1983] Meggs’ History of Graphic Design, Wiley, 2016, 696&thinsp;p. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;the spirit of a more
          progressive age&nbsp;»</li>
        <li><span id="note-3-12"><a href="#link-3-12">[12]</a></span>. Massimo Vignelli (1931-2014) est un designer italien. Il a notamment réalisé les identités visuelles d’American Airlines ou encore du métro de New-York. Son travail reflète entièrement les
          principes du Style International.</li>
        <li><span id="note-3-13"><a href="#link-3-13">[13]</a></span>. Massimo Vignelli dans le documentaire <em>Helvetica</em> de Gary Hustwit, 2007, 5’15’’. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;We always had the tendency to use
          a very few typefaces. It’s not that we don’t believe in type, we believe there are not that many good typefaces. If I want to be really generous there’s a dozen, basically I use no more than three. » <br><a href="https://www.youtube.com/watch?v=dD2lNQo4c2Y">www.youtube.com</a></li>
      </ul>

      <h3 id="p2-1-2">Mad Men</h3>
      <h3>les identités visuelles à l’échelle internationale</h3>


      <p>L’omniprésence des productions visuelles liées au Style International nous ramène assez naturellement à celle de la police de caractère phare du mouvement&thinsp;:&nbsp;l’Helvetica<sup><a id="link-3-14" href="#note-3-14">[14]</a></sup>. C’est pour
        cette raison que ce chapitre s’appuiera sur des extraits du documentaire <em>Helvetica</em>
        <text-imago :num="'08'" :cat="'f'" :caption="`Affiche pour le documentaire <em>Helvetica</em> de Gary Hustwit, Experimental Jetset, 2007`"></text-imago> réalisé par Gary Hustwit en 2007<sup><a id="link-3-15" href="#note-3-15">[15]</a></sup>.</p>
      <p>Pour comprendre l’implantation des productions visuelles liées au mouvement au niveau mondial, il faut remonter à la seconde moitié du XXe siècle, et plus particulièrement entre 1950 et 1980. Pendant cette période, le développement des échanges commerciaux
        à l’international a nécessité une communication à grande échelle, qui devait dorénavant passer les frontières qui les contenaient auparavant. Pour se développer, les entreprises et organisations devaient être clairement identifiables auprès d’un
        large public, tout en tentant d’effacer les barrières culturelles. Le design et la typographie ont évidemment eu un rôle crucial dans cette tâche.</p>
      <p>Le Style International, avec les valeurs de neutralité et d’universalité qu’il portait, s’est évidemment imposé comme style de prédilection pour les entreprises et organisations internationales. Selon Leslie Savan&thinsp;:&nbsp;<br><citation :inline="1" :tr="1"><em class="Translation">Les gouvernements et les entreprises aiment l’Helvetica, car d’une part elle les fait paraître neutres et efficaces, mais également parce que la douceur des lettres les fait paraître presque humains.<sup><a id="link-3-16" href="#note-3-16">[16]</a></sup></em></citation><br>Au-delà de la neutralité dans la manière de montrer les informations, c’est également l’apparente neutralité politique du mouvement même qui a pu être décisive. En effet, le Style International ne semble pas à première vue porter une quelconque idéologie
        - sinon de ne justement pas en porter. </p>
      <p>Si les principes du Style International se sont largement imposés dans le domaine de la signalétique, c’est dans celui de l’identité visuelle qu’il a réellement marqué un tournant dans l’histoire de la communication visuelle. En effet, on pourrait
        même se demander si ce n’est pas ce mouvement qui a donné lieu à la notion d’identité visuelle comme on l’entend aujourd’hui. En effet, des identités comme celle d’American Airlines<sup><a id="link-3-17" href="#note-3-17">[17]</a></sup>
        <text-imago
          :num="'09'" :cat="'f'" :caption="`Logotype d’American Airlines, Massimo Vignelli, 1968`"></text-imago> ou du métro de New-York<sup><a id="link-3-18" href="#note-3-18">[18]</a></sup>
        <text-imago :num="'10'" :cat="'f'" :caption="`Extrait de la charte graphique du métro de New-York, Massimo Vignelli, 1970`"></text-imago> par Massimo Vignelli ou encore celle d’IBM<sup><a id="link-3-19" href="#note-3-19">[19]</a></sup>
        <text-imago :num="'11'" :cat="'f'" :caption="`Logotype d’IBM, Paul Rand, 1956`"></text-imago> par Paul Rand sont des exemples qui ont forgé la conception que l’on a aujourd’hui de la communication d’entreprise. Ces identités, exemplaires par leur longévité et leur efficacité, ont permis à ces entités d’être reconnaissables
        par un large public, grâce à un signe, ou un système de signes. Ces identités passent en tout premier lieu par un logotype, sorte de visage de l’entreprise ou de la marque. </p>
      <p>Cette période de transition durant laquelle les organisations et entreprises ont dû se tourner vers des formes de communication ayant une portée internationale a forgé notre manière de penser l’identité visuelle. Le Style International est apparu
        comme une réponse naturelle à un besoin d’universalité, et cette association avec le marché pourrait être aussi bien être perçu par certains comme une complicité. Dans cette configuration, on peut se demander quelle place occupe le designer et le
        principe de neutralité dans la transmission des informations est certainement une des clés pour appréhender cette position ambivalente.</p>

      <ul class="Footnotes">
        <li><span id="note-3-14"><a href="#link-3-14">[14]</a></span>. Police de caractère de la famille des linéales créée en 1957 par Max Miedinger et Eduard Hoffmann, alors qu’ils étaient employés à la Fonderie Haas à Bâle.</li>
        <li><span id="note-3-15"><a href="#link-3-15">[15]</a></span>. Gary Hustwit, <em>Helvetica</em>, 2007. Avec Massimo Vignelli, Wim Crouwel, Matthew Carter, Alfred Hoffmann, Hermann Zapf, Michael Bierut, Leslie Savan, Jonathan Hoefler &amp; Tobias Frere-Jones,
          Erik Spiekermann, Neville Brody, Rick Poynor, Lars Muller, Paula Scher, Stefan Sagmeister, David Carson, Experimental Jetset, Designer Republic et Norm.<br><a href="https://www.youtube.com/watch?v=dD2lNQo4c2Y">www.youtube.com</a></li>
        <li><span id="note-3-16"><a href="#link-3-16">[16]</a></span>. Leslie Savan dans le documentaire <em>Helvetica</em> de Gary Hustwit (2007). 28’07’’. Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;Governments and corporations love Helvetica
          because on one hand it makes them seem neutral and efficient, but also the smoothness of the letters makes them seem almost human. » </li>
