      </ul>

      <h3 id="p2-1-3">Le verre de cristal</h3>
      <h4>ou le designer devrait être invisible</h4>

      <p>Le texte de Beatrice Warde «&nbsp;The Crystal Goblet, or Printing Should Be Invisible&nbsp;»<sup><a id="link-3-20" href="#note-3-20">[20]</a></sup> est une métaphore intéressante concernant la neutralité, aussi bien celle de la forme que celle du
        faiseur de forme. Elle explique que <citation :inline="1" :tr="1">«&nbsp;les vertus du verre à vin parfait peuvent être mises en parallèle avec la typographie<sup><a id="link-3-21" href="#note-3-21">[21]</a></sup>&nbsp;»</citation>, en comparant la dégustation
        d’un vin dans un verre en or massif décoré et un verre en cristal. Selon elle, le verre doit être aussi invisible que possible, et doit <citation :inline="1" :tr="1">«&nbsp;révéler plutôt que cacher&nbsp;»</citation> son contenu. Cela équivaut à dire que la
        forme <em>(le verre)</em> doit être aussi invisible que possible pour mettre en avant le fond <em>(le vin)</em>. En plaçant le style personnel <em>(le verre décoré)</em> en opposition avec la neutralité <em>(le verre en cristal)</em>, ce texte de
        Warde est, en quelque sorte, devenu un manifeste pour le Style International. Cette métaphore est utilisée jusqu’à aujourd’hui comme un argument en faveur de la recherche de neutralité des designers. La mise en forme doit être neutre, et la main
        du designer invisible.</p>
      <p>Le terme <em>neutre</em> nous vient du latin <em>neuter</em>, signifiant «&nbsp;ni l’un ni l’autre&nbsp;»<sup><a id="link-3-22" href="#note-3-22">[22]</a></sup> et la neutralité est définie comme&thinsp;:&nbsp;<br><citation :inline="1" :tr="1">Le caractère, attitude d’une personne, d’une organisation, qui s’abstient de prendre parti dans un débat, une discussion, un conflit opposant des personnes, des thèses ou des positions divergentes.<sup><a id="link-3-23" href="#note-3-23">[23]</a></sup></citation><br>Dans le cas d’une production de design, une recherche de neutralité ne se traduit pas réellement par le refus de prendre parti puisque, de fait, il n’est souvent pas question d’une opposition ou d’un conflit. Il s’agirait plutôt d’un effacement du
        designer, et des valeurs et opinions qu’il porte, au profit de la production d’images qui délivrait les faits tels qu’ils sont. C’est cette idée qu’exprime Wim Crouwel<sup><a id="link-3-24" href="#note-3-24">[24]</a></sup> dans une interview&thinsp;:&nbsp;<br><citation :inline="1" :tr="1">Un message expliqué à quelqu’un doit être aussi direct que possible sans que vienne interférer mes idées personnelles et ma pensée.<sup><a id="link-3-25" href="#note-3-25">[25]</a></sup></citation><br>Il ajoute également qu’il est <citation :inline="1" :tr="1">«&nbsp;celui qui met en forme le message<sup><a id="link-3-26" href="#note-3-26">[26]</a></sup>&nbsp;»</citation> et que ses opinions ou son point de vue n’ont rien à faire dans cette tâche. Les idées
        de Wim Crouwel sont en accord avec ce que préconisaient les initiateurs du mouvement, qui portaient l’idée que le design est une activité socialement utile et importante. Cette affirmation pourrait être entendue comme un appel à une forme d’engagement
        de la part des designers, mais au contraire, pour eux, l’intérêt social du design se trouve dans la diffusion objective d’informations dans la société. </p>
      <p>Il semble donc que le rôle du designer se définisse ici dans une opposition entre engagement et neutralité. Pour le Style international, la forme doit être invisible et donc, par prolongement, la main du designer aussi. La place de celui qui met en
        forme le message frôle ici celle d’un simple exécutant, appliquant une même formule dans des contextes très variés. </p>

      <ul class="Footnotes">
        <li><span id="note-3-20"><a href="#link-3-20">[20]</a></span>. Beatrice Warde, [1932] The Crystal Goblet&thinsp;:&nbsp;Sixteen Essays on Typography, Cleveland, Sylvan Press, 1955. Disponible en ligne&thinsp;:&nbsp;http://www.arts.ucsb.edu/faculty/reese/classes/artistsbooks/Beatrice%20Warde,%20The%20Crystal%20Goblet.pdf
          et en version traduite en français&thinsp;:&nbsp;http://indexgrafik.fr/le-verre-en-cristal-ou-la-typographie-devrait-etre-invisible/</li>
        <li><span id="note-3-21"><a href="#link-3-21">[21]</a></span>. Ibid., Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;all the virtues of the perfect wine-glass have a parallel in typography.&nbsp;»</li>
        <li><span id="note-3-22"><a href="#link-3-22">[22]</a></span>. <a href="http://www.cnrtl.fr/etymologie/neutre">www.cnrtl.fr</a></li>
        <li><span id="note-3-23"><a href="#link-3-23">[23]</a></span>. <a href="http://www.cnrtl.fr/definition/neutralit%C3%A9">www.cnrtl.fr</a></li>
