          personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;A message explained to people should be as straightforward as possible without coming in between my personal ideas and thinking. »</li>
        <li><span id="note-3-26"><a href="#link-3-26">[26]</a></span>. Ibid., Traduction personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;I am the one who is structuring the message and not making it with my personal feelings or my personal political feelings.&nbsp;»</li>
      </ul>

      <h3 id="p2-2">Le design(er) dans le système économique actuel</h3>

      <p>Si ces mouvements ayant profondément modifié le paysage visuel découlaient d’un contexte socio-économique bien particulier, comment la neutralité prônée alors pourrait-elle être comprise autrement que neutre vis-à-vis de l’idéologie politique de l’époque,
        c’est-à-dire justement, tout sauf neutre. Certes, les formes étaient réduites et les individualités gommées, mais n’est-ce pas là une caractéristique de ce libéralisme d’après-guerre dont la tendance de lissage n’a fait que se confirmer par la suite
        ? La volonté d’accès à un marché international dont le modernisme serait l’outil implique une mise en concurrence à l’échelle internationale.</p>
      <p>Il est donc intéressant d’explorer également ce que l’on appelle l’uberisation et la manière dont ce phénomène s’exprime dans notre milieu, malgré qu’il n’en porte pas encore le nom. La mise en concurrence doublée d’un devoir de neutralité créent
        les conditions d’une promiscuité qui pourrait pousser le designer à s’effacer, et à cautionner et permettre l’existence de productions bancales.</p>

      <h3 id="p2-2-1">Le cas Helvetica</h3>

      <h4>normalisation des identités visuelles</h4>

      <p>S’il y a un élément qui caractérise tout le Style International, c’est bien l’Helvetica<sup><a id="link-3-27" href="#note-3-27">[27]</a></sup>. Cette police de caractères est partout, <citation :inline="1" :tr="1">«&nbsp;vous n’avez plus le choix, elle est comme l’air<sup><a id="link-3-28" href="#note-3-28">[28]</a></sup>&nbsp;»</citation>,
        disait Erik Spiekermann<sup><a id="link-3-29" href="#note-3-29">[29]</a></sup> dans le documentaire de Gary Hustwit. Cette présence est majoritairement observable dans l’identité visuelle d’entreprises.
        <text-imago :num="'01'" :cat="'g'" :caption="`50 logotypes d’entreprises internationales utilisant l’Helvetica`"></text-imago> Il suffit de voir le nombre de grandes entreprises ayant adopté un logotype en Helvetica pour se rendre compte d’à quel point elle est omniprésente.</p>
      <p>Elle semble apporter aux entreprises une image de modernité et de fiabilité. Ce qui pourrait paraître étonnant, c’est que l’intérêt de ces entreprises est de se démarquer de leurs concurrents, notamment par le biais de leur image, mais adopte une
        même police de caractères. Cela peut s’expliquer facilement&thinsp;:&nbsp;Helvetica est transparente. Leslie Savan choisit l’exemple de deux entreprises pour expliquer cette idée&thinsp;:&nbsp;<citation :inline="1" :tr="1">«&nbsp;American Apparel utilise l’Helvetica et apparaît effrontée. American Airlines l’utilise	et apparaît sobre<sup><a id="link-3-30" href="#note-3-30">[30]</a></sup>&nbsp;»</citation>.
        <text-imago :num="'02'" :cat="'g'" :caption="`Logotype d’American Airlines (Massimo Vignelli, 1968) et d’American Apparel (auteur et date inconnue)`"></text-imago> Helvetica invite à la libre interprétation, elle ne dit rien en elle-même, elle s’adapte à tout. </p>
      <p>Comme l’exprime Norm<sup><a id="link-3-31" href="#note-3-31">[31]</a></sup> dans ce même documentaire, en utilisant l’Helvetica <citation :inline="1" :tr="1">«&nbsp;vous ne ferez que ce que la police vous dicte<sup><a id="link-3-32" href="#note-3-32">[32]</a></sup>&nbsp;»</citation>.
        Pour eux, la police de caractères porte en elle-même un style et une esthétique qui se manifeste à sa simple utilisation, sans besoin d’un quelconque travail supplémentaire. Ils vont jusqu’à exprimer que le choix de cette typographie peut être un
        choix de la «&nbsp;facilité&nbsp;» :<br><citation :inline="1" :tr="1">Si vous êtes un mauvais designer, ou un néophyte, il suffira d’utiliser l’Helvetica Bold dans le même corps, pour un flyer par exemple, et ça sera bon. Il semblerait alors qu’en terme de mode, dans la conception graphique, nous soyons arrivés à la fin	de l’histoire.<sup><a id="link-3-33" href="#note-3-33">[33]</a></sup></citation>
      <p>
        <text-imago :num="'03'" :cat="'g'" :caption="`Flyer réalisé par mes soins pour une exposition canine en Helvetica`"></text-imago><br>Bien sûr, les caractéristiques que Norm expriment ici ne sont pas seulement inhérentes à l’Helvetica, mais peuvent se transposer aux productions graphiques qui s’appuient sur l'utilisation seule de police de caractères sans-serif,
        dans la lignée du Style International. Massimo Vignelli, dans ses propos cités plus tôt, affirmait qu’il n’y avait qu’une douzaine de bonnes polices de caractères, et qu’il n’en utilisait lui-même que trois. Au contraire, Stefan Sagmeister trouve ce choix réducteur&thinsp;:&nbsp;<br><citation :inline="1" :tr="1">J’ai toujours trouvé que l’approche des gens utilisant seulement trois ou quatre polices de caractères était très suspecte [...] C’est comparable à un écrivain qui dirait&thinsp;:&nbsp;«&nbsp;<em>Je vais seulement écrire avec trois ou quatre mots</em>&nbsp;».
        Oui, vous pourriez sûrement le faire, mais pourquoi ? Serait-ce suffisant pour restituer le travail de toute une vie ?&nbsp;<sup><a id="link-3-34" href="#note-3-34">[34]</a></sup>
      </citation><br>Sagmeister affirme que cette restriction dans le choix des polices de caractères est comparable à un appauvrissement de vocabulaire et on retombe sur le fondement même du Style International&thinsp;:&nbsp;épurer jusqu’à la forme idéale et universelle.
        Cet appauvrissement du vocabulaire graphique engendre un nombre considérable de productions semblables les unes aux autres et qui n’ont pas forcément de nos jours la puissance de communication qu’elles pouvaient avoir dans les années 1960. Prenons
        un livre avec une couverture comportant beaucoup d’espace blanc et quatre lignes d’Helvetica ferrées en haut à gauche. Cette couverture dit-elle à première vue «&nbsp;je suis un livre intéressant&nbsp;» ou «&nbsp;ne me lisez pas, l’intérieur est
        aussi ennuyeux que l’extérieur&nbsp;»? Il semble se dessiner ici la limite très fine entre simplicité, clarté et puissance, et simplicité, clarté et ennui que nous verrons plus tard avec des propos de David Carson<sup><a id="link-3-35" href="#note-3-35">[35]</a></sup>.
        À la fin de sa carrière, Josef Müller-brockmann en est même arrivé à une conclusion plutôt similaire&thinsp;:&nbsp;<br><citation :inline="1" :tr="1">J’ai amené mon amour pour l’ordre jusqu’au point où l’ennui était évident, produisant des solutions de design valides mais ennuyeuses à mourir. Grâce au temps qui passe, je suis maintenant capable d’examiner mes posters pour l’Orchestre de la Tonhalle de Zurich pour découvrir pourquoi certains sont meilleurs que d’autres.&nbsp;<sup><a id="link-3-36" href="#note-3-36">[36]</a></sup></citation><br>Ces productions visuelles ont en commun de n’apporter des informations que de manière textuelle, et non dans leurs formes&thinsp;:&nbsp;à première vue, il n’y a aucune indication sur le contenu qu’elles présentent. Ces systèmes formels minimums n’orientent
        pas la lecture vers une appréhension du contenu. Finalement, on peut aller jusqu’à dire qu’ils ne communiquent pas réellement. David Carson rejoint cette idée en affirmant que la lisibilité et la clarté ne sont pas signe d’une communication efficace&thinsp;:&nbsp;</p>
      <citation :tr="1">Ne confondez pas lisibilité et communication. Juste parce que quelque chose est lisible ne veut pas dire qu’il communique. Et plus important&thinsp;:&nbsp;ne veut pas dire qu’il communique le bon message.<sup><a id="link-3-37" href="#note-3-37">[37]</a></sup></citation>
      <p>On peut se demander si l’omniprésence de cette forme dans les productions ne conduit pas à un appauvrissement général du travail de design graphique. En suivant l’idée selon laquelle le Style International aurait fourni des outils permettant aux designers
        d’atteindre une perfection dans la forme, on se heurte de fait à un appauvrissement formel et dans la fonction même du designer graphique. En effet, l’utilisation d’une formule définie ne peut mener qu’à une unique solution, en faisant fi des différences
        dans les contextes d’intervention. Aujourd’hui, l’image d’une entreprise d’électroménager peut adopter un système formel similaire à celui d’une agence immobilière, et un magasin de prêt-à-porter à celui d’un musée. D’un autre côté, et qui sera
        développé plus longuement ensuite, c’est la fonction même du designer graphique qui est remise en cause par l’utilisation de cette formule développée par le Style International. Si sa tâche et de faire entrer des informations dans une forme clairement
        définie à l’avance, alors toute la partie de recherche qui viserait à définir la forme la plus appropriée au contexte d’intervention s’efface. En appliquant une formule, qui, certes, fonctionne presque à tous les coups sur le plan formel, le designer
        se retrouve, peut-être sans s’en rendre compte, en position d’exécutant. </p>
      <p>On a entrevu dans une partie précédente comment le Style International s’est imposé comme forme de prédilection pour l’image des grandes entreprises, notamment avec des systèmes d’identité reposant en premier sur un logotype. Pour des entreprises
        ayant un besoin de diffusion à une échelle internationale, un logotype est un socle solide pour leur image. Néanmoins, dans le contexte d’une entreprise ou d’une organisation locale, la question de la nécessité d’un logotype se pose, et encore plus
        pour des organismes ayant des moyens très restreints et dont l’intervention d’un designer graphique représentera une part importante de leur budget. Autour de nous, toutes les petites entreprises, associations, clubs sportifs, et même villes, ont
        un logotype, dont la qualité est très variable. Pour exister, il faut un logo, c’est devenu une règle.</p>
      <p>Néanmoins, cette règle semble de plus en plus remise en question dans le milieu du design graphique, même si encore beaucoup de gens, plus ou moins proche de la profession, semblent penser que la plus grande partie du travail de designer graphique
        serait de faire des logos et que n’importe quelle entité ne peut se passer de ce pré-requis.</p>

