      <h3 id="p2-2-2">Aparté : </h3>
      <h4>Projet d’identité visuelle pour le Fablab de Toulouse réalisé en avril 2015 <text-imago :num="'04'" :cat="'g'" :caption="`Projet d’identité visuelle pour Artilect Fablab Toulouse, réalisé en avril 2015. - En haut : matrices découpées au laser servant de pochoir pour les affiches, flyers et programmes du Fablab Festival 2015. - En bas : objets de démonstration lors des initiations à la découpe laser et à l’impression 3D.`"></text-imago></h4>
      <p>En prenant le contrepied de l’affirmation, j’ai réalisé, dans le cadre de mes études, un projet d’identité visuelle pour le Fablab de Toulouse. Ce projet est né d’un souhait du Fablab de renouveler leur identité avec un nouveau logotype et des flyers
        de présentation du lieu. Étant une association, leur budget est restreint et dépend de leur nombre d’adhérents. Le but de cette identité était donc de faire connaître ce Fablab et le rendre accessible au plus grand nombre. Est-ce qu’un nouveau logo
        et des plaquettes de présentation pourrait attirer plus de gens dans leurs locaux ? Rien n’est moins sûr. C’est à partir de là que le projet est devenu fictif, puisque le Fablab tenait vraiment à avoir un logotype. J’ai décidé de partir dans une
        optique inverse, et de leur permettre de construire eux-même leur identité, et avec leurs moyens et outils. En effet, le Fablab a une capacité matérielle (découpe laser, impression 3D, etc.) et humaine considérable, sans avoir un budget conséquent
        à mettre dans une prestation de design graphique. C’est à partir de ces caractéristiques que s’est construit le système d’identité, avec la création de moyens d’impression et de création de visuels à partir de leurs machines. </p>
      <p>En plus d’être économique, cette identité parle autant du Fablab dans sa forme que dans son fond, contrairement à un logo qui n’aurait aucun rapport évident avec les techniques qui se développent dans ce lieu. De plus, ce système d’identité permet
        au Fablab d’acquérir une certaine autonomie vis-à-vis de la conception et de l’impression de leurs supports de communication, et d’avoir une identité qui évolue au rythme du lieu.</p>

      <h3 id="p2-2-3">Vendre et se vendre</h3>
      <h4>le branding et l’uberisation</h4>

      <p>Selon Naomi Klein dans son livre <em>No Logo</em><sup><a id="link-3-38" href="#note-3-38">[38]</a></sup>, des théoriciens du marketing ont développé dès les années 1980 l’idée selon laquelle les entreprises devaient «&nbsp;produire des marques plutôt
        que des marchandises<sup><a id="link-3-39" href="#note-3-39">[39]</a></sup>&nbsp;». Alors que la plupart des entreprises avaient comme part principale de leur activité la production de marchandises, des entreprises comme Nike, Intel ou Microsoft
        ont commencé à prendre des parts du marché, avec une stratégie nouvelle&thinsp;:&nbsp;produire des images de leur marque en priorité. Cette nécessité de marketing est poussée par la production industrialisée de marchandises, qui inonde le marché
        de produits toujours plus uniformes. Il ne s’agit plus alors pour les entreprises de seulement fabriquer ces produits, mais elles doivent aussi parvenir à différencier leur image de celles de leurs concurrents. C’est dans ce contexte que s’est développé
        le branding concurrentiel, qui englobe la publicité, mais qui ne doit pas être vu comme son synonyme. Selon Naomi Klein, la première tâche du branding a été de mettre un nom propre sur des produits comme la farine ou le sucre qui était autrefois
        «&nbsp;servis à la pelle, à même les barils, par des boutiquiers locaux<sup><a id="link-3-40" href="#note-3-40">[40]</a></sup>&nbsp;».
        <text-imago :num="'05'" :cat="'g'" :caption="`Paquet de sucre en poudre`"></text-imago>
      </p>
      <p>Aujourd’hui, le branding correspond au développement par les entreprises de leur image et de leur identité afin de se différencier de leurs concurrents et d’occuper une place dans l’esprit des consommateurs.
        <text-imago :num="'06'" :cat="'g'" :caption="`Adjective food, xkcd (Randall Munroe), décembre 2016 - Traduction : «J’essaie de duper les supermarchés avec ma nouvelle ligne de produits composés uniquement d’adjectifs.»`"></text-imago> Dans la logique actuelle, le designer est lui aussi encouragé, pour survivre, à se comporter comme une entreprise et à se vendre comme une marque. Si cela est évident sur un plan comptable et fiscal, puisqu’il n’est souvent pas salarié,
        cela se ressent aussi sur le plan formel. En effet, de nombreux “studios” ou “agences” de design ne sont en fait que composés d’un ou deux designers indépendants, mais adopte une communication qui normalise leur offre ou leur service à la manière
        de marques. Avec cette image, ils souhaitent s’adresser à des structures plus grandes et établir une relation d’entreprise à entreprise. Pour ce faire, beaucoup reprennent des codes et des stratégies de communication de grandes structures, par exemple
        en passant de leur nom propre à un nom de studio, en adoptant une image plutôt qu’un visage, ou en passant du <em>je</em> au <em>nous</em> dans la forme écrite. Il semble y avoir dans cette stratégie une volonté de se montrer comme une entreprise
        sérieuse et de confiance. Ce qui, à la base, ne pouvait être qu’une pratique rigoureuse de gestion sur le plan juridique de l’entreprise, a été transféré sur le plan formel par ces designers.</p>
      <p>Cette démarche, que l’on pourrait presque qualifier de «&nbsp;stratégie de camouflage&nbsp;» poursuit le même but que celui des structures qui ont une identité normée&thinsp;:&nbsp;exister en tant qu’entreprise, mais surtout en tant qu’entreprise
        non-négligeable. Il pourrait presque être vu comme non professionnel de communiquer en son nom, et il est vu comme inconscient de ne pas communiquer du tout. Néanmoins, cette stratégie n’est pas forcément porteuse. En effet, il ne semble pas y avoir
        une différence de renommée majeure entre des graphistes ayant décidé d’abandonner leur nom ou de le garder. D’un autre côté, personne n’est dupe, un indépendant sans renommée, s’appliquant ou non l’image d’une entreprise sérieuse, reste un indépendant
        sans renommée. Son travail ne sera certainement impacté ni en bien ni en mal par cette démarche de communication. Les studios qui sont actuellement reconnus, internationaux, et à la mode le sont indépendamment du mode de communication qu’ils ont
        choisi. En tant que designer indépendant, il semble illusoire de se convaincre que ce genre de stratégie pourra remplacer la construction d’un réseau si l’on travaille à l’échelle d’une ville ou région. Se persuader du contraire revient à se considérer
        comme un produit mis en concurrence avec d’autres sur l’étagère d’un supermarché. </p>
      <p>Un autre facteur entre en jeu dans le métier de designer graphique&thinsp;:&nbsp;l’uberisation. Ce terme a la part belle des titres de presse depuis l’arrivée sur le marché de la société Uber. Il ne désigne pas le statut de travailleur indépendant
        en lui-même, ni un glissement vers plus de travailleurs avec un statut indépendant au détriment de celui de salarié dans une profession donnée, mais bien la mise en concurrence directe de travailleurs indépendants via une plateforme. Les designers
        font bien sûr face à cette nouvelle donne dans la manière de trouver du travail. Des plateformes comme <em>fiverr</em>, <em>graphiste.com</em> ou <em>99designs</em> proposent de la mise en concurrence d’offres de plusieurs designers face à un même
        brief. Leur pertinence ne sera pas débattue ici, car la démarche de design engendrée par cette organisation du travail n’a rien à voir avec celle défendue dans ce texte. On pourrait même arguer que ce sont deux professions différentes auxquelles
        on donne un même nom. Cependant, même sans passer par ces plateformes dédiées, les effets de l’ubérisation se constate quand même dans le milieu du design.</p>
      <p>Qu’il s’agisse de designers indépendants ou d’agences de design graphique, le branding auquel ils s’adonnent est révélateur&thinsp;:&nbsp;réseaux sociaux où l’on met en scène sa pratique, publications papier et web où l’on suit les tendances, et où
        on les applique de façon quasi-simultanée sur ses propres images de communication. Le branding est ici une tentative de se faire connaître à une échelle variable, allant du local à l’international, de se faire connaître parmi ses pairs via la participation
        à des évènements, conférences, où à des stratégies sur les réseaux sociaux. Si l’on ne dépend pas d’une plateforme comme les VTC d’Uber, la survie est tout de même conditionnée par une mise à disposition de soi sur un marché extrêmement concurrentiel,
        où la communication des projets est souvent un meilleur retour sur investissement que les projets eux-mêmes. Ainsi, la plateforme tierce en question est diffuse, vaporeuse, un cloud de réseaux numériques et humains, mais pourtant bien réelle. Elle
        est composée de l’attention combinée de la presse spécialisée et de tous nos confrères, dans une conquête d’un bouche-à-oreille efficace et d’une appréciation de la part des initiés. On peut aller jusqu’à aborder une nouvelle fonctionnalité mise
        en place par Étapes<sup><a id="link-3-41" href="#note-3-41">[41]</a></sup>, publication web et papier faisant plutôt autorité sur le design graphique en France. Étapes a lancé il y a peu une plate-forme complémentaire à son site internet invitant
        les étudiants et professionnels à soumettre leurs travaux eux-mêmes. La motivation pour beaucoup étant d’être «&nbsp;dans Étapes&nbsp;», on peut facilement imaginer que le travail de veille de la rédaction a été considérablement allégé. </p>
      <p>De manière anecdotique, avez-vous déjà entendu un(e) étudiant(e) dire «&nbsp;je dois mettre mon site à jour&nbsp;» ou encore se précipiter pour prendre des photos sitôt un projet terminé afin de le publier sur Internet ? Les avez-vous vu également
        prendre des photos de leurs travaux en cours de manière plus spontanée, pour publier des «&nbsp;work in progress&nbsp;» sur les réseaux sociaux ? Cette mise en concurrence se retrouve dans les comportements des jeunes designers avant même leur entrée
        sur le marché du travail, qui se démènent pour se démarquer par la communication de leurs projets. Ainsi, en mettant de côté la gestion et l’aspect financier que l’on tire du travail, il n’y a pas de différence majeure entre l’espace-temps des études
        et celui du travail pour le designer. Il doit en permanence semer, entretenir, et développer cette communication dès son entrée en école. Le designer <em>ne sera pas</em> ubérisé, il l’est déjà, et est encouragé à embrasser ce statut dès que la
        nécessité du premier stage arrive.</p>

      <ul class="Footnotes">
        <li><span id="note-3-38"><a href="#link-3-38">[38]</a></span>. Naomi Klein, [2002] <em>No logo</em>&thinsp;:&nbsp;la tyrannie des marques, Montréal, Actes Sud, 2015, 743&thinsp;p.</li>
        <li><span id="note-3-39"><a href="#link-3-39">[39]</a></span>. Ibid. p. 29</li>
        <li><span id="note-3-40"><a href="#link-3-40">[40]</a></span>. Ibid. p. 33</li>
        <li><span id="note-3-41"><a href="#link-3-41">[41]</a></span>. Revue mensuelle spécialisée dans le design graphique et la culture visuelle.<br><a href="http://etapes.com/">etapes.com</a></li>
      </ul>

      <h3 id="p2-2-4">Le designer ne peut pas être neutre</h3>
      <h4>et devrait-il l’être ?</h4>

      <p>Cet énoncé est auto-référentiel, et fait écho au paradoxe d’Épiménide le Crétois qui a affirmé <citation :inline="1">«&nbsp;les Crétois sont toujours des menteurs<sup><a id="link-3-42" href="#note-3-42">[42]</a></sup>&nbsp;»</citation>. Il est lui-même crétois, donc s’il dit la vérité,
        il ment, donc s’il ment, il dit la vérité&nbsp;<sup><a id="link-3-43" href="#note-3-43">[43]</a></sup>. Avec le designer et la neutralité il n’y a pas de quoi se retourner le cerveau bien longtemps&thinsp;:&nbsp;si j’affirme que le designer ne peut
        pas être neutre, étant moi-même designer, alors tout ce que j’écris ne peut pas l’être. Et c’est parce que je ne m’impose pas de l’être que je peux affirmer que le designer ne peut pas être neutre. Plus simplement, il est impossible d’être neutre
        dans une culture et un environnement donné, puisque ces derniers ne le sont pas. Les schémas présents dans nos vies, de choses très matérielles comme notre manière de nous habiller ou de manger jusqu’à l’idéologie politique dans laquelle nous baignons
        (le libéralisme) peuvent paraître neutre parce qu’elles sont majoritaires. Quelque chose de neutre peut correspondre à quelque chose qui ne dérange ni ne déroge à «&nbsp;l’ordre établi&nbsp;» d’un contexte spatio-temporel donné, une sorte de neutralité
        relative au cadre dans laquelle elle s’exerce.</p>
      <p>Mettre en forme un message est aussi important que penser le message en lui-même. L’habillage du message conditionnera la manière dont il sera reçu et le rôle du designer est donc crucial dans l’efficacité de la communication. Si ce travail est bien
        fait, le message sera renforcé, et s’il est mal fait, il sera affaibli, voir même contredit. Le simple fait que le terme «&nbsp;habillage&nbsp;» ait été utilisé dans le paragraphe ci-dessus illustre ce problème de neutralité : si le designer a pour
        mission d’habiller un message qu’il n’a pas participé à concevoir, il devient un intermédiaire formel, à la manière de la pratique de prescription en design d’espaces&thinsp;:&nbsp;le fait de répondre à une commande en proposant des objets et agencements
        issus de catalogues, en tentant de trouver une cohérence dans ce qui existe. Cette activité est pratiquée lorsqu’un projet ne justifie pas une recherche approfondie, soit par manque de ressources temporelles ou budgétaires, soit par manque d’une
        volonté, ou de l’intérêt d’un engagement vers une justesse formelle chez le commanditaire. Ainsi, le designer qui doit se résoudre à «&nbsp;habiller&nbsp;» un message est, de fait, imprégné du rapport au projet tenu par le commanditaire. Être neutre
        se résout, au mieux, à déléguer la responsabilité de son positionnement à la personne qui commande ou conçoit un message-objet de communication.</p>
      <p>De la même manière, si on tend à qualifier de neutre une participation non-imprégnée de style personnel à un acte de communication, nous avons collectivement créer une échelle de neutralité de modes de représentation, en nous basant sur l’évidence
        biaisée de notre sens de vision comme modèle d’objectivité. Impossible d’être plus neutre que ce que l’on voit ! Collectivement, la discipline pense le dessin comme imprégné d’une position personnelle, mais la photographie comme plus ou moins neutre,
        par la transparence du médium. Cela n’est d’ailleurs pas spécifique à la profession&thinsp;:&nbsp;l’histoire rapportée par la Bible de Saint-Thomas, déclinée dans le langage courant dans l’expression «&nbsp;le voir pour le croire&nbsp;», illustre
        ce propos&thinsp;:&nbsp;Saint-Thomas était incrédule face à la crucifixion de Jésus, et, quoi qu’aient pu lui en rapporter les disciples de Jésus, refusait d’y croire avant d’en avoir fait l’expérience, par la vision, puis par le toucher. Si la
        partie relative au toucher n’est pas passée dans le langage courant, elle est aussi pertinente&thinsp;:&nbsp;Thomas déclare avoir besoin de l’expérience la plus proche d’un phénomène pour accepter son existence. On peut aussi explorer cette idée
        en revenant à un des principes du Style International&thinsp;:&nbsp;la préférence des photographies plutôt que des illustrations ou du dessin pour accompagner les textes. Ce médium était vu comme la représentation la plus objective et non biaisée,
        évitant le passage par la traduction personnelle avant la restitution graphique qui s’opère avec le dessin. La nature même de la photographie semble suffire selon eux à garantir que les choses sont montrées telles qu’elles le sont en réalité. Dans
        cette logique, le photographe est vu, au même titre que le designer, comme un faiseur de forme qui ne devrait pas laisser apparaître ses opinions ou prendre position. Pourtant, toute mise en forme est une prise de position, et Adam Broomberg<sup><a id="link-3-44" href="#note-3-44">[44]</a></sup>      affirmait en 2013&thinsp;:&nbsp;<br>      <citation :inline="1" :tr="1">Tout ce qui sort d’une caméra est un document politique. Si je prends une photographie de la moquette, c’est un document politique.<sup><a id="link-3-45" href="#note-3-45">[45]</a></sup></citation>
      <p>Cette réflexion a été aussi largement évoquée par Susan Sontag dans son livre <em>On Photography</em><sup><a id="link-3-46" href="#note-3-46">[46]</a></sup>, où elle met en lumière les problématiques morales et esthétiques qui accompagnent la forme
        photographique. Elle commence par expliquer en quoi la photographie est vue comme une restitution transparente de la réalité : <br>
      <citation :inline="1" :tr="1">Les photographies sont des pièces à conviction. Ce dont nous entendons parler, mais dont nous doutons, peut nous paraître certain une fois qu’on nous en a montré une photographie.<sup><a id="link-3-47" href="#note-3-47">[47]</a></sup></citation><br>Sontag met ici en avant le pouvoir d’autorité que porte la photographie et la manière dont elle est traitée comme un miroir de la réalité. Cependant, elle explique en quoi la photographie doit toujours être vue comme une interprétation du monde, au-delà
        de sa nature qui pourrait mener à l’affirmation inverse&thinsp;:&nbsp;
        <text-imago :num="'07'" :cat="'g'" :caption="`Renversement de la statue de Saddam Hussein le 9 avril 2003 sur la Place Ferdaous à Bagdad (Irak), jour de la chute de la ville aux mains des troupes américaines. La position de la statue indique que les deux photographies ont été prises à très peu d’intervalles l’une de l’autre, mais donne un aperçu très différent du même événement. En haut, la foule semble plutôt clairsemée et calme, tandis que sur celle d’en bas, en plan serré, elle semble se presser en désordre autour de la statue.`"></text-imago>
      </p>
      <citation :big="1" :tr="1">En décidant à quoi devrait ressembler une image, en préférant une valeur d’exposition à une autre, les photographes imposent toujours des critères à leurs sujets. Bien que dans un sens la caméra capte en effet la réalité, ne fait pas que l’interpréter,
        les photographies sont autant une interprétation du monde que les peintures ou les dessins.<sup><a href="#note-3-47">[47]</a></sup></citation>
      <p>Cette réflexion sur la photographie est également un point de départ intéressant pour questionner le rapport texte-image. En tant que designer graphique,
        on est amené à manipuler ces deux formes et à les faire coexister sur un même format, et encore une fois, il n’y a pas de neutralité possible dans la réalisation de cette tâche. On a vu précédemment avec la pensée de Roland Barthes<sup><a id="link-3-48" href="#note-3-48">[48]</a></sup>      que le caractère polysémique des images entraîne une nécessité de guider le lecteur grâce à un message linguistique. Néanmoins, rien ne prouve que c’est toujours le texte qui va jouer ce rôle de guide du sens de l’image, et il semble que dans bien
        des cas le texte et l’image se complètent. Il semble y avoir trois grands types de rapport texte-image&thinsp;:&nbsp;la <em>complémentarité</em>, la <em>redondance</em> et le <em>contrepoint</em><sup><a id="link-3-49" href="#note-3-49">[49]</a></sup>,
        c’est-à-dire qu’ils peuvent se compléter, se répéter ou se contredire. Le designer, en sélectionnant les images qui accompagnent les textes renforce ou subvertit le message. Selon Teju Cole dans son article «&nbsp;Against Neutrality&nbsp;», les images et le texte entretiennent une relation de garantie mutuelle&thinsp;:&nbsp;<br><citation :inline="1" :tr="1">L’image et les mots apparaissent de façon simultanée. Ils se garantissent l’un l’autre. Vous croyez plus les mots parce que l’image les vérifie, et vous avez une plus grande confiance en la photographie parce que vous faites confiance aux mots. De plus, chacun augmente la pression sur l’interprétation&thinsp;:&nbsp;un photographe de guerre peut par exemple faire passer une situation triste comme acceptable, tout comme l’histoire d’un scandale peut faire paraître pathétique le politicien représenté.<sup><a id="link-3-50" href="#note-3-50">[50]</a></sup></citation></p>
      <p>Il est évident que ce rapport texte-image est très parlant dans le domaine du journalisme, où l’art de la Une opère depuis longtemps, et où le choix du duo titre-image est crucial. Dans le cas du designer graphique, il peut être amené à décider lui-même
        des textes et des images avec lesquels il travaillera, mais avouons-le, c’est très rare. Dans beaucoup de cas, il se retrouve avec l’obligation d’utiliser un texte qu’il n’aura pas participé à construire et des images qu’il n’aura pas sélectionnées.
        Si le contenu linguistique et iconique qui ensemble formeront le message sont pensés sans le designer, alors le positionnement de ce dernier est transparent et il n’en ressort que celui du commanditaire.</p>
      <p>Il y a donc une forme d’engagement dans le choix des commanditaires. En effet, il semble délicat pour un designer de travailler pour un commanditaire dont le positionnement éthique ne serait pas compatible avec le sien. S’il est évidemment impossible
        de toujours travailler sur des commandes qui seraient à cent pour cent compatibles avec ses propres valeurs et engagements, trouver un point d’équilibre semble primordial au bon déroulement d’un projet. On peut penser que le résultat juste d’une
        commande se situe dans l’espace se trouvant à l’intersection du positionnement du commanditaire et de celui du designer. Si dans certains cas l’absence d’une possible intersection peut être évidente dès le départ, son existence ne peut être que
        supposée au début d’un projet. Le travail de recherche de cette intersection ne peut se faire qu’au fur et à mesure d’un projet et le résultat final pourra témoigner de la rencontre des positionnements. </p>
      <p>Ainsi, les matériaux que nous utilisons en temps que designer ne sont pas neutres, et il en va de même pour le contexte de création et d’existence de notre travail. Avoir la volonté d’adopter un positionnement neutre semble comparable à un désengagement
        au profit de la position majoritaire. Ce type de mécanisme inconscient est à questionner au regard des responsabilités sociales du designer que nous verrons dans la suite de ce texte. Argumenter qu’un bon design est invisible ne revient-il pas à
        abaisser le processus de design à une simple stylisation ? On peut voir ce positionnement comme un refus de prendre les responsabilités qui accompagnent le pouvoir que nous avons de communiquer des messages. Peut-être plus important encore, ne manquons-nous
        pas l’opportunité de parler de l’impact politique et social de notre travail ? Ainsi, l’exploration des conditions culturelles de production et de lecture des images faite en première partie n’aurait pas suffi à expliquer l’environnement visuel
        si étroitement lié à l’idéologie politique et économique dont nous avons hérité : Jean Gadrey, économiste, dira à ce propos dans <em>Les Nouveaux Chiens de garde</em>, adaptation éponyme du livre de Serge Halimi&thinsp;:&nbsp;<br><citation>Tout ne s’explique pas par l’économie, mais il faut bien être clair&thinsp;:&nbsp;rien ne s’explique sans elle. Comment comprendrait-on l’appauvrissement de l’information si on ne prenait pas en compte une politique systématique de réduction des coûts
        ?&nbsp;<sup><a id="link-3-51" href="#note-3-51">[51]</a></sup></citation> Si Gadrey parle ici de journalisme, le fonctionnement et les conséquences de notre profession sont suffisamment similaires pour qu’on puisse faire le parallèle&thinsp;:&nbsp;stagariat, travail précaire, mise en concurrence, et mise en forme de messages
        dont nous devons être les vecteurs muets.</p>

