          personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;Anything that comes out of that camera is a political document. If I take a shot of the carpet, that’s a political document.&nbsp;»</li>
        <li><span id="note-3-46"><a href="#link-3-46">[46]</a></span>. Susan Sontag, [1977] <em>On Photography</em>, Penguin Books, 2008. 207&thinsp;p.</li>
