        <li><span id="note-3-49"><a href="#link-3-49">[49]</a></span>. Bernard Marquetti, Les rapports images/textes, in&thinsp;:&nbsp;Études de communication, 10, 1989. Disponible en ligne&thinsp;:&nbsp;<a href="http://edc.revues.org/2866">edc.revues.org</a></li>
        <li><span id="note-3-50"><a href="#link-3-50">[50]</a></span>. Teju Cole, Against Neutrality, in&thinsp;:&nbsp;The New York Times Magazine, 2016. Disponible en ligne&thinsp;:&nbsp;<a href="https://www.nytimes.com/2016/01/17/magazine/against-neutrality.html?r=0">www.nytimes.com</a><br>Traduction
          personnelle. Citation originale&thinsp;:&nbsp;«&nbsp;The photograph and the words arrive simultaneously. They guarantee each other. You believe the words more because the photograph verifies them, and trust the photograph because you trust the
          words. Additionally, each puts further pressure on the interpretation&thinsp;:&nbsp;A war photograph can, for example, make a grim situation palatable, just as a story about a scandal can make the politician depicted look pathetic. »</li>
        <li><span id="note-3-51"><a href="#link-3-51">[51]</a></span>. Gilles Balbastre et Yannick Kergoat, <em>Les Nouveaux Chiens de garde</em>, <br>2012, 104 min.</li>
      </ul>





      <h3 id="p2-2-5">Aparté : Retranscription d’une conférence de Slavoj Žižek chez Google en 2008</h3>


      <p>Le philosophe présente lors de cette conférence son livre <em>Violence</em>. Cette intervention constitue, il me semble, une lecture intéressante pour clore ce passage sur la neutralité. Il expose une réflexion sur l’idéologie et en quoi elle régit
        nos vies.<br>Slavoj Žižek est connu pour ses travaux sur Lacan et Hegel. Il est chercheur à l’université de Ljubljana et notamment connu pour ses livres <em>Parallaxe</em> et <em>Fragile Absolu</em>. La retranscription et la traduction ont été faites
        par mes soins.</p>

      <p>La vidéo est visionnable en ligne&thinsp;:&nbsp;<a href="https://www.youtube.com/watch?v=_x0eyNkNpL0">www.youtube.com</a></p>

      <citation :tr="1">[...] Maintenant, l’attitude dialectique nécessaire à aborder ce livre est à l’opposée du sujet même du livre. Ne pas se concentrer sur la violence, mais sur ce à quoi la violence réagit, sur la texture quotidienne de nos vies et l’idéologie dans
        laquelle nous baignons.<br>Maintenant, votre réaction devrait être&thinsp;:&nbsp;«&nbsp;Mais ne vivons-nous pas dans une époque post-idéologique ?&nbsp;» Bon, je suis un vieux marxiste, comme le type de mecs qui sont morts en 1990, qui croyaient encore
        aux grandes causes, etc.<br>Je vais essayer de vous convaincre que l’idéologie n’est évidemment pas à prendre dans le sens d’une grande vision du monde qui s’imposerait à la société, mais comme un réseau compliqué d’éthique, de politique, de social,
        de préjugés, qui même si nous y sommes alertes, déterminent toujours la façon dont nous fonctionnons. C’est toujours quelque chose d’idéologique qui régit notre vie.<br>Qu’est-ce qu’est l’idéologie ? Peut-être que certains d’entre vous le savent,
        mais je ne peux pas m’empêcher de vous raconter cette histoire, car elle est parfaite pour introduire ce sujet.<br>Vous vous souvenez, de cette dramatique interview de Donald Rumsfeld il y a environ cinq ans, juste avant la guerre du Golfe, dans
        laquelle il voulait expliquer à quel point Saddam Hussein était dangereux ? Il a utilisé sa célèbre analogie au sujet des choses que nous savions savoir, les connus connus. Nous savions donc que Saddam était le «&nbsp;big boss&nbsp;» de l’Irak.</citation>
      <citation :tr="1">[ Phrase en question de Donald Rumsfeld&thinsp;:&nbsp;Les rapports qui disent que quelque chose n’est pas arrivé sont toujours intéressants à mes yeux, car comme nous le savons, il y a les connus connus; il y a les choses que nous savons savoir. Nous savons aussi qu’il y a des inconnus connus; ce qui veut dire que ne savons même pas que nous ne savons pas. Mais il y a aussi les inconnus inconnus - ceux que nous ne savons pas ne pas savoir. Si vous regardez à travers l’histoire de notre pays et des pays libres,	c’est cette dernière catégorie qui pose problème.<sup><a id="link-3-52" href="#note-3-52">[52]</a></sup>]</citation>
      <citation :tr="1">Puis il a continué : il y a aussi des inconnus connus, des choses que nous savons ne pas savoir. Comme, par exemple, je sais qu’il y a des voitures en face de ce bâtiment, mais je ne sais pas combien il y en a. Je sais que je ne le sais pas. De cette
        manière, si nous ne savions pas de combien d’armes Saddam disposait, ni où est-ce qu’elles étaient planquées&thinsp;:&nbsp;nous savions ne pas le savoir.<br>Et puis il a continué : il y a encore des inconnus inconnus, au sens où nous ne savons
        même pas que nous ne savons pas. Là, sa paranoïa gonflait&thinsp;:&nbsp;Et si il existait des armes secrètes en Irak, dont nous ne connaissions ni la nature, ni la position, radicalement plus inconnues ? C’est un inconnu inconnu. Maintenant, si
        vous avez un peu d’attrait pour l’analyse structurelle, vous verrez immédiatement que quelque chose manque ici, un quatrième terme. Nous avons le connu connu, l’inconnu connu, et les inconnus inconnus. Nous savons, nous savons ne pas savoir, et
        nous ne savons même pas si nous devrions savoir.<br>C’est la catégorie la plus intéressante qui manque ici&thinsp;:&nbsp;le connu inconnu, ce que nous ne savons pas savoir, c’est l’inconscient idéologique. Il forme les préjugés silencieux, qui déterminent
        notre manière d’agir, de réagir, et ce que nous sommes. En quelque sorte, nous évoluons dans un tissu idéologique tellement complexe et diffus que nous ne savons même pas que ces comportements découlent de cet ensemble de connus qu’est l’idéologie.
        C’est cela même qui précipita le discours de Rumsfeld, ce connu inconnu.</citation>

      <ul class="Footnotes">
        <li><span id="note-3-52"><a href="#link-3-52">[52]</a></span>. Extrait d’un interview de Donald H. Rumsfeld le 12 février 2002<br>à Washington. La retranscription est disponible en ligne&thinsp;:&nbsp;<a href="http://archive.defense.gov/Transcripts/Transcript.aspx?TranscriptID=2636. ">archive.defense.gov</a><br>Traduction
          personnelle. Citation originale : «&nbsp;Reports that say that something hasn’t happened are always interesting to me, because as we know, there are known knowns; there are things we know we know. We also know there are known unknowns; that is
          to say we know there are some things we do not know. But there are also unknown unknowns - the ones we don’t know we don’t know. And if one looks throughout the history of our country and other free countries, it is the latter category that tend
          to be the difficult ones. »</li>
      </ul>
    </partie>

    <partie :name="'Corps'">
      <h2 id="p3">Rôle et responsabilité du designer graphique</h2>


      <p>Re-situer la production actuelle dans son contexte d’existence économique était donc nécessaire, en plus de son contexte d’existence culturel. Mais une question reste en suspens&thinsp;:&nbsp;quelle est la réelle responsabilité du designer graphique
        quant à l’impact de ses productions ? </p>

      <h3 id="p3-1">Construire l’aile d’un avion</h3>
      <h4>quelle responsabilité du designer face à sa production ?</h4>


      <p>Pour reprendre l’énoncé de cette partie, la construction de l’aile d’un avion, des plans jusqu’à la fabrication, nécessitent un processus de conception rigoureux et une pleine conscience de la part de tous les acteurs que l’erreur et l’approximation
        ne sont pas permises. Ce domaine fait partie de ceux qui suivent des protocoles, qui ne supportent pas l’approximation. Une affiche ou un logo, eux, ne tueront personne<sup><a id="link-4-1" href="#note-4-1">[1]</a></sup>.
        <text-imago :num="'01'"
                    :cat="'h'" :caption="`«Affiche Rouge», affiche de propagande du régime de Vichy en 1944`"></text-imago>
      </p>
      <p>Bien sûr, le processus de design graphique ne doit pas être régi par des protocoles rigides comme ceux que l’on trouve en ingénierie, mais cette apparente innocuité ne pourrait-elle pas entraîner moins de considération vis-à-vis des responsabilités
        sociales et morales que porte notre profession ?</p>
      <p>Victor Papanek, connu pour son livre <em>Design pour un monde réel</em><sup><a id="link-4-2" href="#note-4-2">[2]</a></sup> évoque tout au long de son travail la responsabilité éthique de créer des choses qui auront un impact bénéfique sur le monde,
        dénonçant à de multiples reprises une forme de design irresponsable entraîné par un consumérisme toujours plus débridé et qui se donne à voir par des productions tape-à-l’oeil, dangereuses ou inutiles. <em>Design pour un monde réel</em> est traversé
        par une pensée directrice&thinsp;:&nbsp;la question n’est pas de savoir si on peut le faire, mais si on doit le faire. Papanek introduit l’idée de ne pas travailler sur un projet allant à l’encontre du bien commun, ou de ne pas travailler pour quelque
        chose qui ne nécessite pas vraiment l’intervention du designer. C’est une pensée qu’il expose et illustre de différentes manières au fil de son livre et qu’il pousse parfois jusqu’à son extrême, c’est-à-dire ne plus travailler du tout&thinsp;:&nbsp;<br><citation :tr="1" :inline="1">Dans un environnement qui est visuellement, physiquement, et chimiquement bloqué, ce que les architectes, les designers industriels, les planificateurs, etc., pourraient faire de mieux pour l’humanité serait de cesser complètement leur travail. Dans
        toute pollution les designers ont leur part de responsabilité.<sup><a id="link-4-3" href="#note-4-3">[3]</a></sup></citation> Cette phrase apparaît au tout début de <em>Design pour un monde réel</em> et pourrait sembler défaitiste et sombre s’il ne poursuivait pas par&thinsp;:&nbsp; <br> <citation :tr="1" :inline="1">Dans ce livre, j’adopte une vision plus constructive&thinsp;:&nbsp;le design peut et doit devenir un moyen pour les jeunes de participer à l’évolution de la société.<sup><a id="link-4-4" href="#note-4-4">[4]</a></sup></citation>
      <p>Pour Papanek, le design peut être défini comme <citation :tr="1" :inline="1">«&nbsp;l’effort conscient et intuitif de définir un ordre ayant du sens<sup><a  href="#note-4-5">[5]</a></sup>&nbsp;».</citation> Le terme conscient renvoie à <citation :tr="1" :inline="1">«&nbsp;l’intellectualisation, la recherche et l’analyse<sup><a  href="#note-4-5">[5]</a></sup>&nbsp;»</citation> tandis que le terme intuitif,
      qui ne figurait d’ailleurs pas dans la première version du texte, renvoie aux <citation :tr="1" :inline="1">«&nbsp;impressions, idées et pensées que nous avons, sans le savoir, collectées dans notre subconscient, inconscient et préconscient<sup><a id="link-4-5" href="#note-4-5">[5]</a></sup>&nbsp;»</citation>.
        Pour Papanek, le design ne doit pas être considéré comme une chose en elle-même, mais plutôt être vu comme une activité pratiquée par tous les hommes, tout le temps. Je me permets de glisser ici l’extrait du livre qui éclaire cette idée, même s’il
        est un peu long, car il a eu une forte influence au départ de ma recherche&thinsp;:&nbsp;<br>
      <citation :tr="1" :inline="1">Les hommes sont tous des designers. La plupart de nos actes se rattachent au design, qui est la source de toute activité humaine. La préparation et le modelage de toute action en vue d’une fin désirée et prévisible&thinsp;:&nbsp;tel est le processus
        du design. Toute tentative pour le rendre indépendant, pour en faire un chose-en-soi, va à l’encontre de sa valeur intrinsèque de première matrice fondamentale de la vie. Le design, c’est composer un poème épique, réaliser une fresque, peindre un
        chef d’oeuvre, écrire un concerto. Mais c’est aussi vider et réorganiser un tiroir de bureau, extraire une dent cariée, faire cuire une tarte aux pommes, choisir les équipes pour un jeu de base-ball et éduquer un enfant.<sup><a id="link-4-6" href="#note-4-6">[6]</a></sup></citation>
      <p>Si on replace les propos de Papanek dans une perspective contemporaine, on voit assez naturellement le type de design qu’il tend à combattre dans ces propos. Un des exemples qui semble parfaitement entrer dans cette catégorie est l’architecture urbaine
        hostile, c’est-à-dire une manière de penser et construire l’espace public afin qu’il soit utilisé seulement d’une certaine façon. Les bancs sont un des exemples les plus connus de cette forme de design&thinsp;:&nbsp;beaucoup sont conçus de sorte
        que l’on puisse s’y asseoir, mais jamais s’y allonger. Ainsi, les personnes sans abris ne peuvent pas les utiliser pour dormir. Vers un même but, certains recoins de villes présentent des pics sur le sol et empêche quiconque de s’asseoir ou de s’allonger
        à l’endroit donné.
        <text-imago :num="'02'" :cat="'h'" :caption="`Dispositif anti-SDF à Paris`"></text-imago>
        <text-imago :num="'03'" :cat="'h'" :caption="`Sarah Ross, Archisuit, 2005. Jogging matelassé qui s’adapte au mobilier urbain de Los Angeles.`"></text-imago>
      </p>
      <p>La visée des exemples précédents est facilement identifiable tandis que d’autres exemples d’architecture urbaine hostile sont plus durs à déceler, comme par exemple des caractéristiques de la planification urbaine de la ville de New-York. L’urbaniste
        Robert Moses, en charge de la rénovation de New-York entre 1930 et 1970 a été largement critiqué notamment à cause du caractère discriminatoire de ses réalisations. Il semble qu’il est mis un point d’honneur à ce que certaines parties de la ville,
        comme les plages de Long Island, ne soient pas fréquentées par les populations les plus pauvres. Pour ce faire, il a fait construire des ponts d’une hauteur d’environ deux mètres, empêchant ainsi les bus de passer dessous.
        <text-imago :num="'04'"
                    :cat="'h'" :caption="`Un pont au abords de Long Island aux États-Unis`"></text-imago> En comparaison, les autres ponts font entre quatre et cinq mètres de hauteur dans les autres parties de la ville. De cette manière, l’accès à ces plages devient beaucoup plus difficile pour les personnes pauvres qui n’ont pas de voiture
        et dépendent des transports en commun.</p>
      <p>En se rapprochant de la communication visuelle, il y a un domaine qui polarise les critiques&thinsp;:&nbsp;la publicité. La célèbre phrase de V. Papanek semble résumer assez fidèlement le but de la publicité : <br><citation :tr="1" :inline="1">Il n’y a peut-être qu’une seule autre profession qui soit plus factice [que le design industriel] : le design publicitaire, qui persuade les gens d’acheter des objets dont ils n’ont pas besoin, avec de l’argent qu’ils n’ont pas, afin d’impressionner
        d’autres gens qui s’en moquent.<sup><a id="link-4-7" href="#note-4-7">[7]</a></sup></citation> L’omniprésence de la publicité dans nos vies est un sujet déjà largement débattu dans la société, et il me semble donc inutile de le développer plus que ça ici.</p>
      <p>Ces exemples font écho à des propos d’Alfonso Natalini, fondateur de Superstudio<sup><a id="link-4-8" href="#note-4-8">[8]</a></sup> qui interroge dès les années 1970 la responsabilité de l’architecte, et du concepteur en général, qui doit pour eux
        «&nbsp;tenter de réévaluer son rôle dans le cauchemar qu’il a contribué à concevoir&nbsp;». Il pousse lui aussi cette pensée jusqu’à un rejet du design et de l’architecture tels qu’ils sont pratiqués à l’époque. Ces propos datent de plus de quarante
        ans, mais comme on l’a vu avec les exemples précédents, ils semblent pouvoir parfaitement se transposer dans la société contemporaine&thinsp;:&nbsp;<br><citation :tr="1" :inline="1">[…] si le design est plutôt une incitation à consommer, alors nous devons rejeter le design ; si l’architecture sert plutôt à codifier le modèle bourgeois de société et de propriété, alors nous devons rejeter l’architecture ; si l’architecture et
        l’urbanisme sont plutôt la formalisation des divisions sociales injustes actuelles, alors nous devons rejeter l’urbanisation et ses villes… jusqu’à ce que tout acte de design ait pour but de rencontrer les besoins primordiaux. D’ici là, le design
          doit disparaître. Nous pouvons vivre sans architecture.<sup><a id="link-4-9" href="#note-4-9">[9]</a></sup></citation></p>
      <p>Définir le rôle et les responsabilités du designer graphique ne peut se faire que de façon subjective et personnelle tant les pratiques sont diverses et il ne s’agira en rien de tenter d’en donner une définition unique. Il s’agirait plutôt de définir
        des types de pratiques qui dessinent dans un même temps des rôles et des responsabilités. On pourrait emprunter ici les trois catégories de pratiques énoncées par Étienne Ozeray dans son mémoire <em>Pour un design graphique libre</em><sup><a id="link-4-10" href="#note-4-10">[10]</a></sup>,
        c’est-à-dire que le design graphique <em>rend lisible</em>, <em>rend visible</em> et <em>rend possible</em>. Il ne faut pas envisager ces catégories de pratiques comme indépendantes les unes des autres, car c’est justement leurs rencontres qui rendent
        ce métier complexe, exigeant et inépuisable dans les questionnements qu’il soulève. Étienne Ozeray développe des définitions de chacun de ces termes, mais il s’agira plutôt ici d’un développement personnel qui pourra légèrement différer de ses propres
        définitions.</p>
      <p>Il faut déjà faire une distinction entre les termes <em>lisible</em> et <em>visible</em> qui peuvent paraître très proches mais qui ne se rapportent pas à la même notion. <em>Rendre lisible</em> fait référence à la mise en forme d’éléments intangibles
        (savoirs, données, informations, etc.) afin d’en permettre ou d’en faciliter la lecture tandis que rendre visible renvoie au besoin de visibilité de tout type de production, au besoin d’être vu à différentes échelles. Malgré la proximité des deux
        termes, il convient de les différencier car on verra qu’ils ne soulèvent pas les mêmes enjeux.</p>
      <p><span class="ut-alegreyabold">→</span> Tout d’abord, le design graphique rend lisible, et le designer est un acteur majeur de l’accès et de la transmission des savoirs en permettant leur existence formelle. Les savoirs ou les données n’ont pas de
        forme prédéfinie et c’est au designer de les donner à voir. Le livre par exemple, essentiel dans la transmission des savoirs et des connaissances ainsi que dans leur conservation, est sans cesse repensé dans sa forme afin de d’optimiser la lecture.
        On peut aussi penser aux données chiffrées, qui sont souvent illisibles et indigestes à moins d’en donner une visualisation graphique. Ici encore, le rôle du designer graphique est essentiel à la lisibilité de ses informations. De la même manière,
        le design rend lisible l’environnement qui nous entoure à travers notamment la signalétique. Dans des lieux saturés et complexes (aéroports, hôpitaux, gares, etc.), nos repères sont souvent brouillés et les indications visuelles sont les seuls éléments
        qui permettent de trouver son chemin. Ainsi, le design graphique rend lisible le monde qui nous entoure, que ce soit en donnant une existence formelle à des éléments abstraits ou en nommant l’existant pour tracer des chemins.</p>
      <p><span class="ut-alegreyabold">→</span> Une autre tâche qui fait partie du champ du design graphique est de <em>rendre visible</em>. L’incarnation formelle d’une recherche de design n’a souvent que peu d’intérêt si elle ne se confronte pas à un public.
        C’est en ça que le travail du designer sera d’assurer à sa production une visibilité, à des échelles qui peuvent aller du local à l’international. Bien sûr, il n’est pas le seul acteur de cette visibilité puisque des intermédiaires de diffusion
        prendront le relais sur le plan technique, mais il doit néanmoins s’assurer que sa création sera repérable parmi les autres. Sans forcément être dans une démarche de concurrence vis-à-vis des productions qui cohabiteront avec la sienne, il doit
        veiller à ce qu’elle se démarque assez pour attirer les regards. Cette catégorie, <em>rendre visible</em>, doit être envisagée avec un certain recul critique car elle s’inscrit dans un contexte de mise en concurrence caractéristique du libéralisme.
        <text-imago :num="'04'" :cat="'e'" :caption="`Des graffitis sur les toits de Paris`"></text-imago> En effet, la diffusion d’une image implique sa mise en concurrence formelle avec les autres et implique de travailler de manière à créer une forme qui se démarque. </p>
      <p>Néanmoins, sur le plan physique, autant à cause du temps très court d’affichage que par la multiplication d’année en année du nombre d’espaces d’affichage (et de la diversification de leur nature), il est difficile de réellement se démarquer. Ainsi,
        si ce domaine, <em>rendre visible</em>, est soumis aux mêmes principes de mise en concurrence que ceux soulevés plus tôt sous la forme d’un libéralisme débridé appelé uberisation, il est aussi au service de ce système de soumission des individualités.
        De la même manière que le livreur à vélo est remplaçable et précaire, nous sommes en situation de concurrence dans notre diffusion, nos images sont précaires. <em>Rendre visible</em>, dans un contexte physique d’affichage déjà surchargé, c’est écraser,
        c’est se tailler une place en s’imposant à l’autre, et à sa production, et, si notre production est publicitaire, c’est donner de la légitimité, et sa propre complicité, aux idéologies desquelles découlent ces principes.</p>
      <p>Le graff,
        <text-imago :num="'02'" :cat="'h'" :caption="`Robin Kinross, La Typographie Moderne, un essai d’histoire critique, B42, 2012`"></text-imago> milieu illégaliste, a bien compris cela&thinsp;:&nbsp;aux antipodes d’une communication marchande, l’espace est certes partagé pour se diffuser, mais écraser l’autre est une violation d’un code déontologique implicite, et sera lourdement
        puni. Un graffeur qui en toye un autre (recouvrir son graff) sera recouvert, sans merci, pour préserver cette promesse implicite de se partager équitablement l’espace. On pourrait voir dans cette pratique une concurrence raisonnée, à l’opposé d’une
        concurrence violente. Ainsi, <em>rendre visible</em> est une tâche qui ne s’inscrit pas par nature dans une démarche de concurrence violente, mais qui est néanmoins soumise aux caractéristiques de notre temps dans lequel ce type de mise en concurrence
        est reine. Ainsi, il semble important d’avoir du recul en tant que designer et d’être en capacité de trouver sa position dans cette pratique.</p>
      <p><span class="ut-alegreyabold">→</span> Le design graphique qui rend possible fait référence à la recherche en design graphique, qu’elle soit fondamentale ou appliquée. Des publications comme la revue <em>Graphisme en France</em>,
        <text-imago :num="'07'"
                    :cat="'h'" :caption="`<em>Graphisme en France</em>, n°23`"></text-imago> les ouvrages parues chez B42
        <text-imago :num="'06'" :cat="'h'" :caption="`<em>Azimut 40-41</em>, ESADSE/Cité du Design, 2014`"></text-imago> ou encore <em>.txt</em>
        <text-imago :num="'09'" :cat="'h'" :caption="`<em>.txt 2</em>, B42, 2015`"></text-imago> ou <em>Azimut</em>
        <text-imago :num="'08'" :cat="'h'"></text-imago> montrent l’émergence d’un réel intérêt pour la recherche autour du design graphique en France. Le terme <em>recherche</em> peut évoquer une multitude de contextes et de pratiques, il semble donc important de commencer par définir le
        cadre dans lequel la recherche en design graphique s’inscrit. Dans «&nbsp;La recherche actuelle en design graphique&nbsp;»<sup><a id="link-4-11" href="#note-4-11">[11]</a></sup>, Alice Twemlow en dégage trois natures&thinsp;:&nbsp;la recherche <em>sur</em>      le design graphique, la recherche <em>pour</em> le design graphique et la recherche <em>par</em> le design graphique. Ces trois catégories ont été théorisées dès les années 1960 au Royaume-Uni.</p>
      <p>La recherche <em>sur</em> le design graphique se focalise sur sa pratique, à la fois dans son histoire, sans ses problématiques contemporaines et dans son avenir. Cette nature de recherche questionne la profession même de designer graphique et a pour
        but selon A. Twemlow de <citation :tr="1" :inline="1">«&nbsp;documenter, d’interpréter, d’évaluer et d’éclairer le savoir lié au design graphique dans son ensemble<sup><a id="link-4-12" href="#note-4-12">[12]</a></sup>&nbsp;»</citation>.</p>
      <p>Une seconde nature, la recherche <em>pour</em> le design fait référence à une recherche incorporée dans le temps d’un projet comme l’étude du sujet traité lors du projet ou de paramètres qui entreront en jeu dans sa réalisation. Ce type de recherche
        permettra de dégager des enjeux qui influeront sur le résultat final.</p>
      <p>Pour finir, on peut aussi définir une recherche <em>par</em> le design graphique où cette fois la discipline est utilisée comme médium <citation :tr="1" :inline="1">«&nbsp;pour susciter des questionnements, réunir des données et les interpréter dans le but d’enrichir la connaissance<sup><a id="link-4-13" href="#note-4-13">[13]</a></sup>&nbsp;»</citation>.
        Cette fois, le design graphique est vu comme un outil qui permet de soulever et mettre en images des problématiques qui sortent de son propre champ. </p>
      <p>Ainsi le terme <em>rendre possible</em> fait référence aux différents processus de recherches théoriques ou formelles qui permettent un recul sur la pratique même de la profession et sur les modèles dans lesquels elle s’inscrit. La design graphique
        rend ainsi possible une réflexion sur les évolutions de la société à différents points de vue (sociales, techniques, politiques, etc.), permettant ainsi aux productions de mettre à l’épreuve ces nouveaux paradigmes. Évidemment, il semble impératif
        que ces recherches et expérimentations soient partagées et diffusées au plus grand nombre afin d’impacter et de faire évoluer la profession entière.</p>
      <p>Que ce soit dans la première partie de ce texte, <em>images et idéologie</em>, ou dans les exemples abordés dans celle-ci, on a vu que les productions de design, et celle de design graphique, pouvaient concrétiser formellement les stigmates de modèles
        sociaux ou politiques critiquables. On a vu par exemple avec la publicité, les nombreuses images excluantes ou discriminantes ou encore avec l’architecture urbaine hostile que la pratique du design peut s’avérer complice de ces modèles dominants
        actuels. La réponse de Victor Papanek, qui serait de cesser de travailler face à la pollution engendrée par les métiers du design ou alors de travailler vers un design qui s’évertue à questionner ce qui est profitable à tous, semble aussi pertinente dans les
        conditions actuelles qu’à l’époque où il l’a énoncée. Les pratiques de design graphique, regroupées ici sous les termes rendre <em>lisible, visible et possible,</em> définissent le champ d’action du designer graphique et dans un même temps ses responsabilités.</p>

      <ul class="Footnotes">
