      <h3 id="p3-2">QQOQCCP</h3>
      <h4>méthodes de projet dans d’autres domaines</h4>

      <p>Il semble pertinent de s’intéresser à ce qui se fait dans d’autres domaines car on y retrouve la plupart du temps une problématique commune&thinsp;:&nbsp;cibler, délimiter et résoudre un problème. Dans des professions liées par exemple au journalisme
        ou à l’ingénierie, des protocoles plus ou moins stricts, et plus ou moins appliqués à la lettre, entrent en jeu dans la phase d’analyse d’un problème. S’il semble évident qu’appliquer des protocoles stricts dans la phase de réalisation formelle
        d’un projet limiterait la liberté du designer et pourrait appauvrir son travail, il pourrait être néanmoins intéressant de questionner une possible transposition de méthodes issues d’autres domaines vers le design graphique dans la phase d’analyse
        d’un problème et du contexte d’intervention du designer.</p>
      <p>On a vu plus tôt qu’une des pratiques du design graphique était de <em>rendre lisible</em>, et cette catégorie renvoie assez naturellement au journalisme qui donne un accès et une lecture par la collecte, la vérification et le commentaire de faits.
        Une des méthodes de base dans ce métier est résumée par le sigle QQOQCCP <em>(quoi ? qui ? où ? quand ? comment ? combien ? Pourquoi ?)</em>, qui correspond en quelque sorte aux 5W dans la presse anglo-saxonne. Il s’agit d’une méthode de raisonnement
        qui vise à définir une situation avec un maximum de précision et permet de dresser un état des lieux qui permettra d’obtenir une qualité du commentaire qui viendra ensuite. Cette méthode est principalement utilisée en journalisme, mais sa simplicité
        permet de l’utiliser dans n’importe quelle condition nécessitant une récolte d’informations précises et de manière la plus exhaustive possible. Elle permet à la fois une vue globale d’un contexte de travail et la mesure du niveau de connaissances
        que l’on possède de cette situation. En ce sens, cette méthode semble formaliser l’analyse préalable associée à tout projet de design.</p>
      <p class="nombot">On peut tenter de décrire individuellement chaque paramètre et voir de quelle manière elle peut devenir un outil d’aide à l’analyse du contexte d’un projet de design graphique.</p>
      <ul class="Body--list">
        <li>- Le <em>quoi</em> consiste en une description de la problématique et répond à la question «&nbsp;de quoi s’agit-il ?&nbsp;». La réponse sera assez simple et décrira l’objet même de la commande (identité visuelle, communication pour un évènement,
          brochures, etc.)</li>
        <li>- Le <em>qui</em> concerne les parties prenantes du projet, c’est-à-dire le commanditaire en premier lieu, mais également les prestataires et partenaires. Il semble primordial dans le cas d’une commande de design graphique d’ajouter au qui le pour
          qui, qui portera sur les destinataires de la production, qu’il s’agisse d’un groupe large ou d’un groupe plus réduit.</li>
        <li>- Ensuite, le <em>où</em> et le quand peuvent se rapporter au contexte spatio-temporel dans lequel existera la production, et ils correspondent aux questions «&nbsp;où sera diffusée la production ?&nbsp;» et «&nbsp;à quel moment ? pour combien de
          temps ?&nbsp;».</li>
        <li>- Le <em>comment</em> se concentre sur les méthodes qui seront utilisées, et dans le cas précis de la commande de design graphique, on peut par exemple penser à l’organisation des différentes étapes du projet selon un calendrier ou encore à la manière
          d’aborder le problème en fonction des priorités qui découleront de l’objectif final.</li>
        <li>- Le <em>combien</em> est certainement le plus pragmatique, mais aussi l’un des plus importants de tous ces paramètres. Il s’agit de déterminer de façon la plus précise possible les moyens affectés à l’objet de la commande, c’est-à-dire le budget
          dégagé, et dans certains cas les équipements qui pourront être utilisés par le cadre de cette commande. Il semble ici important d’ajouter que le combien affectera tous les autres paramètres et doit certainement être défini en premier.</li>
        <li>- Pour finir, le <em>pourquoi</em> décrit l’objectif final que devra remplir la production, répondant aux questions «&nbsp;dans quel but ?&nbsp;» ou «&nbsp;pour quelle finalité ?&nbsp;».</li>
      </ul>

      <p>Ainsi, cette méthode semble compatible avec la commande de design graphique et pourrait permettre de formaliser une série de questions qui semblent nécessaires pour poser un cadre et appréhender tous les paramètres d’une commande comme un ensemble.</p>
      <p>Il existe également un grand nombre de méthodes de raisonnement issues de l’ingénierie, notamment des raisonnements logiques qui entrent dans la catégorie des méthodes de résolution de problèmes ou de prévention de défaillances sur des systèmes. Une
        des méthodes assez simple de raisonnement logique, qui peut d’ailleurs être utilisée dans le cas de tout problème et dans tous les domaines est celle des cinq pourquoi. Cette méthode consiste à poser une première question «&nbsp;Qu’est-ce qu’il
        se passe ?&nbsp;» en essayant d’identifier et d’énoncer le plus clairement possible la situation et ensuite de poser successivement la question pourquoi pour arriver à une cause première. Il faut néanmoins respecter quelques règles pour que ce raisonnement
        soit efficace. Déjà, il faut rester factuel et rapporter ce qui s’est réellement passé en évitant les déductions et les suppositions. Ensuite, cette méthode n’a de l’intérêt que si elle se cantonne à des causes sur lesquelles il est possible d’avoir
        un contrôle.</p>

      <p>Prenons l’exemple suivant&thinsp;:&nbsp;<br> Situation&thinsp;:&nbsp;<br>Un lundi matin des employés d’une usine agro-alimentaire se rendent compte qu’une chambre froide dégage une forte odeur. Les produits ne sont plus frais et ne peuvent plus être
        vendus.</p>
      <ul class="Body--list Body--list__condensed">
        <li>Problème&thinsp;:&nbsp;Les aliments sont décongelés.</li>
        <li>- Pourquoi les aliments sont décongelés ?<br><span class="ut-alegreyabold">→</span> La chambre froide n’a pas fonctionné du weekend. </li>
        <li>- Pourquoi n’a-t-elle pas fonctionné ?<br><span class="ut-alegreyabold">→</span> La chambre froide n’a pas été alimentée.</li>
        <li>- Pourquoi n’a-t-elle pas été alimentée ?<br><span class="ut-alegreyabold">→</span> Il y a eu un orage et il n’y a pas de batterie de secours.</li>
        <li>- Pourquoi n’y en a-t-il pas ?<br>Cause principale <span class="ut-alegreyabold">→</span> Il n’y avait pas de batterie de secours prévue lors de l’installation de la chambre froide.</li>
      </ul>

      <p>Cette méthode très simple permet de ne pas s’arrêter à une cause qui ne serait finalement qu’un élément d’une chaîne cause-conséquence mais de remonter à une cause principale qui pourra annuler les causes précédentes. Il pourrait être intéressant
        d’envisager cette méthode dans le cadre de projet de design graphique car la production découle d’un problème ou d’un besoin. Si, par exemple, un commanditaire veut avoir une visibilité plus large pour un public donné, il faut en priorité se demander
        pourquoi cette visibilité n’est pas atteinte et trouver la cause principale. Bien sûr, si l’on remonte jusqu’à une cause première, il se peut qu’elle ne soit plus liée au travail même du designer graphique, mais c’est ce point qui semble intéressant.
        Si la cause principale du problème n’est pas identifiée, alors le designer, qu’importe la qualité de sa réponse, ne parviendra pas à l’éradiquer. C’est alors que se pose la question de la place du designer, doit-il se limiter à une production qui
        suit un cahier des charges ou doit-il établir ce cahier des charges entièrement avec le commanditaire en fonction du ou des paramètres problème-cause repérés ? Il semble évident que même si des causes sortent de son champ d’action, une position
        de conseiller pourra créer une véritable cohérence dans la réponse apportée, à la fois dans sa production, mais aussi dans les dispositions que le commanditaire pourra prendre face au problème traité. </p>
      <p>De la même manière, des méthodes de raisonnement logique plus complexes comme les arbres de défaillances<sup><a id="link-4-14" href="#note-4-14">[14]</a></sup> ou les diagrammes d’Ishikawa<sup><a id="link-4-15" href="#note-4-15">[15]</a></sup> (dont
        les articles Wikipédia respectifs sont détaillés) pourraient établir des processus intéressants concernant l’analyse de problèmes ou de besoins dans une commande de design graphique. </p>
      <p>Ces transpositions de méthodes issues d’autres domaines vers le design graphique a comme intérêt de formaliser des méthodes d’analyse d’un contexte de commande et de permettre un véritable questionnement de fond sur le problème ou le besoin qui sera
        traité par la production du designer. Il semble, en effet, primordial d’avoir la vue la plus approfondie possible du contexte de la commande pour produire une réponse juste et efficace. Ces transpositions ne sont pas des formules qui tendraient
        à normaliser la production mais, au contraire, elle pourrait permettre de s’éloigner d’une réponse préconçue pour aller vers une réponse véritablement adaptée au contexte et aux besoins ciblés.</p>

      <ul class="Footnotes">
        <li><span id="note-4-14"><a href="#link-4-14">[14]</a></span>. Article Wikipédia sur l’arbre de défaillances :<br><a href="https://fr.wikipedia.org/wiki/Arbre_de_d%C3%A9faillances">fr.wikipedia.org</a></li>
        <li><span id="note-4-15"><a href="#link-4-15">[15]</a></span>. Article Wikipédia sur le diagramme d’Ishikawa&thinsp;:&nbsp;<br><a href="https://fr.wikipedia.org/wiki/Diagramme_de_causes_et_effets">fr.wikipedia.org</a></li>
      </ul>

      <h3 id="p3-3">Aparté : </h3>
      <h4>Témoignages tiré du site internet <em>clientsfromhell.com</em></h4>

      <p>Cette parenthèse regroupe six témoignages anonymes de designers trouvés sur le site <em>clientsfromhell.net</em><sup><a id="link-4-16" href="#note-4-16">[16]</a></sup>. Ce site regroupe des témoignages de designers confrontés à des situations de travail
        et de relations désastreuses avec des commanditaires. Si chaque témoignage décrit une situation de travail bien particulière, il semble que certaines problématiques qui en ressortent soit fréquemment observées, notamment le manque de considération
        des compétences particulières du designer. </p>

