      <h3 id="p3-4">Dans le même bateau</h3>
      <h4>engager la responsabilité des acteurs d’un projet</h4>

      <p>Comment engager la responsabilité des acteurs d’un projet de design graphique ? En commençant certainement par identifier ces acteurs&thinsp;:&nbsp;designer, commanditaire, destinataire, diffuseurs, imprimeurs, prestataires techniques, prestataires
        matériels, etc. La liste est longue, mais nous allons nous intéresser à la triangulaire qui dessine le projet en tant que tel&thinsp;:&nbsp;le commanditaire, le designer et le destinataire. On peut déjà observer que le troisième membre est souvent
        absent de la conception du projet. Cette omission est souvent remplacée par des spéculations sur ce qui sera adapté ou plaira, ou de manière plus creusée avec des études statistiques ou marketing (études de marché). Il semblerait intéressant d’envisager
        d’inclure un groupe représentatif du destinataire de la production dans le processus. </p>
      <p>Ainsi, il faut se pencher sur la manière dont les projets sont élaborés lorsque le destinataire n’est pas inclus dans le processus. On peut d’abord envisager des relations de client à prestataire hiérarchisées, ou la verticalité du processus
        <text-imago
          :num="'10'" :cat="'h'" :caption="`Une relation verticale commanditaire-designer`"></text-imago> est rendue visible par l’acte d’exécuter un cahier des charges donné. Si le projet se déroule sans encombre, il en résultera un manque qualitatif quasi-certain. Dans le cas où des problèmes apparaissent, essentiellement de communication,
        ou de prise de pouvoir d’un des acteurs sur l’autre, les témoignages tirés de <em>clientsfromhell.net</em> en illustrent les conséquences. Il n’est pas rare non plus d’entendre des designers, lors de conférences, parler de premiers projets s’étant
        déroulés de manière catastrophique par manque d’expérience dans la gestion de cette relation et des jeux de pouvoir qui y sont inhérents.</p>
      <p>Des solutions face à ces problèmes ont déjà été envisagées, de différentes manières. Certains tentent d’intercaler des outils, ou personnes, entre le commanditaire et le prestataire. Un chargé de communication ou manager projet qui sert d’intermédiaire,
        ou encore un outil automatisé de rappel de tâches et paiements comme freelancer-app.com. Il peut aussi s’agir d’une plateforme de médiation comme <em>Upwork</em> ou encore <em>TopTal</em> qui promet de mettre en relation avec les meilleurs prestataires
        et commanditaires pour garantir un projet sans accroc. Ces deux plateformes nous renvoient en même temps aux problèmes liés à l’ubérisation que nous avons vu plus tôt. Ainsi, peut importe l’outil utilisé, qu’il soit un intermédiaire de communication
        ou une plateforme qui garantit des rapports sains entre designer et commanditaire, le problème de communication est toujours présent, mais est seulement délégué à un tiers.</p>
      <p>On pourrait aussi envisager des relations horizontales
        <text-imago :num="'11'" :cat="'h'" :caption="`Une relation horizontale commanditaire-designer`"></text-imago> entre commanditaires et designers, où le projet est pensé comme un intérêt commun temporaire des deux acteurs. Le processus de recherche, travail, retour, exécution se fait alors correctement, et la communication se fait sans encombre,
        car les responsabilités du commanditaire et celle du prestataire sont claires et ils travaillent réellement ensemble vers un même objectif. Malgré tout, l’absence d’une représentation du destinataire du projet pourra quand même entraîner un manque
        de recul et des problèmes au moment de la réception de la production par son public.</p>
      <p>Enfin, on peut envisager une troisième possibilité de structure, extensible à encore plus d’acteurs d’un projet, jusqu’aux prestataires de rédaction, impression, développement, etc. Cette possibilité nécessiterait au minimum trois de ces acteurs&thinsp;:&nbsp;le
        commanditaire, le designer, et le destinataire. Ils travailleraient ensemble avec le projet comme intérêt commun
        <text-imago :num="'12'" :cat="'h'" :caption="`Une relation horizontale commanditaire-designer-destinataire`"></text-imago> : l’un avec un objectif de réussite, l’autre avec un objectif de justesse, et le troisième avec un objectif d’assurance de la pertinence de ce qu’on lui propose. Ce trio devrait donc travailler à la manière d’un accordéon, se regroupant
        dans des sessions de travail et d’avancée, puis s’éloignant de manière à prendre du recul sur ce qui a été fait et décidé. Ils pourront ainsi apporter un retour au prochain rapprochement, et ce jusqu’à ce que le projet soit arrêté.</p>
      <p>Bien sûr, les contraintes de délais, budget, disponibilités, et intérêt à la production de quelque chose de juste entament nécessairement ce mode de fonctionnement. Cependant, il doit être pris à la manière de ce que Frédéric Lordon énonce dans son
        livre <em>Imperium</em> à propos d’une horizontalité dans la gestion de la société : <citation :inline="1">[...] voir l’impossible sans désarmer de désirer l’impossible. C’est-à-dire, non pas renoncer, comme le commande le conservatisme empressé, mais faire obstinément du chemin. En sachant qu’on ne verra pas le bout.<sup><a id="link-4-17" href="#note-4-17">[17]</a></sup></citation> Pour lui, si l’horizontalité est impossible à atteindre, il faut le savoir, et en prendre acte. Mais l’objectif d’atteindre cette horizontalité entraîne une amélioration vers elle, et la savoir impossible doit permettre de redoubler de vigilance lorsqu’on
        tente de la construire. Ainsi, une relation raisonnée, dessinée, et surveillée entre les trois protagonistes d’un projet paraît un guide adapté à l’élaboration de projets de design non pas justes, mais les plus justes possibles à un instant et avec
        les ressources données. </p>

      <ul class="Footnotes">
        <li><span id="note-4-17"><a href="#link-4-17">[17]</a></span>. Frédéric Lordon, <em>Imperium, structures et affects des corps politiques</em>, Paris, La Fabrique, 2015, 358&thinsp;p.</li>
      </ul>

    </partie>

    <partie :name="'Conclusion'">
      <h2 id="z4">Conclusion</h2>

      <p>Les idées développées dans ce texte, regroupées en trois parties, ne sont pas seulement un enchaînement, mais convergent vers une définition de la pratique du design graphique comme un soft power qui s’ignore. Ce terme, proposé en 1990 par Joseph
        Nye dans son livre <em>Bound to Lead</em><sup><a id="link-5-1" href="#note-5-1">[1]</a></sup>, décrit la puissance d’influence d’une entité sur une autre sans que ce pouvoir s’exerce de manière visible et directe. En permettant un renforcement des structures
        déjà en place via des conditions socio-économiques et culturelles préalables, le design graphique fait partie de ces forces d’influence qui s’exercent de manière indirecte.</p>
      <p>Il s’agit donc de constater la réalité de ce pouvoir, d’en définir les leviers liés à l’interprétation et à la production d’images, et de se demander comment des paradigmes politiques sont devenus des règles inhérentes à la pratique. Il faut aussi
        cerner les conditions de cette pratique et de quelle manière elles pourraient évoluer dans un futur proche, certainement au détriment des idéaux d’Aicher, Papanek, Natalini, Perrotet et tant d’autres. </p>
      <p>Bien sûr, ces figures de la profession semblent échapper à cette dérive du rôle du designer, comme beaucoup d’autres ayant échappé au statut de référent pour la profession et son histoire. Mais c’est bien là leur caractère d’exception qui leur permet
        d’accéder au temps intellectuel nécessaire à un développement, à l’écriture, puis à la diffusion de leurs recherches et critiques. Le design graphique tel qu’il est présenté au festival de Chaumont, pour un visiteur, semblerait bien étranger au
        portrait qui en est dressé ici. Mais la distinction entre projets d’auteur et projets alimentaires semble bel et bien être le point sensible, et les figures de la profession n’échappent pas toujours à mener les seconds. </p>
      <p>Il ne s’agit alors pas d’échapper au contexte socio-politique complexe qui provoque ces dérives, mais plutôt de l’analyser, de mesurer son influence sur la production. En mesurant le pouvoir que ces mêmes productions portent, il semble que l’idée
        selon laquelle nous n’ayons individuellement aucun pouvoir politique doive être entièrement remise en question. Développer un recul critique, des méthodologies de garantie d’une vigilance active, ainsi que des mécanismes de projet permettant de
        s’approcher, dans un cadre donné, de ce qui devrait être fait, permettra non pas de s’affranchir d’une récupération de tout acte de design par des motivations marchandes, mais de garder cet affranchissement comme objectif pour mieux faire.</p>
      <p>C’est à travers cette réflexion que je souhaite guider ma pratique future, à la fois dans un souci de justesse et d’auto-critique, mais surtout pour la confronter aux phénomènes étudiés dans ce texte. Le contexte actuel tel que j’ai pu le décrire
        rend cette mise en pratique difficile. Pourtant, ce tissu économique et idéologique n’est pas lisse&thinsp;:&nbsp;il comprend des aspérités dans lesquelles cette discussion a lieu et d’autres où la mise en pratique de cette réflexion est possible.
        Ces aspérités doivent être protégées, mais ne doivent pas être le seul contexte d’exercice de cette réflexion car sa mise en pratique en terrain conquis ne permettra ni de la confronter aux difficultés d’un contexte qui lui est hostile, ni de la
        diffuser suffisamment via sa pratique.</p>
      <p>Que ce soit à l’école ou dans la vie professionnelle, la pratique du design graphique invite à une observation du monde qui nous entoure, et à développer des facultés d’analyse qui paraissent essentielles à un travail de qualité. C’est justement ces
        compétences que je voudrais mettre à profit dans ma propre pratique, en envisageant le design graphique comme un outil de décryptage de l’environnement dans lequel nous évoluons. Ce que nous avons pu entrevoir du pouvoir de l’image de par son statut
        de vecteur idéologique de structures dominantes peut être renversé, pour devenir au contraire un vecteur d’émancipation et de prise de recul. Évidemment, le train ne s’arrêtera pas de sitôt, mais viser son arrêt ne pourra qu’augmenter nos chances
        de voir un ralentissement.</p>

      <ul class="Footnotes">
        <li><span id="note-5-1"><a href="#link-5-1">[1]</a></span>. Joseph S. Nye, Bound To Lead&thinsp;:&nbsp;The Changing Nature Of American Power, New York, Basic Books, 1991, 336&thinsp;p.</li>
      </ul>
    </partie>
