<template>
  <figure v-show="show">
    <img :src="src" alt="">
    <figcaption><span @click.stop="closemaps"><a :id="`link-${cat}${num}`" :href="`#image-${cat}${num}`">{{cat}}{{num}}</a></span> - <span v-html="caption"></span></figcaption>
  </figure>
</template>

<script>
  import bus from './../bus';

  export default {
    name: 'MapBigImage',
    data() {
      return {
        show: false,
      };
    },
    props: ['src', 'caption', 'cat', 'num'],
    methods: {
      closemaps() {
        bus.$emit('closeMaps');
        document.getElementById(`link-${this.cat}${this.num}`).click();
      },
    },
    mounted() {
      bus.$on('closeImages', () => {
        this.show = false;
      });
      bus.$on('showImage', (p) => {
        if (p.cat !== this.cat) {
          this.show = false;
          return;
        }
        if (p.num !== this.num) {
          this.show = false;
          return;
        }
        this.show = true;
        this.$nextTick(() => {
          bus.$emit('openMapsTo', {
            cat: this.cat,
            num: this.num,
          });
        });
      });
      bus.$on('closeImage', (p) => {
        if (p.cat !== this.cat) return;
        if (p.num !== this.num) return;
        this.show = false;
      });
    },
  };
</script>

<style lang="scss" scoped>
  figure {
    display:block;
    margin-bottom:1.5em;
    cursor:pointer;
  }
  figcaption {
    //font-style: italic;
  }
  img {
    max-height:75vh;
  }
</style>



