<template>
  <blockquote :class="classes">
    <slot></slot>
  </blockquote>
</template>

<script>
  export default {
    name: 'Citation',
    props: ['big', 'inline', 'tr'],
    computed: {
      classes() {
        return [
          this.big ? 'Citation__exergue' : '',
          this.inline ? 'Citation__inline' : '',
          this.tr ? 'Citation__translation' : '',
        ];
      },
    },
  };
</script>

<style lang="scss">
  .Citation {

    &__exergue {
     font-size:1.2em;
     margin:3em 0;
      max-width: 54ch;
    }

    &__translation {
      color:darkblue;
      line-height:inherit;
      font-style:normal;
    }

    &__inline {
      display:inline;
    }
  }

</style>



